/**
 * 
 * @param text
 */
export function testFontSizeText(text) {
    const num = Number(text);
    if (isNaN(num)) {
        return 'nan';
    }
    else if (num > MAX_FONT_SIZE) {
        return 'exceed_max';
    }
    else if (num <= MIN_FONT_SIZE) {
        return 'exceed_min';
    }
    else {
        return 'valid';
    }
}
export const printTextInMultiLine = (context, text, x, y, lineHeight) => {
    const textArr = text.split(/\r?\n/g);
    let currentLine = 0;
    for (let str of textArr) {
        context.fillText(str, x, y + (lineHeight * currentLine));
        currentLine++;
    }
};
export const MAX_FONT_SIZE = 999;
export const MIN_FONT_SIZE = 12;
