/**
 * 
 */
import Operation from "../Operation";
import { convertToCameraBoundSnapshot } from "./CameraBoundOp";
import { shouldDispatch } from "./util";
import { showToast } from "../../../component/toast";
const FACTOR = 1 / 0.86;
const UPPER_BOUND = 20;
const LOWER_BOUND = 0.03;
class ZoomOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'zoom';
        this.sp = [0, 0];
        this.description = "";
    }
    registerHooks() {
        this.opsManager.hooks.mousewheel.tap("MousewheelPlugin", this.handleMouseWheel.bind(this));
    }
    eventsToListen() {
        return {
            'zoom:in': {
                value: this.zoomIn.bind(this),
                information: {
                    args: '',
                    message: ``
                }
            },
            'zoom:out': {
                value: this.zoomOut.bind(this),
                information: {
                    args: '',
                    message: ``
                }
            },
            'zoom:to': {
                value: this.zoomTo.bind(this),
                information: {
                    args: 'scale: number, animate: boolean',
                    message: ``
                }
            }
        };
    }
    handleMouseWheel(event, mx, my, clientX, clientY) {
        if (!this.app.appConfig.cameraTransformEnabled) {
            return;
        }
        if (!shouldDispatch(this.app)) {
            showToast({
                msg: '',
                type: 'info'
            });
            return;
        }
        let scale;
        if (event.deltaY > 0) {
            scale = FACTOR;
        }
        else {
            scale = 1 / FACTOR;
        }
        if (this.app.camera.zoomFactor * scale >= UPPER_BOUND && scale > 1) {
            return;
        }
        else if (this.app.camera.zoomFactor * scale <= LOWER_BOUND && scale < 1) {
            return;
        }
        if (this.isMouseDown) {
            return;
        }
        if (!this.app.container.isTargetOnContainer(event.target)) {
            return;
        }
        this.beginBatch();
        this.dispatch(this.getSnapshot(scale, false, [clientX, clientY]));
    }
    zoomIn() {
        if (!shouldDispatch(this.app))
            return;
        if ((this.app.camera.zoomFactor * FACTOR) >= UPPER_BOUND) {
            return;
        }
        this.beginBatch();
        return this.dispatch(this.getSnapshot(FACTOR, false));
    }
    zoomOut() {
        if (!shouldDispatch(this.app))
            return;
        if ((this.app.camera.zoomFactor / FACTOR) <= LOWER_BOUND) {
            return;
        }
        this.beginBatch();
        return this.dispatch(this.getSnapshot(1 / FACTOR, false));
    }
    zoomTo(destScaleNum, animate) {
        if (!shouldDispatch(this.app))
            return;
        this.beginBatch();
        return this.dispatch(this.getSnapshot(destScaleNum / this.app.camera.zoomFactor, animate));
    }
    getSnapshot(scale, animate, center) {
        return convertToCameraBoundSnapshot(this.app.camera, {
            type: 'zoom',
            animate: animate,
            center,
            scale
        });
    }
}
export default ZoomOp;
