const POINT_MATCHER = /\(x:([.\d]+),y:([.\d]+)\)/;
// 
class Point {
    constructor(x, y) {
        this.x = x || 0;
        this.y = y || 0;
    }
    destroy() {
    }
    clone() {
        return new Point(this.x, this.y);
    }
    static fromString(str) {
        let match = POINT_MATCHER.exec(str);
        if (match) {
            return new Point(+match[1], +match[2]);
        }
        else {
            return new Point(0, 0);
        }
    }
    toString() {
        return `(x:${(this.x).toFixed(8)},y:${(this.y).toFixed(8)})`;
    }
    toCoord(width, height) {
        return new Coord(this.x * width, this.y * height);
    }
    copy(point) {
        this.x = point.x;
        this.y = point.y;
    }
    minus(p) {
        return new Point(this.x - p.x, this.y - p.y);
    }
    topLeft(x, y) {
        if (typeof x == 'number' && typeof y === 'number') {
            if (x < this.x) {
                this.x = x;
            }
            if (y < this.y) {
                this.y = y;
            }
        }
        else if (x instanceof Point) {
            this.topLeft(x.x, x.y);
        }
        return this;
    }
    bottomRight(x, y) {
        if (typeof x == 'number' && typeof y === 'number') {
            if (x > this.x) {
                this.x = x;
            }
            if (y > this.y) {
                this.y = y;
            }
        }
        else if (x instanceof Point) {
            this.bottomRight(x.x, x.y);
        }
        return this;
    }
    static middle(a, b) {
        return new Point((a.x + b.x) / 2, (a.y + b.y) / 2);
    }
    static areaSize(a, b) {
        return {
            width: Math.ceil(Math.abs(a.x - b.x)),
            height: Math.ceil(Math.abs(a.y - b.y)),
        };
    }
    static clone({ x, y }) {
        return new Point(x, y);
    }
}
export default Point;
