/**
 * 
 */
import { transformOp } from "./util";
/**
 * 
 * @param page
 * @param snapshot
 */
export const addIncrementSnapshot = (page, snapshot) => {
    if (snapshot.data.type === 'text') {
        addTextSnapshot(page, snapshot);
    }
    else {
        addSnapshotAndDrawOnlyIt(page, snapshot);
    }
};
/**
* 
*/
const addTextSnapshot = (page, snapshot) => {
    /**
     * PagePage
     * select
     * drawOpQueue
     */
    if (page.boardName === page.app.room.currIndex &&
        page.pageIndex === page.app.room.currBoard.currIndex.toString()) {
        if (snapshot.data.dependOn) {
            //
            const dependOnId = snapshot.data.dependOn;
            let drawOp = page.drawOpQueue[dependOnId];
            if (drawOp) {
                drawOp = transformOp(page, drawOp, snapshot);
                page.drawOpQueue[dependOnId] = drawOp;
                setInprogressSnapshot(page, drawOp);
            }
        }
        else {
            //focus
            page.drawOpQueue[snapshot.opId] = snapshot;
            setInprogressSnapshot(page, snapshot);
        }
        page.app.room.pageToIncRefresh = page;
        // drawInProgressSnapshots(page)
    }
};
/**
* 
* @param snapshot
*/
const addSnapshotAndDrawOnlyIt = (page, snapshot) => {
    /**
     * PagePage
     */
    setInprogressSnapshot(page, snapshot);
    if (page.boardName === page.app.room.currIndex &&
        page.pageIndex === page.app.room.currBoard.currIndex.toString()) {
        if (snapshot.data.type !== 'laser' && snapshot.data.type !== 'mouse-pointer') {
            page.drawOpQueue[snapshot.opId] = snapshot;
        }
        page.app.room.pageToIncRefresh = page;
        // drawInProgressSnapshots(page)
    }
};
/**
 * inProgress
 * @param snapshot
 */
const setInprogressSnapshot = (page, snapshot) => {
    if (snapshot.data.type === 'select') {
        if (snapshot.data.inProgress) {
            page.lastInProgressSelectOfUser[snapshot.uid] = snapshot;
        }
    }
    else if (snapshot.data.type === 'mouse-pointer') {
        page.lastMousePointerOfUser[snapshot.uid] = snapshot;
    }
    else if (page.lastInProgressSnapshotOfUser.hasOwnProperty(snapshot.uid) &&
        page.lastInProgressSnapshotOfUser[snapshot.uid].snapshot.opId === snapshot.opId) {
        page.lastInProgressSnapshotOfUser[snapshot.uid] = {
            snapshot: snapshot,
            beginAt: page.lastInProgressSnapshotOfUser[snapshot.uid].beginAt
        };
    }
    else {
        page.lastInProgressSnapshotOfUser[snapshot.uid] = {
            snapshot: snapshot,
            beginAt: Date.now()
        };
    }
};
