import createDebug from 'debug';
const noop = () => { };
const logger = {
    mouse: createDebug('mouse'),
    h5: createDebug('h5'),
    package: createDebug('package'),
    shape: createDebug('shape'),
    user: createDebug('user'),
    command: createDebug('command'),
    image: function (url, note = '') {
        var image = new Image();
        image.onload = function () {
            var style = [
                'font-size: 14px;',
                'padding: ' + image.height / 2 + 'px ' + image.width / 2 + 'px;',
                'background: url(' + url + ') no-repeat;',
                'background-size: contain;'
            ].join(' ');
            console.log(note + '%c ', style);
        };
        image.src = url;
    },
    m: {
        adapter: createDebug('adapter'),
        worker: createDebug('worker'),
        containermanager: createDebug('containermanager'),
        shapemanager: createDebug('shapemanager'),
        usermanager: createDebug('usermanager'),
        syncmanager: createDebug('syncmanager'),
    },
    log: console.log.bind(console),
    warn: console.warn.bind(console),
    error: console.error.bind(console),
};
// 
if (!localStorage.getItem('debugMouse')) {
    logger.mouse = noop;
}
export default logger;
