export function packBulk(anyActions) {
    let str = anyActions.map(anyAction => pack(anyAction)).join(';') + ';';
    logger.m.syncmanager('packBulk anyAction', str);
    return str;
}
/**
 * 
 */
export function pack(anyAction, isP2P) {
    logger.m.syncmanager('pack anyAction', anyAction);
    console.assert(anyAction.action !== undefined, 'ooops');
    let { action } = anyAction;
    let str = action.toString();
    return str;
}
export function unpackBulk(data) {
    var actions = [];
    const ops = data.split(';');
    ops.forEach(op => {
        const action = unpack(op);
        if (action) {
            if (Array.isArray(action)) {
                while (action.length) {
                    let tmp = action.shift();
                    if (tmp) {
                        actions.push(tmp);
                    }
                }
            }
            else {
                actions.push(action);
            }
        }
    });
    return actions;
}
export function unpack(data) {
    if (!data) {
        return;
    }
    const actionIndex = data.indexOf(':');
    logger.m.syncmanager('unpack command ', data);
    let action;
    let valueStr;
    if (actionIndex == -1) {
        action = +data;
        valueStr = '';
    }
    else {
        action = +(data.slice(0, actionIndex));
        valueStr = data.slice(actionIndex + 1, data.length);
    }
    let value = valueStr.length > 1 ? valueStr.split(',') : [];
    logger.command('unpack', data);
    switch (action) {
        case 1 /* START */: {
            let ret = [new StartAction(+value[0], +value[1])];
            if (value[2]) {
                ret.unshift(new CalibrateAction({ color: formatColor(value[2]) }));
            }
            return ret;
        }
        case 2 /* MOVE */: {
            let ret = [new MoveAction(+value[0], +value[1])];
            if (value[2]) {
                ret.unshift(new CalibrateAction({ color: formatColor(value[2]) }));
            }
            return ret;
        }
        case 3 /* END */: {
            let ret = [new EndAction(+value[0], +value[1])];
            if (value[2]) {
                ret.unshift(new CalibrateAction({ color: formatColor(value[2]) }));
            }
            return ret;
        }
        case 12 /* LASER_PEN */: {
            return new LaserPenAction(+value[0], +value[1]);
        }
        case 13 /* LASER_PEN_END */:
            return new LaserPenEndAction();
        case 4 /* REVOKE */:
            return new RevokeAction();
        case 5 /* SERIAL */:
            return new SerialAction();
        case 6 /* CLEAR */:
            return new ClearAction();
        case 7 /* CLEAR_ACK */:
            return new ClearAckAction();
        case 8 /* SYNC_REQUEST */:
            return new SyncRequestAction(); // TODO
        case 9 /* SYNC */:
            console.warn('');
            return undefined;
        // return {
        //   action: action,
        //   sync: {
        //     account: value[0],
        //     dataMap: {},
        //     all: value[1] as '0' | '1',
        //     index: +value[2],
        //     num: +value[3]
        //   }
        // }
        case 10 /* SYNC_PREPARE */:
            return new SyncPrepareAction();
        case 11 /* SYNC_PREPARE_ACK */:
            return new SyncPrepareAckAction();
        case 14 /* FILE */:
            return new FileAction({
                docId: value[0],
                currentPage: +value[1],
                pageCount: +value[2],
                type: value[2] == '0' ? 'clear' : 'img'
            });
        // return {
        //   action: action,
        //   file: {
        //     docId: value[0] || '',
        //     currentPage: value[1] || '',
        //     pageCount: value[2] || '',
        //     type: value[3] === '0' ? 'img' : (value[3] === '1' ? 'h5' : ''),
        //     url: value[4] || '',
        //     urlStr: value[5] || '',
        //     h5Cmd: value[6] || ''
        //   }
        // }
        // case OLD_ACTION.REDO:
        //   return RedoAction.fromString(valueStr)
        // case OLD_ACTION.CALIBRATE:
        //   return CalibrateAction.fromString(valueStr)
        // case OLD_ACTION.TEXT:
        //   return TextAction.fromString(valueStr)
        default:
            const fullCaseChecker = action;
    }
}
export function formatColor(color, format = 16) {
    if (!color)
        return -1;
    let res = '';
    if (format === 16) {
        color = +color;
        if (color < 0) {
            res = hex2rgb(color);
            console.log('hex2rgb', color, res);
            return res;
        }
        res = color.toString(16);
        if (res.length < 3) {
            res = ('000' + res).slice(-3);
        }
        if (res.length > 3 && res.length < 6) {
            res = ('000' + res).slice(-6);
        }
        res = '#' + res;
        //console.log('formatColor 16', color, res)
        return res;
    }
    else if (format === 10) {
        res = color.replace(/#/gi, '');
        res = parseInt('0x' + res);
        //console.log('formatColor 10', color, res)
        return isNaN(res) ? -1 : res;
    }
}
// rgbargb
export function hex2rgb(val) {
    // return '#' + (0x00FFFFFF & val)
    let alpha = (val >> 24) & 0xff;
    let red = (val >> 16) & 0xff;
    let green = (val >> 8) & 0xff;
    let blue = val & 0xff;
    red = red.toString(16);
    green = green.toString(16);
    blue = blue.toString(16);
    console.log('rgb', red, green, blue);
    return ('#' +
        ('0' + red).slice(-2) +
        ('0' + green).slice(-2) +
        ('0' + blue).slice(-2));
}
