import * as CanvasHelper from '../DrawWorker/CanvasHelper';
import Point from './Point';
import { EventEmitter } from 'eventemitter3';
import logger from '../../debug';
import { CalibrateAction } from '../Actions';
import globalHub from './GlobalHub';
class User extends EventEmitter {
    constructor(UID, maxUserHistory) {
        super();
        this.undoCount = 0; // 
        this.account = UID;
        // 
        //zhongkai: 
        this.ctx = document.createElement('canvas').getContext('2d');
        // this.ctx.canvas.style.transform = 'translate3d(0,0,0)'
        this.ctx.canvas.className = 'wb_-_-' + UID;
        CanvasHelper.setpixelated(this.ctx);
        CanvasHelper.setLineStyle(this.ctx);
        this.shapes = []; // index
        this.recycle = [];
        this.mode = 'draw'; // 
        this.tool = 'flag';
        this.flagPos = new Point(99999, 99999).toString();
        this.size = 4;
        this.color = '#000000';
        this.fontsize = 24;
        this.isMouseDown = false; // 
        this.start = new Point(0, 0);
        this.oldMid = new Point(0, 0);
        this.prevOldMid = new Point(0, 0);
        this.end = new Point(0, 0);
        this.prevEnd = new Point(0, 0);
        // nullJSON
        this.keyDown = {
            altKey: false,
            ctrlKey: false,
            shiftKey: false,
            which: -1,
        }; // 
        // local undo/redo model
        globalHub.emit('userUpdate', this.account, {
            tool: this.tool,
            size: this.size,
            color: this.color,
            fontsize: this.fontsize,
            mode: this.mode,
            keyDown: this.keyDown,
        });
        // private data
        this.maxUserHistory = maxUserHistory;
        this._resetCalibrate = this.getCalibrateAction();
    }
    // isFlag?: boolean;// TODO
    get isFlag() {
        return this.tool === 'flag';
    }
    update(opt) {
        User.PROPS.forEach((key) => {
            if (key in opt) {
                this[key] = opt[key];
            }
        });
        this.emit('update', this.account, opt);
        globalHub.emit('userUpdate', this.account, opt);
        logger.user('update user ', this, 'with ', opt);
        // Object.assign(this, opt);
    }
    pushShape(shape) {
        if (this.recycle.length) {
            this.recycle.forEach(shape => shape.destroy());
            this.recycle.length = 0;
        }
        this.undoCount = 0; // 0
        this.shapes.push(shape);
        this.configActiveShape();
        // shapes
        this.emitUserShape();
        return shape;
    }
    configActiveShape() {
        console.log('config active shape ');
        this.activeShape = this.shapes[this.shapes.length - 1];
    }
    undo() {
        if (this.undoCount >= this.maxUserHistory) {
            console.warn('reach maxUserHistory! abort undo');
            return this.shapes.length;
        }
        this.undoCount++;
        let shape = this.shapes.pop();
        if (shape) {
            this.recycle.push(shape);
            this.configActiveShape();
            logger.log('undo:', this);
            this.emit('undo', this);
            // shapes
            this.emitUserShape();
        }
        return this.shapes.length;
    }
    redo() {
        if (this.undoCount === 0) {
            // undo
            return this.recycle.length;
        }
        this.undoCount--;
        let shape = this.recycle.pop();
        if (shape) {
            this.shapes.push(shape);
            this.configActiveShape();
            this.emit('redo', this);
            // shapes
            this.emitUserShape();
        }
        return this.recycle.length;
        ;
    }
    // 
    getCalibrateAction() {
        const { mode, tool, size, color, fontsize, isMouseDown, keyDown, flagPos } = this;
        return new CalibrateAction({
            mode, tool, size, color, fontsize,
            isMouseDown, flagPos, keyDown
        });
    }
    clearShapes() {
        logger.user('user reset:', this);
        this.shapes.forEach(shape => shape.destroy());
        this.shapes = [];
        this.recycle = [];
        this.undoCount = 0;
        // shapes
        this.emitUserShape();
    }
    emitUserShape() {
        let { undoCount } = this; // 
        let redoLength = this.recycle.length; // 
        let undoLength = this.shapes.length; // 
        let avaliableUndoCount = this.maxUserHistory - undoCount;
        if (undoLength < avaliableUndoCount) {
            avaliableUndoCount = undoLength;
        }
        globalHub.emit('userShapes', this.account, avaliableUndoCount, this.recycle.length);
    }
    /**
     * undoCount
     */
    guessMyUndoCount() {
        this.undoCount = this.recycle.length;
    }
    whenMouseDown(offset) {
        this.prevOldMid = offset.clone();
        this.prevEnd = offset.clone();
        this.start = offset.clone();
        this.oldMid = offset.clone();
        this.end = offset.clone();
    }
    whenMouseMove(offset) {
        this.prevOldMid = this.oldMid.clone();
        this.prevEnd = this.end.clone();
        this.oldMid = Point.middle(this.end, offset);
        this.end = offset.clone();
    }
    whenMouseUp(offset) {
        this.prevOldMid = this.oldMid.clone();
        this.prevEnd = this.end.clone();
        this.oldMid = Point.middle(this.end, offset);
        this.end = offset.clone();
    }
    reset() {
        this.update(this._resetCalibrate);
        globalHub.emit('userUpdate', this.account, this._resetCalibrate);
    }
    destroy() {
        if (this.shapes.length || this.recycle.length) {
            this.clearShapes();
        }
        logger.user('delete user ctx:', this);
        // delete this.ctx;
    }
}
User.PROPS = [
    'mode',
    'tool',
    'size',
    'color',
    'fontsize',
    'isMouseDown',
    'flagPos',
    'keyDown',
];
export default User;
