import Point from "./core/Model/Point";
export default class Simulator {
    constructor(app) {
        this.app = app;
    }
    freeDraw(times = 10, scale = 0.0003) {
        this.app.useTool('free', this.app.account);
        for (let i = 0; i < times; i++) {
            const sx = Math.random();
            const sy = Math.random();
            const dx = Math.random() * scale;
            const dy = Math.random() * scale;
            this.app.drawAdapter.worker.mousedown(new Point(sx, sy), this.app.account);
            this.app.drawAdapter.worker.mousemove(new Point(sx + dx, sy + dy), this.app.account);
            this.app.drawAdapter.worker.mouseup(new Point(sx + dx, sy + dy), this.app.account);
        }
    }
    rectDraw(times = 10, scale = 0.0003) {
        this.app.useTool('rect', this.app.account);
        for (let i = 0; i < times; i++) {
            const sx = Math.random();
            const sy = Math.random();
            const dx = Math.random() * scale;
            const dy = Math.random() * scale;
            this.app.drawAdapter.worker.mousedown(new Point(sx, sy), this.app.account);
            this.app.drawAdapter.worker.mousemove(new Point(sx + dx, sy + dy), this.app.account);
            this.app.drawAdapter.worker.mouseup(new Point(sx + dx, sy + dy), this.app.account);
        }
    }
}
