/**
 * Board
 *
 * BoardBoardPage
 *
 * Board
 */
import Page from "../page";
import { addPage, deletePage, gotoPage } from "./actions";
class Board {
    constructor(app, boardName, displayName) {
        this.pages = {};
        //pageskeypageNames
        this.pageNames = [];
        //
        this.newPageIndex = 1;
        this.app = app;
        this.dispatch = app.dispatcher;
        this.boardName = boardName;
        this.displayName = displayName;
        this.pages = {
            '0': new Page(app, boardName, '0')
        };
        this.pageNames = ['0'];
        this.currIndex = 0;
    }
    get currPage() {
        return this.pages[this.currIndex];
    }
    get payloadOfPages() {
        return this.pageNames.map(name => {
            return this.pages[name].payload;
        });
    }
    /**
     * currIndexpage
     *
     * 0
     * 
     *
     * 
     * 0,1,2
     * 02
     *
     * 2currPageIndex1
     * 0currPageIndex0
     */
    getCurrPageIndex() {
        return this.pageNames.indexOf(this.currIndex.toString());
    }
    getPageBuiltInIndex(index) {
        return Number(this.pageNames[index]);
    }
    addOpInBoard(snapshot) {
        const { action, page, insertAfter } = snapshot.data;
        if (action === 'add') {
            addPage(this, insertAfter, snapshot);
        }
        if (action === 'goto') {
            gotoPage(this, page, snapshot);
        }
        if (action === 'delete') {
            deletePage(this, page, snapshot);
        }
        this.app.opsManager.hooks.addOpHook.call(snapshot, true);
    }
}
export default Board;
