import logger from "../debug";
import { WActions } from "./Itypes";
import { StartAction, MoveAction, EndAction, ClearAckAction, LaserPenAction, LaserPenEndAction, SyncAction, RevokeAction, SerialAction, ClearAction, RedoAction, FileAction, CalibrateAction, SyncRequestAction, SyncPrepareAction, SyncPrepareAckAction, TextAction, VersionAction } from "./Actions";
/**
 *  WebSocketCommand, Action
 */
/**
 * Array<Action>commandcommand
 *
 * @param anyActions
 */
export function packBulk(anyActions) {
    let str = anyActions.map(anyAction => pack(anyAction)).join(';') + ';';
    logger.m.syncmanager('packBulk anyAction', str);
    return str;
}
/**
 * (command)
 *
 * 
 *  MoveAction(x=0.3, y=0.5)
 *  2:0.3, 0.5
 *
 * @param anyAction
 * @param isP2P
 */
export function pack(anyAction, isP2P) {
    logger.m.syncmanager('pack anyAction', anyAction);
    console.assert(anyAction.action !== undefined, 'ooops');
    let { action } = anyAction;
    let str = action.toString();
    return str;
}
/**
 * commandArray<Action>
 *
 * @param data
 */
export function unpackBulk(data) {
    var actions = [];
    const ops = data.split(';');
    ops.forEach(op => {
        const action = unpack(op);
        if (action) {
            actions.push(action);
        }
    });
    return actions;
}
/**
 * commandactionactionjs
 *
 * 
 *  "2:0.3,0.5"
 *  MoveAction(x=0.3, y=0.5)
 *
 * @param data
 */
export function unpack(data) {
    if (!data) {
        return;
    }
    const actionIndex = data.indexOf(':');
    logger.m.syncmanager('unpack command ', data);
    let action;
    let valueStr;
    /**
     * +datadata
     * actionIndex == -1, valueStr
     */
    if (actionIndex == -1) {
        action = +data;
        valueStr = '';
    }
    else {
        action = +(data.slice(0, actionIndex));
        valueStr = data.slice(actionIndex + 1, data.length);
    }
    logger.command('unpack', data);
    switch (action) {
        case WActions.START:
            return StartAction.fromString(valueStr);
        case WActions.MOVE:
            return MoveAction.fromString(valueStr);
        case WActions.END:
            return EndAction.fromString(valueStr);
        case WActions.REVOKE:
            return RevokeAction.fromString(valueStr);
        case WActions.SERIAL:
            return SerialAction.fromString(valueStr);
        case WActions.CLEAR:
            return ClearAction.fromString(valueStr);
        case WActions.CLEAR_ACK:
            return ClearAckAction.fromString(valueStr);
        case WActions.LASER_PEN:
            return LaserPenAction.fromString(valueStr);
        case WActions.SYNC_REQUEST:
            return SyncRequestAction.fromString(valueStr); // TODO
        case WActions.SYNC:
            return SyncAction.fromString(valueStr);
        // return {
        //   action: action,
        //   sync: {
        //     account: value[0],
        //     dataMap: {},
        //     all: value[1] as '0' | '1',
        //     index: +value[2],
        //     num: +value[3]
        //   }
        // }
        case WActions.SYNC_PREPARE:
            return SyncPrepareAction.fromString(valueStr);
        case WActions.SYNC_PREPARE_ACK:
            return SyncPrepareAckAction.fromString(valueStr);
        case WActions.LASER_PEN_END:
            return LaserPenEndAction.fromString(valueStr);
        case WActions.FILE:
            return FileAction.fromString(valueStr);
        // return {
        //   action: action,
        //   file: {
        //     docId: value[0] || '',
        //     currentPage: value[1] || '',
        //     pageCount: value[2] || '',
        //     type: value[3] === '0' ? 'img' : (value[3] === '1' ? 'h5' : ''),
        //     url: value[4] || '',
        //     urlStr: value[5] || '',
        //     h5Cmd: value[6] || ''
        //   }
        // }
        case WActions.REDO:
            return RedoAction.fromString(valueStr);
        case WActions.CALIBRATE:
            return CalibrateAction.fromString(valueStr);
        case WActions.TEXT:
            return TextAction.fromString(valueStr);
        case WActions.VERSION:
            return VersionAction.fromString(valueStr);
        default:
            const fullCaseChecker = action;
    }
}
