import { getBoundaryBoxFromTwoPoints, getTransformedBoundary, rectCollide } from "../misc/shapeHelper";
/**
* id
*/
export const findOpsUnderRect = (points, drawOps) => {
    if (points.length === 0)
        return [];
    const mouseBoundary = getBoundaryBoxFromTwoPoints(points[0], points[points.length - 1]);
    const res = [];
    for (let opId in drawOps) {
        const snapshot = drawOps[opId];
        if (snapshot.data.boundary && snapshotSelectable(snapshot)) {
            const boundary = getTransformedBoundary(snapshot.data.boundary, snapshot.data.dx, snapshot.data.dy, snapshot.data.scaleX, snapshot.data.scaleY);
            if (rectCollide(boundary, mouseBoundary)) {
                res.push(opId);
            }
        }
    }
    return res;
};
/**
 * id
 */
export const findOpsUnderMouse = (points, drawOps, opsManager) => {
    if (points.length === 0)
        return [];
    const mouseBoundary = getBoundaryBoxFromTwoPoints(points[0], points[points.length - 1]);
    const [mx, my] = points[points.length - 1];
    const opIds = Object.keys(drawOps);
    for (let i = opIds.length - 1; i >= 0; i--) {
        /**
         * 
         */
        const snapshot = drawOps[opIds[i]];
        const op = opsManager.opMap[snapshot.data.type];
        if (snapshot.data.boundary && snapshotSelectable(snapshot)) {
            const boundary = getTransformedBoundary(snapshot.data.boundary, snapshot.data.dx, snapshot.data.dy, snapshot.data.scaleX, snapshot.data.scaleY);
            if (rectCollide(boundary, mouseBoundary)) {
                if (op && 'ifMouseOnOp' in op) {
                    //
                    if (op.ifMouseOnOp(snapshot, mx, my)) {
                        return [opIds[i]];
                    }
                }
                else {
                    return [opIds[i]];
                }
            }
        }
    }
    return [];
};
const nonSelectableOpTypes = ['mouse-pointer', 'eraser'];
export const snapshotSelectable = (snapshot) => {
    return !nonSelectableOpTypes.includes(snapshot.data.type) && !snapshot.data.locked;
};
/**
 * 
 * @param currIds
 * @param lastIds
 */
export const idsChanged = (currIds, lastIds) => {
    if (currIds.length !== lastIds.length)
        return true;
    for (let id of currIds) {
        if (lastIds.indexOf(id) === -1)
            return true;
    }
    return false;
};
/**
 * currIds - lastIds
 * @param currIds
 * @param lastIds
 */
export const arrayDeduce = (currIds, lastIds) => {
    const res = [];
    for (let id of currIds) {
        if (lastIds.indexOf(id) === -1) {
            res.push(id);
        }
    }
    return res;
};
/**
 * Draws a rounded rectangle using the current state of the canvas.
 * If you omit the last three params, it will draw a rectangle
 * outline with a 5 pixel border radius
 * @param {CanvasRenderingContext2D} ctx
 * @param {Number} x The top left x coordinate
 * @param {Number} y The top left y coordinate
 * @param {Number} width The width of the rectangle
 * @param {Number} height The height of the rectangle
 * @param {Number} [radius = 5] The corner radius; It can also be an object
 *                 to specify different radii for corners
 * @param {Number} [radius.tl = 0] Top left
 * @param {Number} [radius.tr = 0] Top right
 * @param {Number} [radius.br = 0] Bottom right
 * @param {Number} [radius.bl = 0] Bottom left
 * @param {Boolean} [fill = false] Whether to fill the rectangle.
 * @param {Boolean} [stroke = true] Whether to stroke the rectangle.
 */
export const roundRect = (ctx, x, y, width, height, radius, type) => {
    ctx.beginPath();
    ctx.moveTo(x + radius, y);
    ctx.lineTo(x + width - radius, y);
    ctx.quadraticCurveTo(x + width, y, x + width, y + radius);
    ctx.lineTo(x + width, y + height - radius);
    ctx.quadraticCurveTo(x + width, y + height, x + width - radius, y + height);
    ctx.lineTo(x + radius, y + height);
    ctx.quadraticCurveTo(x, y + height, x, y + height - radius);
    ctx.lineTo(x, y + radius);
    ctx.quadraticCurveTo(x, y, x + radius, y);
    ctx.closePath();
    if (type === 'stroke') {
        ctx.stroke();
    }
    else {
        ctx.fill();
    }
};
export const drawUserName = (opt) => {
    let { ctx, account, scale, px, py, bgColor, fontSize = 24, textPadding = 15, rectMarginBottom = 15, radius = 10 } = opt;
    let rectHeight = (fontSize + 12) / scale;
    textPadding = textPadding / scale;
    rectMarginBottom = rectMarginBottom / scale;
    fontSize = fontSize / scale;
    radius = radius / scale;
    let textFillColor = (bgColor === 'white' || bgColor === 'rgb(255,255,255)') ? 'black' : 'white';
    ctx.beginPath();
    ctx.font = `${fontSize}px Arial`;
    const textWidth = ctx.measureText(account).width;
    ctx.fillStyle = bgColor;
    roundRect(ctx, px - textWidth / 2 - textPadding, py - rectMarginBottom - rectHeight, textWidth + textPadding * 2, rectHeight, radius, 'fill');
    ctx.fillStyle = textFillColor;
    ctx.textAlign = 'left';
    ctx.textBaseline = 'middle';
    ctx.fillText(account, px - textWidth / 2, py - rectMarginBottom - rectHeight / 2);
};
