import { DEFAULT_FONTSIZE } from "../../config";
import textConfig from "./textConfig";
export const MIN_WIDTH = 20;
export const PADDING = 4;
export const DEFAULT_LINEHEIGHT = 1.2;
//textArea
//pretextArea
export function createElements() {
    const textArea = document.createElement('textarea');
    const pre = document.createElement('pre');
    const textAreaContainer = document.createElement('div');
    textAreaContainer.style.position = 'absolute';
    textAreaContainer.style.display = 'none';
    textAreaContainer.style.outline = '1px dashed black';
    // textArea.style.position = 'absolute'
    textArea.style.resize = 'none';
    textArea.style.overflow = 'hidden';
    textArea.style['white-space'] = 'nowrap';
    textArea.style.background = 'transparent';
    //textArea
    // textArea.style.display = 'none'
    textArea.style.fontFamily = textConfig.getFontFamily();
    textArea.style.fontSize = `${DEFAULT_FONTSIZE}px`;
    textArea.style.transformOrigin = 'left top';
    textArea.style.padding = `${PADDING}px`;
    textArea.style.border = 'none';
    textArea.style.outline = 'none';
    textArea.style.boxSizing = 'content-box';
    textArea.style.lineHeight = `${DEFAULT_LINEHEIGHT}`;
    pre.style.width = 'fit-content';
    pre.style.position = 'absolute';
    pre.style.userSelect = 'none';
    pre.style.pointerEvents = 'none';
    pre.style.visibility = 'hidden';
    pre.style.fontFamily = textConfig.getFontFamily();
    pre.style.fontSize = `${DEFAULT_FONTSIZE}px`;
    pre.style.transformOrigin = 'left top';
    pre.style.padding = `${PADDING}px`;
    pre.style.margin = '0';
    pre.style.lineHeight = `${DEFAULT_LINEHEIGHT}`;
    //border-boxgetBoundingClientRectpre
    pre.style.boxSizing = 'border-box';
    textAreaContainer.appendChild(textArea);
    return {
        textAreaContainer,
        textArea,
        pre
    };
}
export function setElementsStyle(textAreaContainer, textArea, pre, opts) {
    textAreaContainer.style.display = 'block';
    textAreaContainer.style.left = `${opts.sx}px`;
    textAreaContainer.style.top = `${opts.sy}px`;
    pre.style.left = `${opts.sx}px`;
    pre.style.top = `${opts.sy}px`;
    textArea.style.color = opts.color;
    textArea.style.width = `${(opts.w || MIN_WIDTH) * DEFAULT_FONTSIZE / opts.fontSize - PADDING * 2}px`;
    textArea.style.height = `${(opts.h || (opts.fontSize + 12)) * DEFAULT_FONTSIZE / opts.fontSize - PADDING * 2}px`;
    setElementsSize(textAreaContainer, textArea, pre, opts.fontSize);
}
/**
 * fontSize
 * textAeratransformfontSize
 *
 *
 * pre
 * pretextArea
 * pretextArea, textAreaContainer
 * @param textAreaContainer
 * @param textArea
 * @param pre
 * @param fontSize
 */
export function setElementsSize(textAreaContainer, textArea, pre, fontSize) {
    pre.style.transform = `scale(${(fontSize) / DEFAULT_FONTSIZE})`;
    textArea.style.transform = `scale(${(fontSize) / DEFAULT_FONTSIZE})`;
    //clientWidthclientHeightpadding
    textArea.style.width = `${pre.clientWidth - PADDING * 2}px`;
    textArea.style.height = `${pre.clientHeight - PADDING * 2}px`;
    textAreaContainer.style.width = `${(pre.clientWidth) * fontSize / DEFAULT_FONTSIZE}px`;
    textAreaContainer.style.height = `${(pre.clientHeight) * fontSize / DEFAULT_FONTSIZE}px`;
}
/**
 * 
 */
export function drawTextSnapshot(pre, opts) {
    pre.innerText = opts.text;
    pre.style.color = opts.color;
    pre.style.left = `${opts.sx}px`;
    pre.style.top = `${opts.sy}px`;
    pre.style.position = 'absolute';
    pre.style.fontFamily = textConfig.getFontFamily();
    pre.style.fontSize = `${DEFAULT_FONTSIZE}px`;
    pre.style.margin = '0';
    pre.style.transform = `scale(${opts.fontSize / DEFAULT_FONTSIZE})`;
    pre.style.userSelect = 'none';
    pre.style.transformOrigin = 'left top';
    pre.style.padding = `${PADDING}px`;
    pre.style.pointerEvents = 'none';
}
export function clearTextArea(textAreaContainer, textArea) {
    textArea.value = '';
    textAreaContainer.style.display = 'none';
}
