/**
 * 
 */
import Operation from "../Operation";
class PageOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'page';
        this.description = "";
    }
    registerHooks() {
        this.opsManager.hooks.pageChange.tap("pageChangePlugin", this.onPageChange.bind(this));
    }
    onPageChange() {
        return this.emit('event:appState:change', 'page', {
            activeIndex: this.opsManager.currBoard.getCurrPageIndex(),
            pageNames: this.opsManager.currBoard.pageNames.slice(),
            currPage: this.opsManager.currBoard.currIndex,
            pagePayloads: this.opsManager.currBoard.payloadOfPages
        });
    }
    eventsToListen() {
        return {
            'page:goto': {
                value: this.goto.bind(this),
                information: {
                    args: 'page: number',
                    message: ``
                }
            },
            'page:add': {
                value: this.add.bind(this),
                information: {
                    args: 'opt: {insertAfter?: number, payload?: any}',
                    message: `board`
                }
            },
            'page:delete': {
                value: this.delete.bind(this),
                information: {
                    args: 'page: number',
                    message: '1'
                }
            }
        };
    }
    add(opt) {
        this.dispatch({
            type: this.type,
            action: 'add',
            insertAfter: opt.insertAfter,
            payload: opt.payload
        });
    }
    goto(page) {
        //
        if (page !== this.opsManager.currBoard.currIndex &&
            this.opsManager.currBoard.pageNames.includes(page.toString())) {
            this.dispatch({
                type: this.type,
                action: 'goto',
                page: page,
            });
        }
    }
    delete(page) {
        const pages = this.opsManager.currBoard.pages;
        if (page in pages && Object.keys(pages).length > 1) {
            this.dispatch({
                type: this.type,
                action: 'delete',
                page: page
            });
        }
    }
}
export default PageOp;
