/**
 * 
 *
 * playtick
 */
import { EventEmitter } from "eventemitter3";
export const DEFAULT_INTERVAL = 10;
export default class TickTick extends EventEmitter {
    constructor() {
        super(...arguments);
        this.state = 'pause';
        //currTimeStampTrack
        this.currTimeStamp = 0;
        //lastRealTime
        this.lastRealTime = undefined;
        this.rate = 1;
        this.microTask = () => {
            if (this.state === 'play') {
                this.tick();
            }
        };
    }
    destory() {
        this.pause();
        this.eventNames().forEach(name => this.off(name));
    }
    tick() {
        let now = Date.now();
        if (this.lastRealTime === undefined) {
            this.lastRealTime = now;
            return;
        }
        else {
            let passedGap = ((now - this.lastRealTime) * this.rate);
            this.currTimeStamp = this.currTimeStamp + passedGap;
            this.lastRealTime = now;
            this.emit('tick', this.currTimeStamp);
        }
    }
    pause() {
        if (this.timer) {
            window.clearInterval(this.timer);
        }
        this.timer = undefined;
        this.lastRealTime = undefined;
        this.state = 'pause';
        this.emit('pause');
    }
    play() {
        if (!this.timer) {
            this.timer = setInterval(this.microTask, DEFAULT_INTERVAL);
        }
        this.state = 'play';
        this.emit('play');
    }
    setCurrentTime(timeStamp = 0) {
        this.currTimeStamp = timeStamp;
        this.lastRealTime = undefined;
    }
}
