import Operation from "../Operation";
import { addImage, draw, getBackgroundImagePorperPosition, getImageProperPosition } from "./util";
class BackgroundOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'background';
        this.description = "";
        this.getBoundaryFromSnapshot = (snapshot) => {
            let boundary;
            if (snapshot.data.hasOwnProperty('imgWidth')) {
                //canvas, imgWidth, imgHeight
                const { sx, sy, width, height } = getImageProperPosition(this, snapshot.data.imgWidth, snapshot.data.imgHeight);
                boundary = {
                    sx, sy, w: width, h: height
                };
            }
            else {
                //3.0.2
                //
                //
                boundary = snapshot.data.boundary;
            }
            return boundary;
        };
        this.dispatchImage = (url, imgWidth, imgHeight, page) => {
            const { sx, sy, width, height } = getBackgroundImagePorperPosition(this, imgWidth, imgHeight);
            if (page) {
                this.dispatchFromPage({
                    type: this.type,
                    url: url,
                    boundary: {
                        sx: sx,
                        sy: sy,
                        w: width,
                        h: height
                    }
                }, page);
            }
            else {
                this.dispatch({
                    type: this.type,
                    url: url,
                    boundary: {
                        sx: sx,
                        sy: sy,
                        w: width,
                        h: height
                    }
                });
            }
        };
    }
    eventsToListen() {
        return {
            'background:imageUpload': {
                value: this.addImage.bind(this),
                information: {
                    args: `url: string, opts?: {
                        width?: number,
                        height?: number,
                        pageIndex?: number
                    }`,
                    message: ``
                }
            },
        };
    }
    addImage(url, opts) {
        return addImage(this, url, opts);
    }
    preview(snapshot, ctx, opts) {
        const data = snapshot.data;
        return draw(ctx, data.url, this.getBoundaryFromSnapshot(snapshot), {
            page: opts.page,
            supportToDataUrl: opts.supportToDataUrl,
            preview: true
        });
    }
    draw(snapshot, ctx, opts) {
        const data = snapshot.data;
        return draw(ctx, data.url, this.getBoundaryFromSnapshot(snapshot), {
            page: opts.page,
            div: opts.div,
            id: snapshot.opId,
            type: 'background'
        });
    }
}
export default BackgroundOp;
