export function isStandardMode(mode) {
    if (typeof mode !== 'string') {
        return [false, ''];
    }
    mode = mode.trim();
    if (mode === '') {
        return [false, ''];
    }
    if (mode === 'player') {
        return [true];
    }
    if (!~mode.indexOf(':')) {
        return [false, '-> '];
    }
    let modeArr = mode.split(':');
    // TODO: mode Arr
    return [true];
}
