import SelectBase from "../SelectOp/SelectBase";
class ElementEraserOp extends SelectBase {
    constructor() {
        super(...arguments);
        this.type = 'element-eraser';
        this.description = "";
        this.lastSelectedOpIds = [];
    }
    eventsToListen() {
        return {
            activate: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    postMouseMove(event) {
        if (this.app.container.isTargetOnContainer(event.target)) {
            document.body.style.cursor = `url(${require('../../../assets/cursors/element-eraser.svg')}) -1 17, auto`;
        }
        else {
            document.body.style.cursor = 'auto';
        }
    }
    postMouseUp(idsBeenSelected) {
        this.postClick(idsBeenSelected);
    }
    postClick(idsBeenSelected) {
        this.dispatch(this.getSelectionSnapshot(false, []));
        if (idsBeenSelected.length > 0) {
            this.dispatch(this.getSnapshot(idsBeenSelected));
        }
        this.lastSelectedOpIds = [];
    }
    getSnapshot(idsBeenSelected) {
        let deleteOpIds = idsBeenSelected.slice();
        return {
            type: 'element-eraser',
            deleteOpIds: deleteOpIds,
            dependOn: deleteOpIds
        };
    }
    /**
     * Op
     */
    transformOp(baseOp, transformOp) {
        const newData = Object.assign({}, baseOp.data, {
            hidden: true
        });
        return Object.assign({}, baseOp, {
            data: newData
        });
    }
}
export default ElementEraserOp;
