/**
 * 
 */
class Player {
    constructor(app) {
        this.records = [];
        this.nextRecordIndex = 0;
        this.lastTime = 0;
        this.offsetBegin = 0;
        this.pkgId = 0;
        this.app = app;
    }
    addRecordData(records) {
        this.records = records.slice();
        this.app.resetState();
        this.nextRecordIndex = 0;
    }
    /**
     * vision.modeaccount
     * @param viewer
     */
    setViewer(viewer) {
        this.app.account = viewer;
        /**
         * 
         */
        const _lastTime = this.lastTime;
        this.lastTime = Infinity;
        this.seekTo(_lastTime);
    }
    seekTo(time) {
        const forward = time > this.lastTime ? true : false;
        this.lastTime = time;
        const nextRecordIndex = forward ? this.nextRecordIndex : 0;
        let endIndex = nextRecordIndex;
        for (; endIndex < this.records.length; endIndex++) {
            const record = this.records[endIndex];
            if (record.time + this.offsetBegin > time) {
                break;
            }
        }
        const recordsToEmit = this.records.slice(nextRecordIndex, endIndex);
        this.nextRecordIndex = endIndex;
        if (!forward) {
            this.app.resetState();
            this.pkgId = 0;
        }
        let snapshotsToEmit = [];
        let broadcasterData;
        let observeModeData;
        for (let i = 0; i < recordsToEmit.length; i++) {
            if (recordsToEmit[i].record.type === 'snapshot') {
                snapshotsToEmit.push({
                    snapshot: recordsToEmit[i].record.value,
                    pkgId: this.pkgId
                });
                this.pkgId += 1;
            }
            else if (recordsToEmit[i].record.type === 'snapshotArr') {
                for (let snapshot of recordsToEmit[i].record.value) {
                    snapshotsToEmit.push({
                        snapshot,
                        pkgId: this.pkgId
                    });
                    this.pkgId += 1;
                }
            }
            else if (recordsToEmit[i].record.type === 'broadcaster') {
                broadcasterData = recordsToEmit[i].record.value;
            }
            else if (recordsToEmit[i].record.type === 'observeMode') {
                if (recordsToEmit[i].record.value.account === this.app.account) {
                    observeModeData = recordsToEmit[i].record.value;
                }
            }
        }
        if (broadcasterData) {
            this.app.vision.receiveBroadcasterAfterSync(broadcasterData);
        }
        if (observeModeData) {
            this.app.vision.receiveObserveModeAfterSync(observeModeData);
        }
        this.app.room.execBatchSnapshots(snapshotsToEmit);
    }
}
export default Player;
