export class ClearAction extends IAction {
    constructor(accounts) {
        super();
        this.TYPE = WActions.CLEAR;
        if (Array.isArray(accounts)) {
            this.accounts = accounts;
        }
        else if (accounts) {
            this.accounts = [accounts];
        }
        else {
            this.accounts = [];
        }
    }
    static fromString(str) {
        let accounts = unstringify(str);
        return new ClearAction(accounts);
    }
    toString() {
        let str = stringify(this.accounts);
        return `${this.TYPE}:${str}`;
    }
}
