export class FileAction extends IAction {
    constructor(file) {
        super();
        this.file = file;
        this.TYPE = WActions.FILE;
    }
    static fromString(str) {
        let value = str.split(',');
        let fileType;
        if (value[3] === '0') {
            fileType = 'img';
        }
        else if (value[3] === '1') {
            fileType = 'h5';
        }
        else {
            fileType = 'clear';
        }
        let file = {
            docId: value[0] || '',
            currentPage: parseInt(value[1] || '1'),
            pageCount: parseInt(value[2] || '1'),
            type: fileType || 'img',
            url: value[4] || '',
            urlStr: value[5] || '',
            h5Cmd: value[6] || ''
        };
        return new FileAction(file);
    }
    static immuteUpdate(fileA, fileB) {
        const gatherFile = {};
        const diff = {};
        FileAction.PROPS.forEach(propName => {
            let valueOld = fileA[propName];
            let valueNext = fileB[propName];
            if (typeof valueNext === 'undefined' || valueNext === '') {
                gatherFile[propName] = valueOld;
            }
            else {
                if (valueNext !== valueOld) {
                    diff[propName] = valueNext;
                    gatherFile[propName] = valueNext;
                }
                else {
                    gatherFile[propName] = valueOld;
                }
            }
        });
        return [gatherFile, diff];
    }
    toString() {
        let file = this.file;
        let fileType;
        if (file.type === 'img') {
            fileType = '0';
        }
        else if (file.type === 'h5') {
            fileType = '1';
        }
        else {
            fileType = '-1';
        }
        return `${this.TYPE}:${file.docId || ''},${file.currentPage || ''},${file.pageCount || ''},${fileType},${file.url || ''},${file.urlStr || ''},${file.h5Cmd || ''}`;
    }
}
FileAction.PROPS = [
    'docId', 'currentPage',
    'pageCount', 'type', 'url',
    'urlStr', 'h5Cmd'
];
