export class CalibrateAction extends IAction {
    constructor(opt) {
        super();
        this.TYPE = WActions.CALIBRATE;
        Object.assign(this, opt);
    }
    static fromString(str) {
        const opt = unstringify(str);
        return new CalibrateAction(opt);
    }
    toString() {
        const originStr = stringify(this, CalibrateAction.PROPS);
        return `${this.TYPE}:${originStr}`;
    }
}
