/**
 * 
 */
import Operation from "../Operation";
import deepClone from 'lodash.clonedeep';
export const COPY_PASTE_SHIFT = 50;
class DuplicateOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'duplicate';
        this.description = "drawable";
        this.copiedSnapshotArray = [];
    }
    registerHooks() {
        this.opsManager.hooks.keydown.tap("KeydownPlugin", this.handleKeyDown.bind(this));
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: ``
                }
            }
        };
    }
    handleKeyDown(ev) {
        const key = ev.key.toLocaleLowerCase();
        if (key === 'c' && (this.isKeyDown('control') || this.isKeyDown('command')) && this.opsManager.selectedOpIds.length > 0) {
            ev.preventDefault();
            let selectedOpIds = this.opsManager.selectedOpIds.filter(opId => {
                return this.drawOpsOfPage.hasOwnProperty(opId);
            });
            this.copiedSnapshotArray = deepClone(selectedOpIds.map(opId => this.drawOpsOfPage[opId]));
            return true;
        }
        else if (key === 'v' && (this.isKeyDown('control') || this.isKeyDown('command')) && this.copiedSnapshotArray.length > 0) {
            ev.preventDefault();
            this.execCommand(this.copiedSnapshotArray.slice());
            return true;
        }
    }
    execCommand(snapshotsToCopy) {
        if (this.opsManager.selectedOpIds.length > 0
            || (snapshotsToCopy && snapshotsToCopy.length > 0)) {
            const snapshot = this.getSnapshot(snapshotsToCopy);
            this.dispatch(snapshot);
            const newOpIds = snapshot.duplicates.map(dup => dup.opId);
            this.opsManager.selectOp.dispatch(this.opsManager.selectOp.getSnapshot(newOpIds));
        }
    }
    getSnapshot(snapshotsToCopy) {
        const snapshotFromKeyboardCopy = snapshotsToCopy ? true : false;
        if (!snapshotsToCopy) {
            const selectedOpIds = this.opsManager.selectedOpIds;
            snapshotsToCopy = [];
            for (let i = 0; i < selectedOpIds.length; i++) {
                const id = selectedOpIds[i];
                if (this.drawOpsOfPage.hasOwnProperty(id)) {
                    const snapshot = this.drawOpsOfPage[id];
                    snapshotsToCopy.push(snapshot);
                }
            }
        }
        snapshotsToCopy = snapshotsToCopy.map(snapshot => {
            snapshot = deepClone(snapshot);
            snapshot.data.dx = (snapshot.data.dx ? snapshot.data.dx : 0) + COPY_PASTE_SHIFT;
            snapshot.data.dy = (snapshot.data.dy ? snapshot.data.dy : 0) + COPY_PASTE_SHIFT;
            snapshot.opId = this.opsManager.currPage.getNextOpId(true);
            return snapshot;
        });
        //ctrl-c, ctrl-vcopiedSnapshotArray 
        if (snapshotFromKeyboardCopy) {
            this.copiedSnapshotArray = snapshotsToCopy.slice();
        }
        return {
            type: this.type,
            duplicates: snapshotsToCopy,
        };
    }
}
export default DuplicateOp;
