/**
 * worker(DrawWorker)DrawWorker
 *
 * 
 */
import DrawWorker from './DrawWorker/index';
import EventEmitter from 'eventemitter3';
import { formatColor } from './utils';
import createDebug from 'debug';
import logger from '../debug';
import { isValidTool } from './validator';
const adapterLog = createDebug('AdapterDraw');
export default class AdapterDraw extends EventEmitter {
    constructor(container, opt) {
        super();
        this.onInited = () => {
            this.emit('inited');
        };
        // opt.color = '#000000'
        this.worker = new DrawWorker(container, opt);
        this.worker.on('action', this.onAction.bind(this));
        this.worker.on('instantAction', this.onInstantAction.bind(this));
        this.worker.on('inited', this.onInited);
    }
    /**
     *
     *
     * @param account
     * @param action
     */
    act(account, action) {
        // var action = this.command2Action(account, action)
        if (!action)
            return;
        let anyAction = { account: account, action: action };
        logger.m.adapter('act->', action, account);
        this.worker.act(anyAction);
    }
    // workeraction
    onAction(anyAction) {
        if (anyAction.account !== this.worker.account || !this.worker.isEnable)
            return;
        logger.m.adapter('onAction', anyAction);
        if (!anyAction)
            return;
        this.emit('action', anyAction);
    }
    // workeraction
    onInstantAction(anyAction) {
        logger.m.adapter('onInstantAction', anyAction);
        if (!anyAction)
            return;
        this.emit('instantAction', anyAction);
    }
    enableDraw(value) {
        return this.worker.enableDraw(value);
    }
    destroy() {
        this.worker.destroy();
        this.removeAllListeners();
        // delete this.worker;
        return;
    }
    useTool(value, account) {
        logger.m.adapter(value, account);
        if (isValidTool(value)) {
            this.worker.useTool(value, account);
        }
        else {
            console.warn('!->', value);
        }
    }
    resize() {
        return this.worker.resize();
    }
    removeUser(account) {
        return this.worker.removeUser(account);
    }
    setContainer(container, opt) {
        return this.worker.setContainer(container, opt);
    }
    image(opt, account) {
        return this.worker.image(opt, account);
    }
    clearImage(account) {
        return this.worker.clearImage(account);
    }
    undo(account) {
        return this.worker.undo(account);
    }
    redo(account) {
        return this.worker.redo(account);
    }
    clear(target, account, opt) {
        return this.worker.clear(target, account, opt);
    }
    setColor(color, account) {
        if (!color)
            return;
        if (formatColor(color, 10) === formatColor.INVALID) {
            console.error('whiteboard::setColor::');
        }
        else {
            console.log('adapterDraw::setColor', color);
            this.worker.setColor(color, account);
        }
    }
    setSize(size, account) {
        if (!size)
            return;
        if (!/^[\d]+$/.test('' + size)) {
            console.error('whiteboard::setSize:::', size);
            return;
        }
        console.log('adapterDraw::setSize', size);
        this.worker.setSize(size, account);
    }
    setFontsize(size, account) {
        if (!size)
            return;
        if (!/^[\d]+$/.test('' + size)) {
            console.error('whiteboard::setFontsize:::', size);
            return;
        }
        console.log('adapterDraw::setFontsize', size);
        this.worker.setFontsize(size, account);
    }
}
