import User from "../Model/User";
import { EventEmitter } from "eventemitter3";
export class UserManager extends EventEmitter {
    constructor({ account, maxUserHistory }) {
        super();
        this.userKeys = [];
        this.userMap = new Map();
        this.onUndo = (user) => {
            this.emit('undo', user);
        };
        this.onRedo = (user) => {
            this.emit('redo', user);
        };
        this.account = account;
        this.maxUserHistory = typeof maxUserHistory === 'number' ? maxUserHistory : 10;
    }
    getUser(account) {
        return this.userMap.get(account);
    }
    addUser(account) {
        if (this.userMap.has(account)) {
            return this.userMap.get(account);
        }
        else {
            let user = new User(account, this.maxUserHistory);
            // 
            user.on('undo', this.onUndo);
            user.on('redo', this.onRedo);
            this.userMap.set(account, user);
            return user;
        }
    }
    hasUser(account) {
        return this.userMap.has(account);
    }
    get myself() { return this.userMap.get(this.account); }
    ;
    get others() {
        let others = [];
        this.userMap.forEach((user, uid) => {
            if (uid === this.account) {
            }
            else {
                others.push(user);
            }
        });
        return others;
    }
    get uids() {
        return [...this.userMap.keys()];
    }
    get all() {
        return [...this.userMap.values()];
    }
    remove(account) {
        // 
        if (this.userMap.has(account)) {
            let user = this.userMap.get(account);
            user.removeAllListeners();
        }
        return this.userMap.delete(account);
    }
    removeOtherUsers() {
        this.others.forEach(user => {
            user.removeAllListeners();
            this.userMap.delete(user.account);
        });
    }
    destroy() {
        // 
        this.all.forEach(user => user.removeAllListeners());
        this.userMap.clear();
        // delete this.account
    }
    toSnapshot(opt) {
        return this.all.map(user => ({
            account: user.account,
            recycle: user.recycle.map(shape => shape.toSnapshot(opt)),
            calibrate: user.getCalibrateAction().toString()
        }));
    }
}
