/**
 * 
 */
import Operation from "../Operation";
import { snapshotOutOfNormalFlowTypes } from "../types";
class UndoOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'undo';
        this.description = "";
        this.idsCouldUndo = [];
    }
    getCount() {
        return this.idsCouldUndo.length;
    }
    registerHooks() {
        this.opsManager.hooks.addOpHook.tap("AddOpSnapshotPlugin", this.onOpAdded.bind(this));
        this.opsManager.hooks.keydown.tap("KeydownPlugin", this.handleKeyDown.bind(this));
    }
    handleKeyDown(ev) {
        const key = ev.key.toLocaleLowerCase();
        if (key === 'z' && (this.isKeyDown('control') || this.isKeyDown('command'))) {
            ev.preventDefault();
            this.execCommand();
        }
    }
    resetCount() {
        this.idsCouldUndo = [];
        this.emit('event:appState:change', 'undoCount', 0);
    }
    onOpAdded(snapshot) {
        if (snapshot.category === 'pageOp' || snapshot.category === 'boardOp') {
            this.idsCouldUndo = [];
        }
        else if (snapshot.data.skipUndo || snapshot.data.inProgress || snapshotOutOfNormalFlowTypes.includes(snapshot.data.type)) {
            return;
        }
        else if (snapshot.data.type === 'undo') {
            const index = this.idsCouldUndo.indexOf(snapshot.data.undoOpId);
            if (index !== -1) {
                this.idsCouldUndo.splice(index, 1);
            }
        }
        else if (snapshot.data.type !== 'undo') {
            if (!this.idsCouldUndo.includes(snapshot.opId)) {
                this.idsCouldUndo.push(snapshot.opId);
            }
        }
        this.emit('event:appState:change', 'undoCount', this.idsCouldUndo.length);
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    execCommand() {
        if (this.idsCouldUndo.length === 0)
            return;
        this.dispatch(this.getSnapshot());
    }
    /**
     * selectundo
     */
    getSnapshot() {
        const opOfPage = this.opsOfPage;
        let undoOpId = '';
        //undo
        /**
         * undoredoinProgressskipUndo
         * undoredoopUndoCountentryundo
         * undoopUndoCount[opId]1
         * undoredoredoopUndoCount[opId]-1undoopUndoCount[opId]0

         * W1 U1 W2 W3 W4 W5 U5 U4 W6 U6 U3
         *
         * U3IdU6opUndoCount
         * {W6}             (U6 + W6)
         * {W6, W4}         (U4)
         * {W6, W4, W5}     (U5 + W5 + W4)
         * W3W3UndoW3
         */
        const opAlreadyBeenUndo = new Set();
        for (let i = opOfPage.length - 1; i >= 0; i--) {
            //
            const op = opOfPage[i];
            const opShouldBeSkiped = op.data.inProgress || op.data.skipUndo || snapshotOutOfNormalFlowTypes.includes(op.data.type);
            if (op.data.type !== 'undo' && !opShouldBeSkiped && !opAlreadyBeenUndo.has(op.opId)) {
                undoOpId = op.opId;
                break;
            }
            else if (op.data.type === 'undo') {
                opAlreadyBeenUndo.add(op.data.undoOpId);
            }
        }
        return {
            type: this.type,
            undoOpId
        };
    }
}
export default UndoOp;
