import { max, min } from "../../misc/util";
import Operation from "../Operation";
import drawConfig from "./drawConfig";
class MouseMoveDrawOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = '';
        /**
         * epspxy1sanite ep
         */
        this.shouldSaniteEp = false;
        this.movedDuringMouseDown = false;
        this.points = [];
        /**
         * sampleSeqmousemove
         */
        this.sampleSeq = 0;
    }
    get lineWidth() {
        return Number(drawConfig.strokeWidth);
    }
    get opacity() {
        return Number(drawConfig.opacity);
    }
    get color() {
        return drawConfig.strokeColor;
    }
    get sp() {
        return this.points[0];
    }
    get ep() {
        let point = this.points[this.points.length - 1];
        if (this.shouldSaniteEp) {
            point = this.sanityEp(point);
        }
        return point;
    }
    registerHooks() {
        this.opsManager.hooks.pageChange.tap('PageChange', this.handlePageChange.bind(this));
    }
    eventsToListen() {
        return {
            activate: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    handlePageChange() {
        this.beginBatch();
        this.sampleSeq = 0;
        this.points = [];
        this.movedDuringMouseDown = false;
    }
    handleMouseMove(event, mx, my) {
        if (this.isMouseDown || this.app.container.isTargetOnContainer(event.target)) {
            document.body.style.cursor = `url(${require('../../../assets/cursors/pen.svg')}) -1 17, auto`;
        }
        else {
            document.body.style.cursor = 'auto';
        }
        if (this.isMouseDown) {
            this.movedDuringMouseDown = true;
            mx = Number(mx.toFixed(1));
            my = Number(my.toFixed(1));
            this.points.push([mx, my]);
            this.dispatch(this.getSnapshot(true));
            this.sampleSeq += 1;
        }
    }
    handleMouseDown(event, mx, my) {
        this.beginBatch();
        mx = Number(mx.toFixed(1));
        my = Number(my.toFixed(1));
        this.points = [[mx, my]];
        this.movedDuringMouseDown = false;
        this.sampleSeq = 0;
    }
    handleMouseUp(event, mx, my) {
        if (this.isMouseDown && this.movedDuringMouseDown) {
            mx = Number(mx.toFixed(1));
            my = Number(my.toFixed(1));
            this.points.push([mx, my]);
            this.dispatch(this.getSnapshot(false));
            this.points = [];
        }
    }
    getTwoPointShapeCommonProperty(opt) {
        let obj = {
            type: this.type,
            boundary: this.getBoundaryRectOfLine(),
            sp: this.sp,
            ep: this.ep,
            lineWidth: this.lineWidth,
            inProgress: opt.inProgress,
            opacity: this.opacity
        };
        if (opt.strokeOrFill === 'stroke') {
            obj.stroke = this.color;
        }
        else if (opt.strokeOrFill === 'fill') {
            obj.fill = this.color;
        }
        else {
            obj.color = this.color;
        }
        return obj;
    }
    getFreeDrawCommonProperty(opt) {
        let obj = {
            type: this.type,
            boundary: this.getBoundaryRectOfFreeDraw(),
            lineWidth: this.lineWidth,
            inProgress: opt.inProgress,
            opacity: this.opacity
        };
        obj.color = this.color;
        return obj;
    }
    getBoundaryRectOfFreeDraw() {
        const points = this.points;
        if (points.length === 0) {
            throw new Error('');
        }
        else {
            let sx = points[0][0];
            let sy = points[0][1];
            let ex = sx;
            let ey = sy;
            for (let point of points) {
                sx = min(point[0], sx);
                sy = min(point[1], sy);
                ex = max(point[0], ex);
                ey = max(point[1], ey);
            }
            return {
                sx,
                sy,
                w: ex - sx,
                h: ey - sy
            };
        }
    }
    getBoundaryRectOfLine() {
        let sx = min(this.sp[0], this.ep[0]);
        let sy = min(this.sp[1], this.ep[1]);
        let ex = max(this.sp[0], this.ep[0]);
        let ey = max(this.sp[1], this.ep[1]);
        return {
            sx: sx,
            sy: sy,
            w: ex - sx,
            h: ey - sy
        };
    }
    strokeOrFill(ctx, opt) {
        ctx.save();
        ctx.lineWidth = opt.lineWidth;
        if (opt.opacity) {
            ctx.globalAlpha = Number(opt.opacity);
        }
        if (opt.stroke) {
            ctx.strokeStyle = opt.stroke;
            ctx.stroke();
        }
        else if (opt.fill) {
            ctx.fillStyle = opt.fill;
            ctx.fill();
        }
        ctx.restore();
    }
    /**
     * rect1
     * @param ep
     */
    sanityEp(ep) {
        if (ep[0] - this.sp[0] >= 0 && ep[0] - this.sp[0] < 1) {
            ep[0] = this.sp[0] + 1;
        }
        if (ep[1] - this.sp[1] >= 0 && ep[1] - this.sp[1] < 1) {
            ep[1] = this.sp[1] + 1;
        }
        return ep;
    }
}
export default MouseMoveDrawOp;
