// KB 
export function getKiloBytes(length) {
    if (typeof length === 'number') {
        return length * 2 / 1024;
    }
    if (typeof length === 'string') {
        return length.length * 2 / 1024;
    }
    return 0;
}
export const sleep = (time = 100) => new Promise(res => {
    setTimeout(res, time);
});
export function sign(x) { return x ? x < 0 ? -1 : 1 : 0; }
