/**
 * boardboard
 */
import Operation from "../Operation";
class BoardOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'board';
        this.description = "board";
    }
    registerHooks() {
        this.opsManager.hooks.boardChange.tap("boardChangePlugin", this.onBoardChange.bind(this));
    }
    onBoardChange() {
        return this.emit('event:appState:change', 'board', {
            boardNames: Object.keys(this.app.room.boards),
            boardDisplayNames: this.app.room.getBoardDisplayNames(),
            currBoard: this.app.room.currIndex,
            currBoardDisplayName: this.app.room.currBoard.displayName
        });
    }
    eventsToListen() {
        return {
            'board:goto': {
                value: this.goto.bind(this),
                information: {
                    args: 'boardName: string',
                    message: `boardboardboard`
                }
            },
            'board:add': {
                value: this.add.bind(this),
                information: {
                    args: 'boardName: string, pageCount: number',
                    message: `[pageCount]board`
                }
            },
            'board:delete': {
                value: this.delete.bind(this),
                information: {
                    args: 'boardName: string',
                    message: 'boardboard1board'
                }
            }
        };
    }
    goto(boardName) {
        //boardboardboard
        if (boardName !== this.app.room.currIndex) {
            this.dispatch({
                type: this.type,
                action: 'goto',
                boardName: boardName
            });
        }
    }
    add(displayName, pageCount) {
        const boardName = `${displayName}_${new Date().valueOf()}`;
        this.dispatch({
            type: this.type,
            action: 'add',
            boardName: boardName,
            displayName: displayName,
            pageCount
        });
    }
    delete(boardName) {
        const boards = this.app.room.boards;
        if (boardName in boards && Object.keys(boards).length > 1) {
            this.dispatch({
                type: this.type,
                action: 'delete',
                boardName: boardName
            });
        }
    }
}
export default BoardOp;
