import { EventEmitter } from "eventemitter3";
import * as CanvasHelper from './CanvasHelper';
import logger from '../../debug';
export default class ContainerManager extends EventEmitter {
    constructor(container, opt) {
        super();
        this.paddingTop = 0; //  
        this.paddingLeft = 0; // 
        this.canvasRatio = null;
        //  canvas  width 
        this.onResize = () => {
            if (!this.container) {
                return;
            }
            if (this.onResize.waiting) {
                return;
            }
            else {
                this.onResize.waiting = true;
                setTimeout(() => {
                    this.onResize.waiting = false;
                    this.resize();
                }, 300);
            }
        };
        /**
         * 
         * @param {number} width 
         * @public
         */
        this.resize = () => {
            const width = parseInt('' + this.container.clientWidth);
            if (isNaN(width) || width <= 0)
                throw new Error('0');
            const height = parseInt('' + this.container.clientHeight);
            if (isNaN(height) || height <= 0)
                throw new Error('0');
            if (width === 0 || height === 0) {
                return;
            }
            this.containerWidth = width;
            this.containerHeight = height;
            if (this.canvasRatio === null) {
                this.calcCanvasLayout(width / height);
            }
            else {
                this.calcCanvasLayout();
            }
            this.updateCanvasStyleSize({
                // ownerCanvasHeight: ownerCanvasHeight!, ownerCanvasWidth: ownerCanvasWidth!,
                paddingLeft: this.paddingLeft, paddingTop: this.paddingTop,
                canvasWidth: this.canvasWidth, canvasHeight: this.canvasHeight,
            });
            console.log('resize');
            if (this.opt && this.opt.identity === 'owner') {
                if (this.canvasWidth && this.canvasHeight) {
                }
                this.emit('ownerCanavasUpdate', this.canvasWidth, this.canvasHeight);
                const { containerHeight: prevHeight, containerWidth: prevWidth, ownerCanvasHeight, ownerCanvasWidth } = this;
                this.updateCanvasPropSize(this.canvasWidth, this.canvasHeight);
            }
        };
        this.onUndo = () => {
            if (this.displayCtx && this.shapes && this.users) {
                this.fullRender();
                for (let user of this.users.all) {
                    if (user.isMouseDown) {
                        // clear 
                        this.displayCtx.drawImage(user.ctx.canvas, 0, 0);
                    }
                }
            }
        };
        this.onRedo = () => {
            if (this.displayCtx && this.shapes && this.users) {
                this.fullRender();
                for (let user of this.users.all) {
                    if (user.isMouseDown) {
                        // clear 
                        this.displayCtx.drawImage(user.ctx.canvas, 0, 0);
                    }
                }
            }
        };
        this.inputDom = buildInputDom(); // 
        this.opt = opt;
        this.inited = false;
        if (!container)
            return;
        this.setContainer(container, opt, true);
    }
    wrap(shapesmanager, users) {
        this.clearListener();
        this.shapes = shapesmanager;
        this.users = users;
        this.shapes.on('undo', this.onUndo);
        this.shapes.on('redo', this.onRedo);
    }
    clearListener() {
        if (this.shapes) {
            this.shapes.removeAllListeners();
        }
    }
    // container
    setContainer(container, opt, init) {
        logger.m.containermanager('setContainer', container, opt);
        if (!(container instanceof HTMLElement)) {
            throw new Error('HTMLElement');
        }
        if (this.container) {
            this.container.removeEventListener('resize', this.onResize);
            this.container.remove();
        }
        this.container = container;
        container.style.position = 'relative';
        if (!opt) {
            if (this.opt) {
                opt = this.opt;
            }
            else {
                opt = {};
            }
        }
        this.containerHeight = container.clientHeight;
        this.containerWidth = container.clientWidth;
        console.warn('canvas height!', opt, this.canvasRatio);
        // 
        if (this.canvasRatio === null) {
            if (opt.height && opt.width) {
                //
                this.canvasHeight = opt.height;
                this.canvasWidth = opt.width;
                this.updateCanvasPropSize(this.canvasWidth, this.canvasHeight);
                this.canvasRatio = this.canvasWidth / this.canvasHeight;
                // 
            }
            else {
                this.canvasRatio = this.containerWidth / this.containerHeight;
            }
        }
        let canvasRatio = this.canvasRatio;
        if (canvasRatio === null) {
            canvasRatio = this.containerWidth / this.containerHeight;
        }
        if (typeof canvasRatio === 'number') {
            this.calcCanvasLayout(canvasRatio);
        }
        this.opt = Object.assign(this.opt, opt);
        // 
        if (!this.inited) {
            this.initEssential();
        }
        //  canvas style
        this.updateCanvasStyleSize({
            // ownerCanvasHeight: ownerCanvasHeight!, ownerCanvasWidth: ownerCanvasWidth!,
            paddingLeft: this.paddingLeft, paddingTop: this.paddingTop,
            canvasWidth: this.canvasWidth, canvasHeight: this.canvasHeight,
        });
        //  canvas width
        if (init && this.opt.identity === 'owner') {
            this.updateCanvasPropSize(this.canvasWidth, this.canvasHeight);
        }
        logger.m.containermanager('setContainer/appendChild', this.displayCanvas, this.inputDom);
        container.appendChild(this.displayCanvas);
        container.appendChild(this.maskCanvas);
        container.appendChild(this.inputDom);
        this.adjustSize = false;
        window.addEventListener('resize', this.onResize);
        window.addEventListener("orientationchange", this.onResize, false);
    }
    // canvas
    calcCanvasLayout(ratio = this.canvasRatio) {
        let containerRatio = this.containerWidth / this.containerHeight;
        if (containerRatio > ratio) {
            // canvas
            this.canvasHeight = Math.floor(this.containerHeight || 0);
            this.canvasWidth = Math.floor(this.canvasHeight * ratio);
        }
        else {
            this.canvasWidth = Math.floor(this.containerWidth || 0);
            this.canvasHeight = Math.floor(this.canvasWidth / ratio);
        }
        let heightGap = this.containerHeight - this.canvasHeight;
        let widthGap = this.containerWidth - this.canvasWidth;
        this.paddingTop = Math.floor(heightGap / 2);
        this.paddingLeft = Math.floor(widthGap / 2);
    }
    // canvas
    initEssential() {
        if (typeof this.containerWidth !== 'number')
            throw new Error('');
        if (typeof this.containerHeight !== 'number')
            throw new Error('');
        const displayCanvas = document.createElement('canvas');
        this.displayCtx = displayCanvas.getContext('2d');
        // CanvasHelper.setpixelated(this.displayCtx)
        CanvasHelper.setLineStyle(this.displayCtx);
        displayCanvas.setAttribute('data-id', 'displayCanvas');
        const displayStyle = `
    position: relative;
    top: ${this.paddingTop}px;
    left: ${this.paddingLeft}px;
    box-sizing: content-box;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #ffffff;
    background-size: contain;
    z-index: ${2};
    `.trim().replace('\n', '');
        displayCanvas.style.cssText = displayStyle;
        this.displayCanvas = displayCanvas;
        const maskCanvas = document.createElement('canvas');
        this.maskCtx = maskCanvas.getContext('2d');
        CanvasHelper.setpixelated(this.maskCtx);
        CanvasHelper.setLineStyle(this.maskCtx);
        this.maskCtx.lineWidth = 1;
        maskCanvas.setAttribute('data-id', 'maskCanvas');
        const maskStyle = `
    position: absolute;
    top: ${this.paddingTop};
    left: ${this.paddingLeft};
    box-sizing: content-box;
    transform: translate3d(0,0,0);
    z-index: ${500};
    `.trim().replace('\n', '');
        maskCanvas.style.cssText = maskStyle;
        this.maskCanvas = maskCanvas;
        this.inited = true;
        logger.m.containermanager('initCanvas');
        // 
        setTimeout(() => {
            this.emit('init-canvas');
            if (this.opt && this.opt.identity === 'owner') {
                this.emit('ownerCanavasUpdate', this.canvasWidth, this.canvasHeight);
            }
        });
    }
    /**
     * 
     * @param {number} scale 
     * @public
     */
    updateCanvasStyleSize(config) {
        const { canvasWidth, canvasHeight } = config;
        if (this.maskCtx && this.maskCanvas) {
            CanvasHelper.setCavansStyleSize(this.maskCtx, canvasWidth, canvasHeight);
            CanvasHelper.applyCanvasLayout(this.maskCanvas, config);
        }
        if (this.displayCtx && this.displayCanvas) {
            CanvasHelper.setCavansStyleSize(this.displayCtx, config.canvasWidth, config.canvasHeight);
            CanvasHelper.applyCanvasLayout(this.displayCanvas, config);
        }
        // if (this.shapes) {
        //   this.shapes.resizeCanvas(config)
        // }
        if (this.users) {
            for (const user of this.users.all) {
                CanvasHelper.setCavansStyleSize(user.ctx, canvasWidth, canvasHeight);
                CanvasHelper.applyCanvasLayout(user.ctx.canvas, config);
            }
        }
    }
    updateCanvasPropSize(width, height) {
        const { ownerCanvasHeight: prevHeight, ownerCanvasWidth: prevWidth } = this;
        this.ownerCanvasWidth = width || 300;
        this.ownerCanvasHeight = height || 300;
        let opt = { width: this.ownerCanvasWidth, height: this.ownerCanvasHeight };
        if (this.maskCtx) {
            CanvasHelper.setCanvasPropSize(this.maskCtx, opt);
        }
        if (this.displayCtx) {
            CanvasHelper.setCanvasPropSize(this.displayCtx, opt);
        }
        if (this.users) {
            for (const user of this.users.all) {
                CanvasHelper.setCanvasPropSize(user.ctx, { width, height });
            }
        }
        if (this.shapes) {
            this.shapes.resizeCanvas({
                widthScale: width / prevWidth, heightScale: height / prevHeight
            });
        }
        // fullrender
        this.fullRender();
    }
    // add UserCanvas 
    addUser(currentAccount, user) {
        const canvas = user.ctx.canvas;
        const account = user.account;
        const displayCanvas = this.displayCanvas;
        // canvas.style.transform = 'translate3d(0,0,0)'
        canvas.style.position = 'absolute';
        canvas.style.boxSizing = 'content-box';
        // canvas.style.transform = 'translate3d(0,0,0)'
        canvas.style.top = this.paddingTop + 'px';
        canvas.style.left = this.paddingLeft + 'px';
        canvas.style.width = this.canvasWidth + 'px';
        canvas.style.height = this.canvasHeight + 'px';
        canvas.width = this.ownerCanvasWidth;
        canvas.height = this.ownerCanvasHeight;
        logger.m.containermanager('addUser', account, user);
        if (account === currentAccount) {
            canvas.style.cursor = 'not-allowed';
        }
        else {
            canvas.style.cursor = 'crosshair';
        }
        this.container.appendChild(canvas);
        CanvasHelper.setpixelated(user.ctx);
        CanvasHelper.setLineStyle(user.ctx);
    }
    destroy() {
        this.container.removeChild(this.maskCanvas);
        this.container.removeChild(this.displayCtx.canvas);
        this.removeAllListeners();
        window.removeEventListener('resize', this.onResize);
        window.removeEventListener("orientationchange", this.onResize, false);
    }
    fullRender() {
        if (this.displayCtx) {
            let playCanvas = this.displayCtx.canvas;
            CanvasHelper.clearCtx(this.displayCtx);
            this.displayCtx.getImageData(0, 0, 1, 1); // canvasChromeCanvas
            // playCanvas.width = playCanvas.width;
        }
        if (this.shapes && this.displayCtx) {
            const targetCtx = this.displayCtx;
            const shapes = this.shapes.visibles;
            // logger.image(targetCtx.canvas.toDataURL(), 'beforeRender, displayCtx')
            targetCtx.save();
            logger.log('displayCtx');
            for (let i = 0, len = shapes.length; i < len; i++) {
                let shape = shapes[i];
                console.assert(typeof shape.ctx !== 'undefined', 'shape ctx is ubdefined', shape);
                let skip = shape.ctx && (shape.ctx.canvas.width === 0 || shape.ctx.canvas.height === 0);
                logger.m.containermanager('render shape:', shape, 'skip', skip);
                if (skip) {
                    continue;
                }
                applyState(targetCtx, shape);
                // if (shape.mode === 'fill') return fill(ctx, shape.corner[0], shape.color,shape);
                // -0.5
                // targetCtx.drawImage(shape.ctx.canvas, shape.corner[0].x - 0.5, shape.corner[0].y)
                if (shape.tool === 'erase') {
                    console.log(targetCtx.globalCompositeOperation);
                }
                targetCtx.drawImage(shape.ctx.canvas, shape.corner[0].x, shape.corner[0].y);
            }
            targetCtx.restore();
        }
        else {
            logger.m.containermanager('no render', this.shapes, this.displayCtx);
        }
    }
}
function buildInputDom() {
    let inputDom = document.createElement('input');
    inputDom.type = 'text';
    inputDom.autofocus = true;
    inputDom.className = 'wb-canvas__input';
    let style = inputDom.style;
    style.position = 'absolute';
    style.display = 'none';
    style.padding = '0px';
    style.zIndex = '999';
    return inputDom;
}
/**
 * displayCtx
 * @param {string} account ID
 * @param {number} size 
 * @param {string} color 
 * @param {string} mode 
 * @private
 */
export function applyState(targetCtx, shapeOrUser) {
    const { ctx, size, color, tool } = shapeOrUser;
    // logger.log('applyState', ctx, size, color, tool);
    if (targetCtx === undefined) {
        console.warn('applyState!');
        return;
    }
    targetCtx.lineWidth = size;
    targetCtx.strokeStyle = color;
    targetCtx.fillStyle = color;
    //  erase  workCtx  destination-out img  
    if (tool === 'erase' && targetCtx !== ctx) {
        let opreation = targetCtx.globalCompositeOperation;
        if (opreation !== 'destination-out') {
            targetCtx.globalCompositeOperation = 'destination-out'; // 
        }
    }
    else {
        targetCtx.globalCompositeOperation = 'source-over'; // 
    }
}
