/**
 * 
 */
import Operation from "../Operation";
import { snapshotOutOfNormalFlowTypes } from "../types";
class RedoOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'redo';
        this.description = "";
        this.idsCouldRedo = [];
    }
    getCount() {
        return this.idsCouldRedo.length;
    }
    registerHooks() {
        this.opsManager.hooks.addOpHook.tap("AddOpSnapshotPlugin", this.onOpAdded.bind(this));
        this.opsManager.hooks.keydown.tap("KeydownPlugin", this.handleKeyDown.bind(this));
    }
    handleKeyDown(ev) {
        const key = ev.key.toLocaleLowerCase();
        if (key === 'y' && (this.isKeyDown('control') || this.isKeyDown('command'))) {
            ev.preventDefault();
            this.execCommand();
        }
    }
    resetCount() {
        this.idsCouldRedo = [];
        this.emit('event:appState:change', 'redoCount', 0);
    }
    onOpAdded(snapshot) {
        if (snapshot.category === 'pageOp' || snapshot.category === 'boardOp') {
            this.idsCouldRedo = [];
        }
        else if (snapshot.data.inProgress || snapshot.data.skipUndo || snapshotOutOfNormalFlowTypes.includes(snapshot.data.type)) {
            return;
        }
        else if (snapshot.data.type === 'redo') {
            const index = this.idsCouldRedo.indexOf(snapshot.data.redoOpId);
            if (index !== -1) {
                this.idsCouldRedo.splice(index, 1);
            }
        }
        else if (snapshot.data.type === 'undo') {
            if (!this.idsCouldRedo.includes(snapshot.opId)) {
                this.idsCouldRedo.push(snapshot.opId);
            }
        }
        else {
            this.idsCouldRedo = [];
        }
        this.emit('event:appState:change', 'redoCount', this.idsCouldRedo.length);
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            },
        };
    }
    execCommand() {
        if (this.idsCouldRedo.length === 0)
            return;
        this.dispatch(this.getSnapshot());
    }
    /**
     * redo+1undo-10undo
     * W1 W2 W3 W4 U4 U3 U2 R2 R3 R41 2 1 0U4
     */
    getSnapshot() {
        const opOfPage = this.opsOfPage;
        let redoOpId = '';
        let count = 0;
        for (let i = opOfPage.length - 1; i >= 0; i--) {
            const op = opOfPage[i];
            if (op.data.type === 'undo' && count === 0) {
                redoOpId = op.opId;
                break;
            }
            else if (op.data.type === 'undo') {
                count--;
            }
            else if (op.data.type === 'redo') {
                count++;
            }
            else if (!op.data.skipUndo && !snapshotOutOfNormalFlowTypes.includes(op.data.type)) {
                console.warn('DEV-->', 'redoundo', opOfPage);
            }
        }
        return {
            type: this.type,
            redoOpId
        };
    }
}
export default RedoOp;
