/**
 * 
 */
import { DEFAULT_FONTSIZE } from "../../config";
import { MAX_FONT_SIZE, MIN_FONT_SIZE, testFontSizeText } from "../../misc/text";
import './TextToolbar.less';
const ID = 'iower-23nsdf090sd';
class TextToolbar {
    constructor() {
        this.fontSize = DEFAULT_FONTSIZE;
        this.color = 'black';
        //powerpoint
        this.fonts = [8, 9, 10, 11, 12, 14, 16, 18, 20, 24, 28, 32, 36, 40, 44, 48, 54, 60, 66, 72, 80, 88];
        this.colors = [
            'rgb(224,32,32)',
            'rgb(250,100,0)',
            'rgb(247,181,0)',
            'rgb(109,212,0)',
            'rgb(68,215,182)',
            'rgb(50,197,255)',
            'rgb(0,145,255)',
            'rgb(98,54,255)',
            'rgb(182,32,224)',
            'rgb(109,114,120)',
            'rgb(0,0,0)',
            'rgb(255,255,255)'
        ];
        this.fontInputPrevValue = '';
        this.colorCells = [];
        this.colorPlatteOpen = false;
        this.toolbarContainer = document.createElement('div');
        this.toolbarContainer.className = 'text-toolbar';
        this.toolbarContainer.id = ID;
        //
        this.colorBtn = document.createElement('div');
        this.colorBtn.innerText = '';
        this.colorBtn.className = 'color flex-cell';
        const colorDot = document.createElement('span');
        this.colorBtn.appendChild(colorDot);
        //
        this.colorPlatteContainer = document.createElement('div');
        this.colorPlatteContainer.className = 'platte-container';
        this.colorPlatteContainer.onmousemove = (ev) => {
            ev.stopPropagation();
        };
        for (let i = 0; i < this.colors.length; i++) {
            const colorCell = document.createElement('div');
            colorCell.className = 'color-cell';
            colorCell.style.backgroundColor = this.colors[i];
            const isWhite = this.colors[i] === 'rgb(255,255,255)';
            if (isWhite) {
                colorCell.style.border = '1px solid #D9D9DB';
            }
            this.colorPlatteContainer.appendChild(colorCell);
            this.colorCells.push(colorCell);
        }
        //
        this.fontInput = document.createElement('input');
        this.fontInput.className = 'fontsize input';
        //
        this.fontIncBtn = document.createElement('div');
        this.fontIncBtn.className = 'fontsize inc flex-cell';
        const incText = document.createElement('span');
        incText.innerText = '+';
        this.fontIncBtn.appendChild(incText);
        //
        this.fontDecBtn = document.createElement('div');
        this.fontDecBtn.className = 'fontsize dec flex-cell';
        const decText = document.createElement('span');
        decText.innerText = '-';
        this.fontDecBtn.appendChild(decText);
        this.toolbarContainer.appendChild(this.fontInput);
        this.toolbarContainer.appendChild(this.fontIncBtn);
        this.toolbarContainer.appendChild(this.fontDecBtn);
        this.toolbarContainer.appendChild(this.colorBtn);
        this.toolbarContainer.appendChild(this.colorPlatteContainer);
        this._registerEvents();
        this._stopPropagation();
    }
    show(args) {
        const { left, top, container, fontSize, color, scale } = args;
        this.setFontsize(fontSize);
        this.setColor(color);
        if (!document.getElementById(ID)) {
            container.appendChild(this.toolbarContainer);
        }
        this.toolbarContainer.style.display = 'flex';
        this.toolbarContainer.style.transformOrigin = 'left bottom';
        this.toolbarContainer.style.transform = `scale(${scale})`;
        this.colorPlatteContainer.style.display = 'none';
        this.updatePosition({
            left, top
        });
    }
    resize(scale) {
        this.toolbarContainer.style.transform = `scale(${scale})`;
    }
    updatePosition(args) {
        const { left, top } = args;
        this.toolbarContainer.style.left = `${left - 23}px`;
        this.toolbarContainer.style.top = `${top - 78}px`;
    }
    hide() {
        this.toolbarContainer.style.display = 'none';
    }
    setFontsize(fontSize) {
        this.fontSize = fontSize;
        this.fontInputPrevValue = Math.round(fontSize).toString();
        this.fontInput.value = this.fontInputPrevValue;
    }
    setColor(color) {
        this.color = color;
        this.colorBtn.firstChild.style.backgroundColor = this.color;
        this.setColorPlatteState(false);
    }
    setColorPlatteState(open) {
        this.colorPlatteOpen = open;
        if (open) {
            this.colorPlatteContainer.style.display = 'flex';
        }
        else {
            this.colorPlatteContainer.style.display = 'none';
        }
    }
    /**
     * 
     */
    onFontSizeChange(func) {
        this.fontSizeCb = func;
    }
    /**
     * 
     */
    onColorChange(func) {
        this.colorCb = func;
    }
    _registerEvents() {
        for (let i = 0; i < this.colorCells.length; i++) {
            this.colorCells[i].onclick = (event) => {
                this.setColor(this.colors[i]);
                if (this.colorCb) {
                    this.colorCb(this.color);
                }
            };
        }
        this.colorBtn.onclick = (event) => {
            this.setColorPlatteState(!this.colorPlatteOpen);
        };
        this.fontIncBtn.onclick = (event) => {
            if (this.fontSize < MAX_FONT_SIZE) {
                this._sendFontChangeCommand(Math.min(this.fontSize + 8, MAX_FONT_SIZE));
            }
        };
        this.fontDecBtn.onclick = (event) => {
            if (this.fontSize > MIN_FONT_SIZE) {
                this._sendFontChangeCommand(Math.max(this.fontSize - 8, MIN_FONT_SIZE));
            }
        };
        const setFontSizeByInput = (blurAfterSet) => {
            const fontSizeStr = this.fontInput.value;
            if (fontSizeStr === '') {
                return;
            }
            else {
                const textType = testFontSizeText(fontSizeStr);
                let fontSize;
                if (textType === 'exceed_max') {
                    fontSize = MAX_FONT_SIZE;
                }
                else if (textType === 'exceed_min') {
                    fontSize = MIN_FONT_SIZE;
                }
                else {
                    fontSize = Number(fontSizeStr);
                }
                this.fontInput.value = fontSize;
                this.fontInputPrevValue = fontSize.toString();
                this._sendFontChangeCommand(this.fontInput.value);
                if (blurAfterSet) {
                    this.fontInput.blur();
                }
            }
        };
        this.fontInput.onkeydown = (event) => {
            event.stopPropagation();
            if (event.key === 'Enter') {
                setFontSizeByInput(true);
            }
        };
        this.fontInput.onblur = () => {
            setFontSizeByInput(false);
        };
        this.fontInput.oninput = (event) => {
            let text = event.target.value;
            text = text.replace(/\D/g, '');
            this.fontInput.value = text;
        };
    }
    /**
     * 
     * @param text
     */
    _sendFontChangeCommand(text) {
        const fontSize = typeof text === 'string' ? Number(text) : text;
        this.setFontsize(fontSize);
        if (this.fontSizeCb) {
            this.fontSizeCb(this.fontSize);
        }
    }
    /**
     * 
     */
    _stopPropagation() {
        this.toolbarContainer.onmousedown = (event) => {
            event.stopPropagation();
        };
        this.toolbarContainer.ontouchstart = (event) => {
            event.stopPropagation();
        };
        //mouseup
        // this.toolbarContainer.onmouseup = (event) => {
        //     event.stopPropagation()
        // }
    }
}
export default TextToolbar;
