/**
 * ctrl-v
 */
import Operation from "../Operation";
class DeleteOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'delete';
        this.description = "";
    }
    registerHooks() {
        this.opsManager.hooks.keydown.tap("KeydownPlugin", this.handleKeyDown.bind(this));
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: ``
                }
            }
        };
    }
    handleKeyDown(ev) {
        const key = ev.key.toLocaleLowerCase();
        if (['backspace', 'delete'].includes(key)) {
            this.execCommand();
            return true;
        }
    }
    execCommand() {
        if (this.opsManager.selectedOpIds.length > 0) {
            this.dispatch(this.getSnapshot());
        }
    }
    getSnapshot() {
        return {
            type: this.type,
            deleteOpIds: this.opsManager.selectedOpIds.slice(),
            dependOn: this.opsManager.selectedOpIds.slice()
        };
    }
    /**
     * Op
     */
    transformOp(baseOp, transformOp) {
        const newData = Object.assign({}, baseOp.data, {
            hidden: true
        });
        return Object.assign({}, baseOp, {
            data: newData
        });
    }
}
export default DeleteOp;
