// schema = { // 
//     appKey: {
//       type: 'string',
//       isRequire: true
//     },
//     account: {
//       type: 'string',
//       isRequire: true
//     },
//     token: {
//       type: 'string',
//       isRequire: true
//     },
//     record: {
//       type: 'boolean',
//       defaultValue: false
//     },
//     channelName: {
//       type: 'string|number',
//       isRequire: true
//     },
//     platform: {
//       type: 'string',
//       isRequire: true,
//       enum: PlatformArray
//     }
//   }
// validate(schema)
// ===>>
// [{key, message}]
export default {
    isUndefined(value) {
        return typeof value === 'undefined';
    },
    isDOM(value) {
        if (typeof HTMLElement === 'object') {
            return value instanceof HTMLElement;
        }
        return value && typeof value === 'object' && value.nodeType === 1 && typeof value.nodeName === 'string';
    },
    isValidString(value) {
        return Object.prototype.toString.call(value) === '[object String]';
    },
    isBoolean(value) {
        return typeof value === 'boolean';
    },
    isFunction(value) {
        return typeof value === 'function';
    },
    isNumber(value) {
        return typeof value === 'number';
    },
    isValidArray(value) {
        return Object.prototype.toString.call(value) === '[object Array]';
    },
    isValidHexColor(value) {
        return /^#[0-9abcdef]{6}$/gi.test(value);
    },
    isObject(value) {
        return typeof value === 'object';
    },
    isValidObject(value) {
        if (typeof value !== 'object') {
            return false;
        }
        value = Object.assign({}, value);
        if (Object.keys && Object.keys(value).length === 0) {
            return false;
        }
        try {
            return JSON.stringify(value) !== '{}';
        }
        catch (e) {
            return false;
        }
    },
    // 
    // data rule skipKey resObj 
    /* rule 
    {
      account: {
        type: 'string', // dom string boolean function number string|number
        isRequire: true
      }
     }
     */
    validate(position, data, ruleObj) {
        let validateErrors = [];
        Object.keys(ruleObj).map((key) => {
            const rule = ruleObj[key];
            let dataValue = data[key];
            if (this.isUndefined(dataValue)) {
                /**
                 * 
                 */
                if (rule.isRequire) {
                    validateErrors.push({
                        key: key,
                        message: `${key} is required`
                    });
                }
            }
            else {
                /**
                 * 
                 */
                switch (rule.type.toLowerCase()) {
                    case 'dom':
                        if (!this.isDOM(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected dom, but get other type`
                            });
                        }
                        break;
                    case 'string':
                        if (!this.isValidString(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected string, but get other type`
                            });
                        }
                        break;
                    case 'boolean':
                        if (!this.isBoolean(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected boolean, but get other type`
                            });
                        }
                        break;
                    case 'function':
                        if (!this.isFunction(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected function, but get other type`
                            });
                        }
                        break;
                    case 'number':
                        if (!this.isNumber(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected number, but get other type`
                            });
                        }
                        break;
                    case 'object':
                        if (!this.isObject(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected object, but get other type`
                            });
                        }
                        break;
                    case 'array':
                        if (!this.isValidArray(dataValue)) {
                            validateErrors.push({
                                key: key,
                                message: `Expected array, but get other type`
                            });
                        }
                        break;
                    default:
                        validateErrors.push({
                            key: key,
                            message: `${key}: ${rule.type.toLowerCase()}`
                        });
                }
                /**
                 * 
                 */
                if (rule.enum && !rule.enum.includes(dataValue)) {
                    validateErrors.push({
                        key,
                        message: `Expected ${key} to be one of ${JSON.stringify(rule.enum)}, but it is not`
                    });
                }
            }
        });
        if (validateErrors.length > 0) {
            console.error('-->', `${position}`);
            console.error('-->', '', validateErrors);
            console.error('-->', '', data);
        }
        return validateErrors.length === 0;
    }
};
