/**
 * opQueuedrawOpQueuehiddenOpQueue
 *
 * opQueuedrawOpQueuehiddenOpQueue
 */
import { snapshotOutOfNormalFlowTypes } from "../ops/types";
import { transformOp, getSelectedOpIds } from "./util";
const missOpSet = new Set();
export const calcDrawOpQueue = (page, isReceivingBatchSnapshots = false) => {
    page.drawOpQueue = {};
    page.hiddenOpQueue = {};
    const { normalQueue, abNormalQueue } = separateNormalQueue(page, page.opQueue);
    const noHistOpQueue = removeHistoryOpQueue(page, normalQueue);
    const noClearQueue = removeClearOpQueue(page, noHistOpQueue);
    const simpleQueue = removeDuplicateOpQueue(page, noClearQueue);
    handleAbNormalQueue(page, abNormalQueue);
    handleNormalQueue(page, simpleQueue);
    if (!isReceivingBatchSnapshots) {
        filterSelectedOpIds(page);
    }
};
const handleAbNormalQueue = (page, abNormalQueue) => {
    page.backgroundSnapshot = undefined;
    for (let i = 0; i < abNormalQueue.length; i++) {
        const snapshot = abNormalQueue[i];
        if (snapshot.data.type === 'background') {
            page.backgroundSnapshot = snapshot;
        }
        else if (snapshot.data.type === 'select') {
            if (snapshot.data.inProgress) {
                page.lastInProgressSelectOfUser[snapshot.uid] = snapshot;
            }
            else {
                //
                delete page.lastInProgressSelectOfUser[snapshot.uid];
                page.lastSelectOfUser[snapshot.uid] = snapshot;
            }
        }
        else if (snapshot.data.type === 'mouse-pointer') {
            page.lastMousePointerOfUser[snapshot.uid] = snapshot;
        }
        else if (snapshot.data.type === 'laser') {
            //addSnapshotAndDrawOnlyIt
        }
    }
};
const handleNormalQueue = (page, simpleQueue) => {
    /**
     * 
     */
    const opQueueMap = {};
    const dependOnIdArr = [];
    for (let snapshot of simpleQueue) {
        //inProgressSelectSnapshot
        page.lastInProgressSelectOfUser = {};
        if (Array.isArray(snapshot.data.dependOn)) {
            for (let dependOpId of snapshot.data.dependOn) {
                if (!opQueueMap.hasOwnProperty(dependOpId)) {
                    if (LOG && !missOpSet.has(dependOpId)) {
                        missOpSet.add(dependOpId);
                        console.error('DEV-->', 'miss op', opQueueMap, snapshot, dependOpId, simpleQueue);
                    }
                }
                else {
                    dependOnIdArr.push(dependOpId);
                    opQueueMap[dependOpId].push(snapshot);
                }
            }
        }
        else if (typeof snapshot.data.dependOn === 'string') {
            if (!opQueueMap.hasOwnProperty(snapshot.data.dependOn)) {
                if (LOG && !missOpSet.has(snapshot.data.dependOn)) {
                    missOpSet.add(snapshot.data.dependOn);
                    console.error('DEV-->', 'miss op', opQueueMap, snapshot, snapshot.data.dependOn, simpleQueue);
                }
            }
            else {
                dependOnIdArr.push(snapshot.data.dependOn);
                opQueueMap[snapshot.data.dependOn].push(snapshot);
            }
        }
        else {
            //
            opQueueMap[snapshot.opId] = [snapshot];
        }
    }
    removeInprogressSnapshotIfBeenDepended(page, dependOnIdArr);
    for (let key in opQueueMap) {
        let drawOp = getDrawOpFromSnapshotQueue(page, key, opQueueMap);
        if (!drawOp.data.hidden) {
            page.drawOpQueue[key] = drawOp;
        }
        else {
            page.hiddenOpQueue[key] = drawOp;
        }
    }
};
/*
 * 
 */
const filterSelectedOpIds = (page) => {
    //drawOpQueueselectidstextOpdrawOpQueue
    if (page.app.room.currBoard.currPage !== page) {
        //selectOp)
        return;
    }
    const selectedOpIds = getSelectedOpIds(page);
    page.opsManager.selectOp.setSelectedOpIds(selectedOpIds);
    const inProgressSelectOfCurrUser = page.lastInProgressSelectOfUser[page.app.account];
    if (inProgressSelectOfCurrUser) {
        page.opsManager.selectOp.setInProgressSelectedOpIds(inProgressSelectOfCurrUser.data.selectedOpIds);
    }
    else {
        page.opsManager.selectOp.setInProgressSelectedOpIds([]);
    }
};
/**
 * opIntermediateMap[dependOpId]: Array<IOpSnapshot>
 */
const getDrawOpFromSnapshotQueue = (page, opId, opQueueMap) => {
    let drawOp = opQueueMap[opId][0];
    for (let i = 1; i < opQueueMap[opId].length; i++) {
        drawOp = transformOp(page, drawOp, opQueueMap[opId][i]);
    }
    return drawOp;
};
/**
 * 
 * h5videoclearundoredo
 */
const separateNormalQueue = (page, opQueue) => {
    //normalQueue, abNormalQueue
    let normalQueue = [];
    let abNormalQueue = [];
    for (let op of opQueue) {
        if (snapshotOutOfNormalFlowTypes.includes(op.data.type)) {
            abNormalQueue.push(op);
        }
        else {
            normalQueue.push(op);
        }
    }
    return {
        normalQueue,
        abNormalQueue
    };
};
/**
 * 
 */
const removeHistoryOpQueue = (page, opQueue) => {
    const res = opQueue.slice();
    for (let i = res.length - 1; i >= 0; i--) {
        const snapshot = res[i];
        if (snapshot.data.type === 'undo' || snapshot.data.type === 'redo') {
            let histSnapshotId = snapshot.data.undoOpId || snapshot.data.redoOpId;
            for (let k = i - 1; k >= 0; k--) {
                if (res[k].opId === histSnapshotId) {
                    //
                    res.splice(i, 1);
                    res.splice(k, 1);
                    i--;
                }
            }
        }
    }
    return res;
};
/**
 * 
 * @param opQueue
 */
const removeClearOpQueue = (page, opQueue) => {
    let res = opQueue.slice();
    for (let i = res.length - 1; i >= 0; i--) {
        const snapshot = res[i];
        if (snapshot.data.type === 'clear') {
            /**
             * clearinProgress
             */
            removeInprogressSnapshotIfBeenDepended(page, res.slice(0, i + 1).map(_ => _.opId));
            res = res.slice(i + 1);
            return res;
        }
    }
    return res;
};
/**
 * 
 * @param opQueue
 */
const removeDuplicateOpQueue = (page, opQueue) => {
    let res = [];
    for (let i = 0; i < opQueue.length; i++) {
        const snapshot = opQueue[i];
        if (snapshot.data.type === 'duplicate') {
            res = res.concat(snapshot.data.duplicates);
        }
        else {
            res.push(opQueue[i]);
        }
    }
    return res;
};
/**
 * inProgressSnapshotOfUser
 * refreshinProgressSnapshotOfUser
 *
 * 
 * @param page
 * @param dependOn
 */
const removeInprogressSnapshotIfBeenDepended = (page, dependOnIds) => {
    for (let uid in page.lastInProgressSnapshotOfUser) {
        const sp = page.lastInProgressSnapshotOfUser[uid];
        if (dependOnIds.includes(sp.snapshot.opId)) {
            delete page.lastInProgressSnapshotOfUser[uid];
        }
    }
};
