/**
 * 
 * 
 */
import Operation from "../Operation";
import { combineBoundary, getTransformedBoundary, pointInRect } from "../../misc/shapeHelper";
import { arrayDeduce, findOpsUnderMouse } from "../util";
import SelectOp from "../SelectOp";
class MoveOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'move';
        this.points = [];
        this.activate = false;
        this.description = "";
        this.idsBeenMoved = [];
    }
    registerHooks() {
        this.opsManager.hooks.mousedown.tap("MousedownPlugin", this.handleMouseDown.bind(this));
        this.opsManager.hooks.mousemove.tap("MousemovePlugin", this.handleMouseMove.bind(this));
        this.opsManager.hooks.mouseup.tap("MouseupPlugin", this.handleMouseUp.bind(this));
    }
    handleMouseMove(event, mx, my) {
        if (this.opsManager.currOp instanceof SelectOp) {
            if (this.activate) {
                this.points.push([mx, my]);
                this.dispatch(this.getSnapshot(true, this.idsBeenMoved));
                return true;
            }
            if (!this.isMouseDown && document.body.style.cursor !== 'nwse-resize' && document.body.style.cursor !== 'nesw-resize') {
                if (this.mouseInSelectedEleBoundary(mx, my, this.opsManager.selectedOpIds) ||
                    this.opsManager.inProgressSelectedOpIds.length > 0) {
                    //
                    document.body.style.cursor = 'move';
                }
                else {
                    const idsUnderMouse = findOpsUnderMouse([[mx, my]], this.drawOpsOfPage, this.opsManager);
                    const idsBeenSelected = arrayDeduce(idsUnderMouse, this.opsManager.selectedOpIds.concat(this.opsManager.inProgressSelectedOpIds));
                    if (idsBeenSelected.length > 0) {
                        //
                        document.body.style.cursor = 'move';
                    }
                    else {
                        document.body.style.cursor = 'default';
                    }
                }
            }
        }
    }
    handleMouseDown(event, mx, my) {
        if (this.opsManager.currOp instanceof SelectOp) {
            const mouseInProgressSelectedBoundary = this.mouseInSelectedEleBoundary(mx, my, this.opsManager.inProgressSelectedOpIds);
            const mouseInSelectedBoundary = this.mouseInSelectedEleBoundary(mx, my, this.opsManager.selectedOpIds);
            if (mouseInProgressSelectedBoundary) {
                /**
                 * 
                 */
                const idsUnderMouse = this.opsManager.inProgressSelectedOpIds.slice();
                this.dispatch(this.opsManager.selectOp.getSnapshot(idsUnderMouse));
                this.beginBatch();
                document.body.style.cursor = 'move';
                this.idsBeenMoved = idsUnderMouse.slice();
                this.points = [[mx, my]];
                this.activate = true;
                return true;
            }
            else if (mouseInSelectedBoundary) {
                /**
                 * 
                 */
                this.beginBatch();
                document.body.style.cursor = 'move';
                this.idsBeenMoved = this.opsManager.selectedOpIds;
                this.points = [[mx, my]];
                this.activate = true;
                return true;
            }
            else {
                /**
                 * 
                 * 
                 */
                const idsUnderMouse = findOpsUnderMouse([[mx, my]], this.drawOpsOfPage, this.opsManager);
                if (idsUnderMouse.length > 0) {
                    //
                    this.dispatch(this.opsManager.selectOp.getSnapshot(idsUnderMouse));
                    this.beginBatch();
                    document.body.style.cursor = 'move';
                    this.idsBeenMoved = idsUnderMouse.slice();
                    this.points = [[mx, my]];
                    this.activate = true;
                    return true;
                }
            }
        }
        this.activate = false;
    }
    handleMouseUp(event, mx, my) {
        if (this.activate) {
            this.activate = false;
            this.points.push([mx, my]);
            this.dispatch(this.getSnapshot(false, this.idsBeenMoved));
            return true;
        }
    }
    getSnapshot(inProgress, idsBeenMoved) {
        return {
            type: this.type,
            sp: this.points[0],
            ep: this.points[this.points.length - 1],
            selectedOpIds: idsBeenMoved.slice(),
            dependOn: idsBeenMoved.slice(),
            inProgress: inProgress
        };
    }
    /**
     * 
     */
    mouseInSelectedEleBoundary(mx, my, selectedOpIds) {
        selectedOpIds = selectedOpIds.filter(id => {
            return this.drawOpsOfPage.hasOwnProperty(id);
        });
        if (selectedOpIds.length === 0)
            return false;
        const padding = this.opsManager.selectOp.eleContainerPadding;
        const boundary = selectedOpIds.map(id => {
            const op = this.drawOpsOfPage[id];
            return getTransformedBoundary(op.data.boundary, op.data.dx, op.data.dy, op.data.scaleX, op.data.scaleY);
        }).reduce((b1, b2) => {
            return combineBoundary(b1, b2);
        });
        return pointInRect({
            sx: boundary.sx - padding,
            sy: boundary.sy - padding,
            w: boundary.w + padding * 2,
            h: boundary.h + padding * 2
        }, [mx, my]);
    }
    /**
     * Op
     */
    transformOp(baseOp, transformOp) {
        const moveOpSnapshot = transformOp.data;
        let dx;
        let dy;
        if (Array.isArray(moveOpSnapshot.points)) {
            /**
             * 3.0.2
             */
            dx = moveOpSnapshot.points[moveOpSnapshot.points.length - 1][0] - moveOpSnapshot.points[0][0];
            dy = moveOpSnapshot.points[moveOpSnapshot.points.length - 1][1] - moveOpSnapshot.points[0][1];
        }
        else {
            dx = moveOpSnapshot.ep[0] - moveOpSnapshot.sp[0];
            dy = moveOpSnapshot.ep[1] - moveOpSnapshot.sp[1];
        }
        const newData = Object.assign({}, baseOp.data, {
            dx: dx + (baseOp.data.dx || 0),
            dy: dy + (baseOp.data.dy || 0),
            inProgress: moveOpSnapshot.inProgress
        });
        const res = Object.assign({}, baseOp, {
            data: newData
        });
        return res;
    }
}
export default MoveOp;
