import Operation from "../Operation";
import { convertToCameraBoundSnapshot } from "./CameraBoundOp";
import { shouldDispatch } from "./util";
class FitToDocOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'fit-to-doc';
        this.description = "";
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    execCommand(animate = true) {
        if (!shouldDispatch(this.app))
            return;
        if (!this.app.room.currBoard.currPage.backgroundSnapshot)
            return;
        return this.dispatch(this.getSnapshot(animate));
    }
    getSnapshot(animate) {
        return convertToCameraBoundSnapshot(this.app.camera, {
            type: 'fit-to-doc',
            animate
        });
    }
}
export default FitToDocOp;
