let timer;
export const transitionToCameraBound = (camera, boundary, callback) => {
    const end = boundary;
    const start = {
        centerX: camera.centerX,
        centerY: camera.centerY,
        width: camera.width,
        height: camera.height
    };
    /**
     * 
     */
    const total = camera.app.isRecordPlayer ? 1 : 15;
    animate({
        total: total,
        worker: (curr) => {
            const percent = (curr + 1) / total;
            camera.setCameraBound({
                centerX: end.centerX * percent + start.centerX * (1 - percent),
                centerY: end.centerY * percent + start.centerY * (1 - percent),
                width: end.width * percent + start.width * (1 - percent),
                height: end.height * percent + start.height * (1 - percent)
            });
        },
        callback: callback
    });
};
export const animate = (opt) => {
    cancelAnimationFrame(timer);
    step(0, opt);
};
const step = (curr, opt) => {
    if (curr >= opt.total) {
        if (typeof opt.callback === 'function') {
            opt.callback();
        }
    }
    else {
        requestAnimationFrame(() => {
            opt.worker(curr);
            step(curr + 1, opt);
        });
    }
};
