import * as CanvasHelper from '../DrawWorker/CanvasHelper';
import logger from '../../debug';
import { sign } from '../utils';
class Shape {
    constructor({ user, mode, tool, size, color, start, actions, fontsize, keyDown }) {
        let id = performance && performance.timing && performance.timing.navigationStart + performance.now();
        this.id = id || Date.now();
        this.user = user;
        this.visible = true;
        // Canvas
        this.ctx = document.createElement('canvas').getContext('2d');
        const shapeCanvas = this.ctx.canvas;
        const userCanvas = user.ctx.canvas;
        shapeCanvas.style.width = userCanvas.style.width;
        shapeCanvas.style.height = userCanvas.style.height;
        this.ctx.lineWidth = user.ctx.lineWidth;
        this.coordWidth = userCanvas.width;
        this.coordHeight = userCanvas.height;
        CanvasHelper.setCanvasPropSize(this.ctx, { width: this.coordWidth, height: this.coordHeight });
        this.tool = tool;
        this.mode = mode;
        this.color = color;
        this.size = size;
        this.fontsize = fontsize;
        // actionaction
        if (Array.isArray(actions)) {
            this.actions = actions;
        }
        else if (typeof actions === 'undefined') {
            console.warn('action undefined! create a shape');
            this.actions = [];
        }
        else {
            this.actions = [actions];
        }
        this.keyDown = keyDown;
        // 
        this.start = start.toCoord(this.coordWidth, this.coordHeight);
        // Canvas
        this.corner = [this.start.clone(), this.start.clone()];
        this.transform = [];
        this.path = [];
        this.zIndex = 1;
        this.detail = Shape.calculateRadius(this.tool, this.start, this.start);
    }
    static calculateRadius(tool, start, end) {
        const width = Math.abs(end.x - start.x);
        const height = Math.abs(end.y - start.y);
        let radius;
        let xRadius;
        let yRadius;
        switch (tool) {
            case 'circle':
            case 'solidCircle':
            case 'ellipse':
            case 'solidEllipse':
                {
                    xRadius = width;
                    yRadius = height;
                    radius = xRadius > yRadius ? xRadius : yRadius;
                    break;
                }
        }
        return {
            xRadius,
            yRadius,
            radius,
            width,
            height,
        };
    }
    updateCorner(offset) {
        const end = this.getCoord(offset);
        const start = this.start;
        const size = this.size;
        const [topLeft, bottomRight] = this.corner;
        const detail = Shape.calculateRadius(this.tool, start, end);
        this.detail = detail;
        const { xRadius, yRadius, radius, width, height, } = detail;
        // const width = Math.abs(end.x - start.x);
        // const height = Math.abs(end.y - start.y)
        // let radius: number | undefined;
        // let xRadius: number | undefined;
        // let yRadius: number | undefined;
        switch (this.tool) {
            case 'solidCircle':
            case 'circle': {
                topLeft.x = start.x - radius - size;
                topLeft.y = start.y - radius - size;
                bottomRight.x = start.x + radius + size;
                bottomRight.y = start.y + radius + size;
                break;
            }
            case 'ellipse':
            case 'solidEllipse': {
                topLeft.x = start.x - xRadius - size;
                topLeft.y = start.y - yRadius - size;
                bottomRight.x = start.x + xRadius + size;
                bottomRight.y = start.y + yRadius + size;
                break;
            }
            case 'rect':
            case 'roundRect':
            case 'solidRect':
            case 'solidRoundRect':
            case 'line': {
                // 
                topLeft.copy(start.clone().topLeft(end));
                bottomRight.copy(start.clone().bottomRight(end));
                break;
            }
            case 'text':
            case 'free':
            case "erase":
                {
                    // 
                    topLeft.topLeft(end);
                    bottomRight.bottomRight(end);
                    break;
                }
            case 'flag': // cornershape
            case 'fill': // corner
                // case 'select':
                break;
            default:
                const fullCaseChecker = this.tool;
        }
        // strokesize cornersize
        if (this.tool !== 'fill' &&
            this.tool !== 'text' &&
            this.tool !== 'free' &&
            this.tool !== 'erase') {
            topLeft.x -= size;
            topLeft.y -= size;
            bottomRight.x += size;
            bottomRight.y += size;
        }
        return detail;
    }
    expandCorner(size = this.size) {
        const [topLeft, bottomRight] = this.corner;
        topLeft.x -= size;
        topLeft.y -= size;
        bottomRight.x += size;
        bottomRight.y += size;
    }
    destroy() {
        logger.shape('shape delete ctx', this.user, this.ctx, this.actions);
        // console.error(new Error('delete shapes'))
        // delete this.user;
        // delete this.ctx;
        // delete this.actions;
    }
    toSnapshot({ width, height }) {
        const { id, mode, tool, size, fontsize, color, start, actions, keyDown, } = this;
        if (tool === 'fill') {
            return {
                id,
                user: this.user.account,
                mode, tool, size, fontsize, color,
                start: start.toPoint(width, height).toString(),
                optimized: true,
                imageData: this.ctx.canvas.toDataURL(),
                keyDown,
                corner: this.corner.map(x => x.toPoint(width, height).toString()),
                actions: actions
                    .filter(x => x)
                    .map(action => action.toString())
                    .join(';') + ';'
            };
        }
        else {
            return {
                id,
                user: this.user.account,
                mode, tool, size, fontsize, color,
                start: start.toPoint(width, height).toString(),
                optimized: false,
                imageData: '',
                corner: this.corner.map(x => x.toPoint(width, height).toString()),
                keyDown,
                actions: actions
                    .filter(x => x)
                    .map(action => action.toString())
                    .join(';') + ';'
            };
        }
    }
    draw(displayCtx) {
        let ownerCoord = this.user.end.toCoord(this.coordWidth, this.coordHeight);
        switch (this.tool) {
            case 'fill': {
                CanvasHelper.fill(displayCtx, ownerCoord, this.color, this);
                break;
            }
            case 'erase':
            case 'free': {
                CanvasHelper.free(displayCtx, this.getCoord(this.user.prevOldMid), this.getCoord(this.user.prevEnd), this.getCoord(this.user.oldMid), this.color, this.size);
                break;
            }
            case 'line': {
                CanvasHelper.line(displayCtx, this.getCoord(this.user.start), this.getCoord(this.user.end), this.color);
                break;
            }
            case 'rect':
            case 'solidRect': {
                let endCoord = this.getCoord(this.user.end);
                let startCoord = this.getCoord(this.user.start);
                let xOffset = endCoord.x - startCoord.x;
                let yOffset = endCoord.y - startCoord.y;
                let offset = Math.min(Math.abs(xOffset), Math.abs(yOffset));
                if (this.user.keyDown.shiftKey) {
                    xOffset = sign(xOffset) * offset;
                    yOffset = sign(yOffset) * offset;
                }
                CanvasHelper.rect(displayCtx, startCoord, xOffset, yOffset, this.tool === 'solidRect', this.color);
                break;
            }
            case 'roundRect':
            case 'solidRoundRect': {
                let endCoord = this.getCoord(this.user.end);
                let startCoord = this.getCoord(this.user.start);
                let xOffset = endCoord.x - startCoord.x;
                let yOffset = endCoord.y - startCoord.y;
                let offset = Math.min(Math.abs(xOffset), Math.abs(yOffset));
                const radius = offset < this.size ? 2 : offset * 0.1;
                if (this.user.keyDown.shiftKey) {
                    xOffset = sign(xOffset) * offset;
                    yOffset = sign(yOffset) * offset;
                }
                CanvasHelper.roundRect(displayCtx, startCoord, xOffset, yOffset, radius, this.tool === 'solidRoundRect', this.color);
                break;
            }
            case 'circle':
            case 'solidCircle': {
                let { radius } = this.detail;
                let startCoord = this.getCoord(this.user.start);
                CanvasHelper.circle(displayCtx, startCoord, radius, this.tool === 'solidCircle', this.color);
                break;
            }
            case 'ellipse':
            case 'solidEllipse': {
                let startCoord = this.getCoord(this.user.start);
                const { xRadius, yRadius } = this.detail;
                CanvasHelper.ellipse(displayCtx, startCoord, xRadius, yRadius, this.tool === 'solidEllipse', this.color);
                break;
            }
        }
    }
    freeMove(displayCtx) {
        CanvasHelper.free(displayCtx, this.getCoord(this.user.prevOldMid), this.getCoord(this.user.prevEnd), this.getCoord(this.user.oldMid), this.color, this.size);
    }
    freeEnd(displayCtx) {
        CanvasHelper.free(displayCtx, this.getCoord(this.user.prevOldMid), this.getCoord(this.user.oldMid), this.getCoord(this.user.end), this.color, this.size);
    }
    getCoord(offset) {
        return offset.toCoord(this.coordWidth, this.coordHeight);
    }
}
export default Shape;
