/**
 * 
 */
import Operation from "../Operation";
import { convertToCameraBoundSnapshot } from './CameraBoundOp';
import { shouldDispatch } from "./util";
import { showToast } from "../../../component/toast";
class PanOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'pan';
        this.activate = false;
        this.description = "";
    }
    eventsToListen() {
        return {
            activate: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    handleMouseMove(event, mx, my, clientX, clientY) {
        if (!this.app.enableCameraTransform) {
            return;
        }
        if (!shouldDispatch(this.app))
            return;
        if (this.activate) {
            document.body.style.cursor = 'grabbing';
        }
        else if (this.app.container.isTargetOnContainer(event.target)) {
            document.body.style.cursor = 'grab';
        }
        else {
            document.body.style.cursor = 'auto';
        }
        if (this.activate && this.lastPoint) {
            this.dispatch(this.getSnapshot(clientX - this.lastPoint[0], clientY - this.lastPoint[1]));
            this.lastPoint = [clientX, clientY];
        }
    }
    handleMouseDown(event, mx, my, clientX, clientY) {
        if (!this.app.appConfig.cameraTransformEnabled) {
            return;
        }
        if (!shouldDispatch(this.app)) {
            showToast({
                msg: '',
                type: 'info'
            });
            return;
        }
        this.activate = true;
        this.beginBatch();
        this.lastPoint = [clientX, clientY];
    }
    handleMouseUp(event, mx, my, clientX, clientY) {
        if (!this.app.appConfig.cameraTransformEnabled) {
            return;
        }
        if (!shouldDispatch(this.app))
            return;
        if (this.activate && this.lastPoint) {
            this.dispatch(this.getSnapshot(clientX - this.lastPoint[0], clientY - this.lastPoint[1]));
            this.lastPoint = undefined;
        }
        this.activate = false;
    }
    getSnapshot(dx, dy) {
        return convertToCameraBoundSnapshot(this.app.camera, {
            type: 'pan',
            dx: -dx,
            dy: -dy,
            animate: false
        });
    }
}
export default PanOp;
