import Operation from '../Operation';
export const convertToCameraBoundSnapshot = (camera, snapshotData) => {
    const boundary = camera.getNextCameraBound(snapshotData);
    /**
     * boundary
     */
    const MAX_ALLOWED_WIDTH = 100000;
    if (boundary.width > MAX_ALLOWED_WIDTH || boundary.height > MAX_ALLOWED_WIDTH) {
        if (boundary.width > boundary.height) {
            boundary.height = boundary.height * MAX_ALLOWED_WIDTH / boundary.width;
            boundary.width = MAX_ALLOWED_WIDTH;
        }
        else {
            boundary.width = boundary.width * MAX_ALLOWED_WIDTH / boundary.height;
            boundary.height = MAX_ALLOWED_WIDTH;
        }
    }
    /**
     * widthheight
     */
    if (boundary.width === 0 || boundary.height === 0) {
        boundary.width = 1000;
        boundary.height = 1000;
    }
    if (LOG) {
        console.log(`--> convert camera bound op: ${JSON.stringify(snapshotData)} -> ${JSON.stringify(boundary)}`);
    }
    return {
        type: 'camera-bound',
        boundary,
        skipUndo: true,
        animate: snapshotData.animate,
        reuseOpType: 'camera-bound'
    };
};
class CameraBoundOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'camera-bound';
        this.description = "";
    }
    eventsToListen() {
        return {
            'camera-bound:send': {
                value: this.sendCameraBound.bind(this),
                information: {
                    args: 'boundary: {centerX: number, centerY: number, width: number, width: number}, animate: boolean',
                    message: `camera-boundcamera-bound`
                }
            }
        };
    }
    sendCameraBound(boundary, animate) {
        this.dispatch({
            type: 'camera-bound',
            boundary,
            skipUndo: true,
            animate,
            reuseOpType: 'camera-bound'
        });
    }
}
export default CameraBoundOp;
