export const drawCurve = (ctx, points, k = 0.3) => {
    if (k > 0.5 || k <= 0) {
        console.error('k should be between 0 and 0.5');
    }
    ctx.beginPath();
    if (points.length <= 2) {
        if (points.length === 2) {
            ctx.moveTo(...points[0]);
            ctx.lineTo(...points[1]);
        }
    }
    else {
        ctx.moveTo(...points[0]);
        let lastP = points[0];
        let currP = points[1];
        // let segPoint1: Point = [k * lastP[0] + (1-k) * currP[0], k * lastP[1] + (1-k) * currP[1]]
        let ctrlPtInLastSeg = [k * lastP[0] + (1 - k) * currP[0], k * lastP[1] + (1 - k) * currP[1]];
        if (Math.abs(k - 0.5) > 0.01) {
            ctx.lineTo(...ctrlPtInLastSeg);
        }
        lastP = currP;
        for (let i = 2; i < points.length; i++) {
            currP = points[i];
            let ctrlPtInSeg1 = [(1 - k) * lastP[0] + k * currP[0], (1 - k) * lastP[1] + k * currP[1]];
            let ctrlPtInSeg2 = [k * lastP[0] + (1 - k) * currP[0], k * lastP[1] + (1 - k) * currP[1]];
            ctx.quadraticCurveTo(...lastP, ...ctrlPtInSeg1);
            ctx.lineTo(...ctrlPtInSeg2);
            ctrlPtInLastSeg = ctrlPtInSeg2;
            lastP = currP;
        }
        ctx.lineTo(...points[points.length - 1]);
    }
};
