import { hideToast, showToast } from "../../component/toast";
let drawPlugin;
let imageDimension = {};
const AcceptTypesArr = ['jpg', 'jpeg', 'png', 'bmp', 'webp'];
const imageInput = document.createElement('input');
window.onload = () => {
    document.body.appendChild(imageInput);
    imageInput.style.position = 'absolute';
    imageInput.style.visibility = 'hidden';
    imageInput.type = 'file';
    imageInput.accept = 'image/*';
    imageInput.onchange = (ev) => {
        if (!drawPlugin) {
            return console.error('--> uploadAndAddImagedrawPlugin');
        }
        const fileInput = ev.target;
        const file = ev.target.files[0];
        if (file && file.size && file.size > 10 * 1024 * 1024) {
            return showToast({
                msg: '10M',
                type: 'error'
            });
        }
        const nameParts = file.name.split('.');
        const suffix = nameParts[nameParts.length - 1];
        const isValidType = AcceptTypesArr.includes(suffix.toLowerCase());
        if (!isValidType) {
            return showToast({
                msg: `${AcceptTypesArr.join(',')}`,
                time: 3,
                type: 'error'
            });
        }
        uploadAndAddImage(drawPlugin, { fileInput }, imageDimension);
    };
};
/**
 * base64nos
 * @param app
 * @param base64Url
 */
export const uploadAndAddImage = (app, opt, dimensions) => {
    if (!opt.dataURL && !opt.fileInput) {
        return console.error('--> uploadAndAddImage dataURLfileInput');
    }
    const nim = app.nim;
    nim.previewFile(Object.assign(Object.assign({}, opt), { beginupload(data) {
            // -  fileInput,  fileInput
            // - ,  `upload`,  `upload.abort()` 
            // inputvaluenullonChange
            // fileInputpreviewFilefileInput
            if (opt.fileInput) {
                opt.fileInput.value = '';
            }
        }, done: function (err, res) {
            hideToast();
            if (err) {
                console.error('', err);
                showToast({
                    msg: '',
                    type: 'error',
                    time: 3
                });
            }
            else {
                app.addImage({
                    url: res.url,
                    width: dimensions.width,
                    height: dimensions.height
                });
            }
        } }));
    showToast({
        msg: '...',
        time: 100000,
        type: 'info'
    });
};
export const popupImageChooserAndAddImage = (app, dimensions) => {
    drawPlugin = app;
    imageDimension = dimensions;
    imageInput.click();
};
