/**
 * 
 */
import Operation from "../Operation";
import { arrayDeduce, findOpsUnderMouse, findOpsUnderRect, idsChanged } from "../util";
import drawConfig from "../DrawOp/drawConfig";
class SelectBase extends Operation {
    constructor() {
        super(...arguments);
        this.points = [];
        this.lastSelectedOpIds = [];
    }
    postMouseUp(idsBeenSelected) { }
    postClick(idsBeenSelected) { }
    postMouseMove(event, idsBeenSelected) { }
    get sp() {
        return this.points.length > 0 ? this.points[0] : undefined;
    }
    get ep() {
        return this.points.length > 0 ? this.points[this.points.length - 1] : undefined;
    }
    handleMouseDown(event, mx, my) {
        //
        this.dispatch(this.getSelectionSnapshot(false, []));
        this.points = [[mx, my]];
        const idsBeenSelected = findOpsUnderMouse(this.points, this.drawOpsOfPage, this.opsManager);
        this.dispatch(this.getSelectionSnapshot(true, idsBeenSelected));
        this.lastSelectedOpIds = idsBeenSelected;
    }
    handleMouseMove(event, mx, my) {
        if (this.isMouseDown) {
            this.points.push([mx, my]);
        }
        else {
            this.points = [[mx, my]];
        }
        let idsBeenSelected;
        if (this.isMouseDown) {
            //
            idsBeenSelected = findOpsUnderRect(this.points, this.drawOpsOfPage);
            this.dispatch(this.getSelectionSnapshot(true, idsBeenSelected));
        }
        else {
            //
            //
            const idsUnderMouse = findOpsUnderMouse(this.points, this.drawOpsOfPage, this.opsManager);
            idsBeenSelected = arrayDeduce(idsUnderMouse, this.opsManager.selectedOpIds);
            if (idsChanged(idsBeenSelected, this.lastSelectedOpIds)) {
                this.dispatch(this.getSelectionSnapshot(true, idsBeenSelected, true));
            }
        }
        this.lastSelectedOpIds = idsBeenSelected;
        this.postMouseMove(event, idsBeenSelected);
    }
    handleMouseUp(event, mx, my) {
        if (this.isMouseDown) {
            const idsBeenSelected = this.isMouseDown ?
                findOpsUnderRect(this.points, this.drawOpsOfPage) :
                findOpsUnderMouse(this.points, this.drawOpsOfPage, this.opsManager);
            this.postMouseUp(idsBeenSelected);
        }
    }
    /**
     * containerclickmousedown, mousemovemouseup
     * click
     * @param event
     * @param mx
     * @param my
     */
    handleClick(event, mx, my) {
        this.points = [[mx, my]];
        const idsBeenSelected = findOpsUnderMouse(this.points, this.drawOpsOfPage, this.opsManager);
        this.postClick(idsBeenSelected);
    }
    getSelectionSnapshot(inProgress = false, selectedOpIds, isHover = false) {
        return {
            type: 'select',
            sp: this.sp,
            ep: this.ep,
            inProgress: inProgress,
            selectedOpIds: selectedOpIds.slice(),
            reuseOpType: isHover ? 'select-hover' : 'select',
            skipUndo: true,
            color: drawConfig.strokeColor,
            nickname: this.app.nickname
        };
    }
}
export default SelectBase;
