import { max, min } from "./util";
/**
 * 
 */
export function distSqrt(p1, p2) {
    return (p1[0] - p2[0]) * (p1[0] - p2[0]) + (p1[1] - p2[1]) * (p1[1] - p2[1]);
}
export function dist(p1, p2) {
    return Math.sqrt(distSqrt(p1, p2));
}
/**
 * 
 * @param rect1
 * @param rect2
 */
export function rectCollide(rect1, rect2) {
    return !(rect1.sx > rect2.sx + rect2.w ||
        rect1.sx + rect1.w < rect2.sx ||
        rect1.sy > rect2.sy + rect2.h ||
        rect1.sy + rect1.h < rect2.sy);
}
/**
 * r
 */
export function pointCollideWithPoint(point, another, r) {
    return distSqrt(point, another) <= r * r;
}
/**
 * 
 * r
 *
 * distance(A, C) + distance(B, C) == distance(A, B)
 */
export function pointCollideWithLine(point, sp, ep, r) {
    return (dist(point, sp) + dist(point, ep) - dist(sp, ep)) <= r;
}
export function pointInRect(rect, point) {
    return (point[0] > rect.sx &&
        point[0] < rect.sx + rect.w &&
        point[1] > rect.sy &&
        point[1] < rect.sy + rect.h);
}
/**
 * scaleXscaleYdxdy
 * @param rect
 * @param dx
 * @param dy
 * @param scale
 */
export function getTransformedBoundary(rect, dx = 0, dy = 0, scaleX = {
    factor: 1,
    coordinate: 0
}, scaleY = {
    factor: 1,
    coordinate: 0
}) {
    let sx = scaleX.factor * (rect.sx - scaleX.coordinate) + scaleX.coordinate;
    let sy = scaleY.factor * (rect.sy - scaleY.coordinate) + scaleY.coordinate;
    sx = sx + (dx ? dx : 0);
    sy = sy + (dy ? dy : 0);
    let w = rect.w * scaleX.factor;
    let h = rect.h * scaleY.factor;
    if (w < 0) {
        sx = sx + w;
        w = -w;
    }
    if (h < 0) {
        sy = sy + h;
        h = -h;
    }
    return {
        sx, sy, w, h
    };
}
export function getTransformedPoint(point, dx = 0, dy = 0, scaleX = {
    factor: 1,
    coordinate: 0
}, scaleY = {
    factor: 1,
    coordinate: 0
}) {
    let x = scaleX.factor * (point[0] - scaleX.coordinate) + scaleX.coordinate;
    let y = scaleY.factor * (point[1] - scaleY.coordinate) + scaleY.coordinate;
    x = x + (dx ? dx : 0);
    y = y + (dy ? dy : 0);
    return [x, y];
}
export function combineBoundary(b1, b2) {
    let sx = min(b1.sx, b2.sx, b1.sx + b1.w, b2.sx + b2.w);
    let sy = min(b1.sy, b2.sy, b1.sy + b1.h, b2.sy + b2.h);
    let ex = max(b1.sx, b2.sx, b1.sx + b1.w, b2.sx + b2.w);
    let ey = max(b1.sy, b2.sy, b1.sy + b1.h, b2.sy + b2.h);
    return {
        sx,
        sy,
        w: ex - sx,
        h: ey - sy
    };
}
export function getBoundaryBoxFromTwoPoints(sp, ep) {
    let sx = min(sp[0], ep[0]);
    let ex = max(sp[0], ep[0]);
    let sy = min(sp[1], ep[1]);
    let ey = max(sp[1], ep[1]);
    return {
        sx,
        sy,
        w: ex - sx,
        h: ey - sy
    };
}
const ARROW_TIP_X_OFFSET = 5;
const ARROW_WINGS_X_OFFSET = 2;
export const getArrowPoints = (size, lastTwoPoints) => {
    var tipXOffset = ARROW_TIP_X_OFFSET * size, wingsXOffset = ARROW_WINGS_X_OFFSET * size, lineCoef = getLineCoef(lastTwoPoints);
    var arrowTipX = lastTwoPoints[1][0] - lastTwoPoints[0][0] > 0 ?
        getXFromCoefAndOffset(lastTwoPoints[1], lineCoef, tipXOffset) :
        2 * lastTwoPoints[1][0] - getXFromCoefAndOffset(lastTwoPoints[1], lineCoef, tipXOffset);
    var arrowTipY = lastTwoPoints[1][0] - lastTwoPoints[0][0] === 0 ?
        lastTwoPoints[1][1] + (lastTwoPoints[1][1] - lastTwoPoints[0][1] > 0 ? tipXOffset : -tipXOffset) :
        getLineFromPointsY(lastTwoPoints, arrowTipX);
    var arrowWing1X = getXFromCoefAndOffset(lastTwoPoints[1], -1 / lineCoef, wingsXOffset);
    var arrowWing1Y = lastTwoPoints[1][1] - lastTwoPoints[0][1] === 0 ?
        lastTwoPoints[1][1] - wingsXOffset :
        getVerticalLineY(lastTwoPoints[1], lineCoef, arrowWing1X);
    var arrowWing2X = 2 * lastTwoPoints[1][0] - getXFromCoefAndOffset(lastTwoPoints[1], -1 / lineCoef, wingsXOffset);
    var arrowWing2Y = lastTwoPoints[1][1] - lastTwoPoints[0][1] === 0 ?
        lastTwoPoints[1][1] + wingsXOffset :
        getVerticalLineY(lastTwoPoints[1], lineCoef, arrowWing2X);
    return [
        [arrowTipX, arrowTipY],
        [arrowWing1X, arrowWing1Y],
        [arrowWing2X, arrowWing2Y]
    ];
};
export const getLineCoef = (points) => {
    return ((points[1][1] - points[0][1]) / (points[1][0] - points[0][0])) || 0;
};
export const getXFromCoefAndOffset = (refPoint, k, z) => {
    return Math.sqrt(Math.pow(z, 2) / (Math.pow(k, 2) + 1)) + refPoint[0];
};
export const getVerticalLineY = (referencePoint, coef, x) => {
    return (referencePoint[0] - x) / coef + referencePoint[1];
};
export const getLineFromPointsY = (points, x) => {
    return (Math.max(-1000000, ((points[1][1] - points[0][1]) / (points[1][0] - points[0][0])) || 0)) * (x - points[0][0]) + points[0][1];
};
