import { WActions } from './Itypes';
import { ClearAckAction, SyncPrepareAction, SyncRequestAction, SyncPrepareAckAction, SyncAction } from './Actions';
import logger from '../debug';
import EventEmitter from 'eventemitter3';
import * as ActionUtils from './ActionUtils';
import { getKiloBytes } from './utils';
import pako from 'pako';
import globalHub from './Model/GlobalHub';
/**
 *  WebSocketCommand, Action
 */
window.pako = pako;
export default class SyncManager extends EventEmitter {
    constructor(account) {
        super();
        /**
         *  account === 0
         *
         * 
         *
         * @param anyAction
         */
        this.broadcastAction = (anyAction) => {
            //  
            let action = anyAction.action;
            if (action.TYPE === WActions.FILE) {
                let fileAction = action;
                let { file } = fileAction;
                console.log(file);
                const isSameDoc = this.fileData && file.docId && file.docId === this.fileData.docId;
                if (this.fileData && JSON.stringify(file) === JSON.stringify(this.fileData)) {
                    return; // 
                }
                if (file.type === 'clear') {
                    this.setFileData(undefined);
                }
                else {
                    this.setFileData(file);
                }
            }
            this.pushQueue(0, anyAction);
            //   this.commandManager.revoke(this.account)
        };
        this.account = account;
        this.dataQue = {}; // Map<uid, List<command>>
        // this.fileData // Map<uid, command>
        this.lastDataMap = {}; // Map<uid, command>
        this.shadowUsers = {};
        this.doNotSendBefore = Date.now();
        this.startLoop();
    }
    /**
     * this.dataQue[account]
     *
     * @param account
     * @param action
     */
    pushQueue(account, action) {
        logger.m.syncmanager('pushQueue', account, action);
        if (typeof action.action === 'undefined') {
            let error = new Error('ss');
            console.log(error.stack);
        }
        // const commandStr = JSON.stringify(action)
        // if (this.lastDataMap[account] === commandStr) {
        //   return
        // }
        // this.lastDataMap[account] = commandStr
        this.dataQue[account] = this.dataQue[account] || [];
        this.dataQue[account].push(action);
    }
    /**
     * objflush
     *
     * @param obj
     * @param delayBase
     */
    send(obj, delayBase) {
        const { toAccount, actions } = obj;
        if (!actions || actions.length < 1)
            return;
        const dataStr = ActionUtils.packBulk(actions.filter(x => !!x));
        console.log('send', dataStr);
        // 10gzip,
        if (dataStr.length > 10 && ("" + toAccount !== '0')) {
            let datas = this.gzipStr(dataStr);
            datas.forEach((data, index) => {
                this.flush({ toAccount, data }, index, delayBase);
            });
        }
        else {
            const data = { toAccount, data: dataStr };
            this.flush(data, 0, delayBase);
            logger.m.syncmanager('sendRaw:\n', data);
        }
    }
    /**
     * 'flush'
     *
     * core/index.jssyncManagerflushconnector.sendData
     */
    flush(data, delayIndex = 0, delayBase = 200) {
        setTimeout(() => {
            this.emit('flush', data);
        }, delayBase * delayIndex);
    }
    /**
     * 
     *
     * @param account
     */
    makeClearAck(account) {
        this.send({
            toAccount: account,
            actions: [{ action: new ClearAckAction() }]
        });
    }
    /**
     * 
     */
    makeSyncPrepare() {
        this.send({
            toAccount: 0,
            actions: [{ action: new SyncPrepareAction() }]
        });
    }
    /**
     * 
     *
     * @param account
     */
    makeSyncPrepareAck(account) {
        this.send({
            toAccount: 0,
            actions: [{ action: new SyncPrepareAckAction() }]
        });
    }
    gzipStr(str, doTag = true) {
        console.log('gzip stirng:', str);
        const gziped = pako.gzip(str, { to: 'string' });
        console.log('gzip stirng:', pako.gzip(str));
        const kiloBytes = getKiloBytes(gziped);
        let dataArr = [];
        if (kiloBytes > 49) {
            const len = gziped.length;
            let begin = 0;
            while (begin < len) {
                // =  49KB * 1024 Byte/KB / 2 Byte
                dataArr.push(gziped.substr(begin, 25088));
                begin += 25088;
            }
        }
        else {
            dataArr.push(gziped);
        }
        if (doTag) {
            const version = 1;
            let { length } = dataArr;
            let id = '' + Date.now() + '.' + Math.floor(Math.random() * 1000) +
                Math.floor(Math.random() * 1000) +
                Math.floor(Math.random() * 1000);
            dataArr = dataArr.map((data, index) => `GZIP:${version}|${id}|${length}|${index}|=||=|${data}`);
        }
        return dataArr;
    }
    /**
     * 
     *
     * @param account
     */
    makeSyncRequest(account) {
        this.send({
            toAccount: account,
            actions: [{ action: new SyncRequestAction() }]
        });
    }
    // accountTODO  
    /**
     * toAccount, snapshot
     *
     * @param toAccount
     * @param snapshot
     */
    syncTo(toAccount, snapshot) {
        if (this.fileData) {
            snapshot.file = this.fileData;
        }
        let action = new SyncAction(snapshot);
        this.send({ toAccount, actions: [{ action }] });
    }
    /**
     * 
     */
    clear() {
        console.warn('clear sync manager');
        this.dataQue = {};
    }
    /**
     * msTime
     *
     * @param msTime
     */
    delayMS(msTime) {
        this.doNotSendBefore = Date.now() + msTime;
    }
    /**
     * 100ms
     */
    startLoop() {
        this.timer = setInterval(() => {
            if (Date.now() < this.doNotSendBefore) {
                return;
            }
            var dq = this.dataQue;
            Object.keys(dq).forEach(key => {
                if (!dq[key] || dq[key].length < 1) {
                    return;
                }
                if (this.send) {
                    this.send({ toAccount: key, actions: dq[key] });
                    dq[key].length = 0;
                }
            });
        }, 100);
    }
    /**
     * syncManager
     */
    destroy() {
        this.timer && clearInterval(this.timer);
        this.timer = undefined;
        this.dataQue = {};
    }
    /**
     * 
     * @param data
     */
    setFileData(data) {
        this.fileData = data;
        globalHub.emit('globalFile', this.fileData);
    }
}
SyncManager.GZIP_TESTER_1 = /1\|([\d.]+)\|(\d+)\|(\d+)\|=\|\|=\|/;
