import { EventEmitter } from "eventemitter3";
import { UserManager } from "./UserManager";
import * as CanvasHelper from './CanvasHelper';
import globalHub from "../Model/GlobalHub";
class ShapesManager extends EventEmitter {
    constructor() {
        super();
        this.onUndo = (user) => {
            let shape = user.recycle[user.recycle.length - 1];
            this.remove(shape);
            this.emit('undo', user);
        };
        this.onRedo = (user) => {
            let shape = user.shapes[user.shapes.length - 1];
            this.push(shape);
            this.emit('redo', user);
        };
        this.visibles = []; // 
        this.transh = [];
    }
    push(shape) {
        this.visibles.push(shape);
        this.visibles.sort((a, b) => a.id - b.id);
        // shapes
        globalHub.emit('globalShapes', this.visibles.length);
    }
    remove(shape) {
        let index = this.visibles.indexOf(shape);
        if (~index) {
            this.visibles.splice(index, 1);
            // shapes
            globalHub.emit('globalShapes', this.visibles.length);
        }
    }
    wrap(another) {
        if (another instanceof UserManager) {
            this.clearListener(); // 
            this._usernameger = another;
            this._usernameger.on('undo', this.onUndo);
            this._usernameger.on('redo', this.onRedo);
        }
    }
    clear() {
        this.visibles.forEach(shape => {
            shape.destroy();
        });
        this.visibles.length = 0;
        globalHub.emit('globalShapes', this.visibles.length);
    }
    destroy() {
        // TODO 
        this.clearListener();
    }
    clearListener() {
        if (this._usernameger) {
            this._usernameger.removeAllListeners();
            delete this._usernameger;
        }
    }
    resizeCanvas(config) {
        const { widthScale, heightScale } = config;
        this.visibles.forEach(shape => {
            let { width, height } = shape.ctx.canvas;
            let newWidth = width * widthScale;
            let newHeight = height * heightScale;
            CanvasHelper.setCanvasPropSize(shape.ctx, {
                height: newHeight, width: newWidth
            });
            // CanvasHelper.applyCanvasLayout(shape.ctx.canvas, config)
            // corner
            shape.corner[0].x *= widthScale;
            shape.corner[0].y *= heightScale;
            shape.corner[1].x *= widthScale;
            shape.corner[1].y *= heightScale;
        });
    }
}
export default ShapesManager;
