import { getTransformedPoint } from "../../misc/shapeHelper";
import MouseMoveDrawOp from "./MouseMoveDrawOp";
import drawConfig from "./drawConfig";
import { drawCurve } from "../../misc/drawCurve";
class PenOp extends MouseMoveDrawOp {
    constructor() {
        super(...arguments);
        this.type = 'pen';
        this.description = "\nop:pen:activate" +
            "op:pen:lineWidth, op:pen:color";
    }
    eventsToListen() {
        return {
            activate: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            }
        };
    }
    getSnapshot(inProgress) {
        const points = this.points;
        const currOpId = this.opsManager.currPage.getOpId();
        const pkg = this.app.room.opIdSnapshotMap[currOpId];
        if (!pkg || !pkg.snapshot.hasOwnProperty('category')) {
            /**
             * 
             */
            return Object.assign({ points: points.slice(), total: points.length }, this.getFreeDrawCommonProperty({
                inProgress: inProgress
            }));
        }
        let boundaryRect = {
            boundary: this.getBoundaryRectOfFreeDraw()
        };
        if (this.sampleSeq > 0 && inProgress) {
            return Object.assign(Object.assign({}, boundaryRect), { points: points.slice(points.length - drawConfig.pointsPerBatch), total: points.length });
        }
        else if (this.sampleSeq > 0) {
            return Object.assign(Object.assign({}, boundaryRect), { points: points.slice(points.length - drawConfig.pointsPerBatch), total: points.length, inProgress: inProgress });
        }
        else {
            return Object.assign({ points: points.slice(), total: points.length }, this.getFreeDrawCommonProperty({
                inProgress: inProgress
            }));
        }
    }
    _draw(snapshot, ctx) {
        const data = snapshot.data;
        const points = data.points;
        const transformedPoints = points.map(p => getTransformedPoint(p, data.dx, data.dy, data.scaleX, data.scaleY));
        drawCurve(ctx, transformedPoints, 0.5);
    }
    draw(snapshot, ctx) {
        const data = snapshot.data;
        this._draw(snapshot, ctx);
        this.strokeOrFill(ctx, {
            stroke: data.color,
            lineWidth: data.lineWidth,
            opacity: data.opacity
        });
    }
    ifMouseOnOp(snapshot, mx, my) {
        const ctx = this.app.container.ctx;
        this._draw(snapshot, ctx);
        //
        ctx.lineWidth = snapshot.data.lineWidth + 20;
        ctx.strokeStyle = 'rgba(0,0,0,0)';
        ctx.stroke();
        return ctx.isPointInStroke(mx, my);
    }
    mergePartial(snapshot, partial) {
        const sp = this._mergePartial(snapshot, partial);
        if (sp.data.points.length !== sp.data.total) {
            console.warn('merge ........', snapshot, partial);
        }
        return sp;
    }
    _mergePartial(snapshot, partial) {
        try {
            const ptrDiff = partial.data.total - snapshot.data.total;
            if (ptrDiff === 0) {
                return Object.assign(Object.assign({}, snapshot), { data: Object.assign(Object.assign(Object.assign({}, snapshot.data), partial.data), { points: snapshot.data.points }) });
            }
            else if (ptrDiff < 0) {
                return snapshot;
            }
            else if (ptrDiff <= partial.data.points.length) {
                const startIndex = partial.data.points.length - ptrDiff;
                return Object.assign(Object.assign({}, snapshot), { data: Object.assign(Object.assign(Object.assign({}, snapshot.data), partial.data), { points: snapshot.data.points.concat(partial.data.points.slice(startIndex)) }) });
            }
            else {
                if (LOG) {
                    console.warn('--> mergePartial error', snapshot, partial);
                }
                return snapshot;
            }
        }
        catch (_a) {
            if (LOG) {
                console.warn('--> mergePartial error', snapshot, partial);
            }
            return snapshot;
        }
    }
}
export default PenOp;
