import { Coord } from '../Model/Point';
import Debug from 'debug';
import logger from '../../debug';
/**
 * beginPath
 * https://codetheory.in/why-clearrect-might-not-be-clearing-canvas-pixels/
 */
const debug = Debug('DrawWorker/CanvasHelper');
export function setLineStyle(ctx) {
    ctx.lineCap = 'round';
    ctx.lineJoin = 'round';
}
/* eslint-disable camelcase */
export function setpixelated(context) {
    if (context === null) {
        return;
    }
    // context.translate(0.5, 0.5)
    // context.lineWidth = .5;
    context['imageSmoothingEnabled'] = false; /* standard */
    context['mozImageSmoothingEnabled'] = false; /* Firefox */
    context['oImageSmoothingEnabled'] = false; /* Opera */
    context['webkitImageSmoothingEnabled'] = false; /* Safari */
    context['msImageSmoothingEnabled'] = false; /* IE */
    /* tslint:enable */
}
/**
 * Canvas
 * @param {CanvasRenderingContext2D} ctx Canvascontext
 * @param {ResizeCanvasOption} option 
 * @private
 */
export function setCavansStyleSize(ctx, width, height) {
    const cvs = ctx.canvas;
    cvs.style.width = width + 'px';
    cvs.style.height = height + 'px';
}
/**
 * canvas width 
 *
 * zhongkai: ctxctx(todo)
 *
 * @param {CanvasRenderingContext2D} ctx Canvascontext
 * @param {ResizeCanvasOption} option 
 * @private
 */
export function setCanvasPropSize(ctx, option) {
    const canvas = ctx.canvas;
    let skip = false;
    if (canvas.width == 0 || canvas.height == 0) {
        skip = true;
    }
    const temp = document.createElement('canvas').getContext('2d');
    if (!skip) {
        temp.canvas.width = canvas.width;
        temp.canvas.height = canvas.height;
        setpixelated(temp);
        setLineStyle(temp);
        temp.drawImage(canvas, 0, 0, canvas.width, canvas.height);
    }
    canvas.width = option.width;
    canvas.height = option.height;
    // canvas.width = option.canvasWidth
    // canvas.height = option.canvasHeight;
    setLineStyle(ctx);
    setpixelated(ctx);
    if (!skip) {
        setpixelated(temp);
        ctx.drawImage(temp.canvas, 0, 0, canvas.width, canvas.height);
    }
}
export function applyCanvasLayout(canvas, { paddingLeft, paddingTop }) {
    canvas.style.left = paddingLeft + 'px';
    canvas.style.top = paddingTop + 'px';
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @private
   */
export function clearCtx(ctx) {
    ctx.clearRect(-40, -40, ctx.canvas.width + 80, ctx.canvas.height + 80);
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {Coord} start 
   * @param {Coord} control 
   * @param {Coord} end 
   * @param {string} color 
   * @param {number} size 
   * @private
   */
export function free(ctx, start, control, end, color, size) {
    ctx.fillStyle = color;
    ctx.strokeStyle = color;
    ctx.beginPath();
    // 
    if (start.x === end.x && start.y === end.y) {
        // IEquadraticCurveTo
        ctx.lineTo(start.x, start.y);
        //  rect(ctx, start, size / 4, size / 4, false, color)
        return;
    }
    // ctx.beginPath()
    ctx.moveTo(start.x, start.y);
    ctx.quadraticCurveTo(control.x, control.y, end.x, end.y);
    ctx.stroke();
    debug('free');
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {Coord} start 
   * @param {Coord} end 
   * @private
   */
export function line(ctx, start, end, color) {
    logger.log('line', arguments);
    ctx.fillStyle = color;
    ctx.strokeStyle = color;
    // ctx.save()
    ctx.beginPath();
    ctx.moveTo(start.x, start.y);
    ctx.lineTo(end.x, end.y);
    ctx.stroke();
    // ctx.closePath();
    // ctx.restore()
    debug('line');
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {Coord} start 
   * @param {number} w px
   * @param {number} h px
   * @param {boolean} fill 
   * @param {string} color 
   * @private
   */
export function rect(ctx, start, w, h, fill = false, color) {
    if (fill) {
        ctx.fillStyle = color;
        ctx.fillRect(start.x, start.y, w, h);
    }
    else {
        ctx.strokeRect(start.x, start.y, w, h);
    }
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {CanvasRenderingContext2D} start 
   * @param {number} w px
   * @param {number} h px
   * @param {*} radius px
   * @param {boolean} fill 
   * @param {string} color 
   * @private
   */
export function roundRect(ctx, start, w, h, radius, fill = false, color) {
    const sx = start.x;
    const sy = start.y;
    const radiusX = w < 0 ? -radius : radius;
    const radiusY = h < 0 ? -radius : radius;
    ctx.beginPath();
    ctx.moveTo(sx + radiusX, sy);
    ctx.lineTo(sx + w - radiusX, sy);
    ctx.quadraticCurveTo(sx + w, sy, sx + w, sy + radiusY);
    ctx.lineTo(sx + w, sy + h - radiusY);
    ctx.quadraticCurveTo(sx + w, sy + h, sx + w - radiusX, sy + h);
    ctx.lineTo(sx + radiusX, sy + h);
    ctx.quadraticCurveTo(sx, sy + h, sx, sy + h - radiusY);
    ctx.lineTo(sx, sy + radiusY);
    ctx.quadraticCurveTo(sx, sy, sx + radiusX, sy);
    ctx.stroke();
    if (fill) {
        ctx.fillStyle = color;
        ctx.fill();
    }
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {CanvasRenderingContext2D} center 
   * @param {number} radius px
   * @param {boolean} fill 
   * @param {string} color 
   * @private
   */
export function circle(ctx, center, radius, fill = false, color) {
    // console.log('circle', center, radius, fill, color);
    if (color) {
        ctx.strokeStyle = color;
    }
    ctx.beginPath();
    // arc(x,y,r,start,stop)
    // ctx.arc(center.x, center.y, radius, 0, Math.PI * 2, true);
    ctx.arc(center.x, center.y, radius, 0, Math.PI * 2);
    if (fill) {
        ctx.fillStyle = color;
        ctx.fill();
    }
    ctx.stroke();
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {CanvasRenderingContext2D} center 
   * @param {number} xRadius xpx
   * @param {number} yRadius ypx
   * @param {boolean} fill 
   * @param {string} color 
   * @private
   */
export function ellipse(ctx, center, xRadius, yRadius, fill = false, color) {
    // console.log('ellipse', center, fill, color);
    if (color) {
        ctx.strokeStyle = color;
    }
    ctx.beginPath();
    // arc(x,y,r,start,stop)
    // ctx.arc(center.x, center.y, radius, 0, Math.PI * 2, true);
    ctx.ellipse(center.x, center.y, xRadius, yRadius, 0, 0, Math.PI * 2);
    if (fill) {
        ctx.fillStyle = color;
        ctx.fill();
    }
    ctx.stroke();
}
function safeColorClamp(x) {
    if (x < 0) {
        return 0;
    }
    else if (x > 255) {
        return 255;
    }
    else {
        return x;
    }
}
/**
   * 
   *  flood fillhttps://en.m.wikipedia.org/wiki/Flood_filljsscanline fill
   * TODO IETyped Arraypolyfill
   * @param {CanvasRenderingContext2D} ctx
   * @param {Coord} s 
   * @param {String} color 
   * @param {Shape} shape 
   * @private
   */
export function fill(ctx, s, color, shape) {
    const sx = Math.round(s.x);
    const sy = Math.round(s.y);
    ctx.save();
    ctx.beginPath();
    const img = ctx.getImageData(0, 0, Math.round(ctx.canvas.width), Math.round(ctx.canvas.height));
    // logger.image(ctx.canvas.toDataURL(), 'fill, playground')
    const { height: fullDisplayHeight, width: fullDisplayWidth } = img;
    if (shape) {
        shape.ctx.canvas.width = fullDisplayWidth;
        shape.ctx.canvas.height = fullDisplayHeight;
        setpixelated(shape.ctx);
        setLineStyle(shape.ctx);
    }
    const X = 0;
    const Y = 1;
    const stroke = color || ctx.strokeStyle;
    const r = parseInt(stroke.substr(1, 2), 16);
    const g = parseInt(stroke.substr(3, 2), 16);
    const b = parseInt(stroke.substr(5, 2), 16);
    // >>> 0 32
    const fillColor = ((255 << 24) | (b << 16) | (g << 8) | r) >>> 0;
    const start = [sx, sy];
    // Shape
    let shapeImageData = shape.ctx.createImageData(fullDisplayWidth, fullDisplayHeight);
    const shapeData = new Uint32Array(shapeImageData.data.buffer);
    // buf8.set(img.data);
    const data = new Uint32Array(img.data.buffer);
    const startColor = data[sy * fullDisplayWidth + sx];
    if (((startColor ^ fillColor) & 0xffffff) === 0) { // 
        if (startColor >>> 24 === fillColor >>> 24) {
            return;
        }
    }
    const rgb = startColor & 0x00ffffff;
    const r_start = (rgb & 0xff) >>> 0;
    const g_start = (rgb & 0xff00) >>> 8;
    const b_start = (rgb & 0xff0000) >>> 16;
    const alpha_start = (startColor & 0xff000000) >>> 24;
    // 
    const R_MIN = safeColorClamp(r_start - 15);
    const G_MIN = safeColorClamp(g_start - 15);
    const B_MIN = safeColorClamp(b_start - 15);
    const R_MAX = safeColorClamp(r_start + 15);
    const G_MAX = safeColorClamp(g_start + 15);
    const B_MAX = safeColorClamp(b_start + 15);
    // 
    const queue = [start];
    let curPixel;
    const maxX = fullDisplayWidth - 1;
    const maxY = fullDisplayHeight - 1;
    // 
    function canFill(color) {
        // return color === startColor
        if (color === startColor) {
            return true;
        }
        const alpha = (color & 0xff000000) >>> 24;
        if (alpha_start < 10) { // 
            if (alpha === 0) {
                return true;
            }
        }
        const rgb = (color & 0x00ffffff) >>> 0;
        const r = (rgb & 0xff) >>> 0;
        const g = (rgb & 0xff00) >>> 8;
        const b = (rgb & 0xff0000) >>> 16;
        // 
        if (r >= R_MIN && r <= R_MAX && g >= G_MIN && g <= G_MAX && b >= B_MIN && b <= B_MAX) {
            //  ,20
            if (Math.abs(alpha_start - alpha) < 10) {
                return true;
            }
        }
        return false;
        // return color === startColor || (color & 0xff000000) >>> 0 < 0xff000000;
    }
    const seen = {};
    let topLeft = new Coord(Math.round(fullDisplayWidth), Math.round(fullDisplayHeight));
    let bottomRight = new Coord(0, 0);
    const getImageDataIndex = (x, y) => y * fullDisplayWidth + x;
    const getColor = (x, y) => data[getImageDataIndex(x, y)];
    while ((curPixel = queue.pop())) {
        const x = curPixel[X];
        let y = curPixel[Y];
        // 
        while (--y >= 0 && canFill(getColor(x, y))) { }
        y++;
        let reachLeft = false;
        let reachRight = false;
        while (y++ < maxY && canFill(getColor(x, y))) {
            // 
            topLeft.topLeft(x, y);
            bottomRight.bottomRight(x + 1, y + 1);
            let imgDataIndex = getImageDataIndex(x, y); // curPixel[Y] * fullDisplayWidth + curPixel[X]
            data[imgDataIndex] = fillColor;
            shapeData[imgDataIndex] = fillColor;
            seen[imgDataIndex] = true;
            if (x > 0 && !seen[imgDataIndex - 1]) {
                if (canFill(data[imgDataIndex - 1])) {
                    if (!reachLeft) {
                        queue.push([x - 1, y]);
                        reachLeft = true;
                    }
                }
                else if (reachLeft) {
                    reachLeft = false;
                }
            }
            if (x < maxX && !seen[imgDataIndex + 1]) {
                if (canFill(data[imgDataIndex + 1])) {
                    if (!reachRight) {
                        if (!seen[imgDataIndex + 1]) {
                            queue.push([x + 1, y]);
                        }
                        reachRight = true;
                    }
                }
                else if (reachRight) {
                    reachRight = false;
                }
            }
        }
    }
    // img.data.set(buf8);
    const finalArea = Coord.areaSize(topLeft, bottomRight);
    logger.log('fill area is ', finalArea);
    logger.log('fill corner is ', topLeft, bottomRight);
    shape.corner = [topLeft, bottomRight];
    shape.ctx.putImageData(shapeImageData, 0, 0);
    let smallImageData = shape.ctx.getImageData(topLeft.x, topLeft.y, finalArea.width, finalArea.height);
    logger.log(smallImageData);
    shape.ctx.canvas.height = smallImageData.height;
    shape.ctx.canvas.width = smallImageData.width;
    setpixelated(shape.ctx);
    setLineStyle(shape.ctx);
    shape.ctx.putImageData(smallImageData, 0, 0);
    console.log('shape:', shape.ctx.canvas.toDataURL());
    clearCtx(ctx);
    logger.image(ctx.canvas.toDataURL(), 'ctx.canvas');
    logger.image(shape.ctx.canvas.toDataURL(), 'shape.ctx');
    ctx.putImageData(img, 0, 0);
    console.error(shape.ctx.canvas.toDataURL());
    logger.image(ctx.canvas.toDataURL(), 'final');
    shape.ctx.beginPath();
    ctx.restore();
}
/**
   * 
   * @param {CanvasRenderingContext2D} ctx Canvascontext
   * @param {CanvasRenderingContext2D} start 
   * @param {string} text 
   * @param {number} size px
   * @param {boolean} fill 
   * @private
   */
export function text(ctx, start, text, size, fill = true, color) {
    const x = start.x;
    const y = start.y;
    ctx.beginPath();
    ctx.moveTo(x, y);
    ctx.font = size + 'px "",Arial';
    ctx.textBaseline = 'top';
    // ctx.textBaseline = 'bottom';
    ctx.textAlign = 'left';
    if (fill) {
        ctx.fillStyle = color;
        ctx.fillText(text, x, y);
    }
    else {
        ctx.strokeText(text, x, y);
    }
    ctx.beginPath();
}
