        ctx.save();
        const data = snapshot.data;
        this._draw(snapshot, ctx);
        ctx.strokeStyle = data.color;
        ctx.lineWidth = data.lineWidth;
        ctx.globalAlpha = data.opacity;
        ctx.stroke();
        const arrowPoints = data.arrowPoints;
