/**
 * 
 */
import Operation from "../Operation";
class ClearOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'clear';
        this.description = "";
    }
    isAvailable() {
        return Object.keys(this.drawOpsOfPage).length > 0;
    }
    registerHooks() {
        this.opsManager.hooks.refresh.tap("RefreshPlugin", this.onRefresh.bind(this));
    }
    eventsToListen() {
        return {
            exec: {
                value: true,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            },
        };
    }
    execCommand() {
        if (Object.keys(this.drawOpsOfPage).length > 0) {
            this.dispatch(this.getSnapshot());
        }
    }
    getSnapshot() {
        return {
            type: this.type
        };
    }
    onRefresh() {
        const available = Object.keys(this.drawOpsOfPage).length > 0;
        this.emit('event:appState:change', 'clearAvailable', available);
    }
}
export default ClearOp;
