import { showToast } from "../../component/toast";
import { transitionToCameraBound } from "../animation";
class Vision {
    constructor(app) {
        /**
         * 
         */
        this.mode = 'freeObserver';
        this.app = app;
        this.camera = this.app.camera;
    }
    get broadcasterName() {
        return this.broadcasterNickname ? this.broadcasterNickname : this.broadcaster;
    }
    setMode(mode) {
        this.mode = mode;
        this.app.emit('event:appState:change', 'vision', {
            mode: mode,
            isBroadcasting: this.broadcaster !== undefined
        });
    }
    setBroadcaster(value, nickname) {
        this.broadcaster = value;
        this.broadcasterNickname = nickname;
        // this.app.emit('event:vision:isBroadcasting', this.broadcaster !== undefined)
        this.app.emit('event:appState:change', 'vision', {
            mode: this.mode,
            isBroadcasting: this.broadcaster !== undefined
        });
    }
    /**
     * , 
     */
    setSelfAsBroadcaster() {
        if (this.app.dataHub.isSynchronizing)
            return;
        const account = this.app.account;
        const nickname = this.app.nickname;
        this.setMode('broadcaster');
        this.setBroadcaster(account, nickname);
        showToast({
            type: 'success',
            msg: '',
            time: 3
        });
        this.app.dataHub.broadcastRoomState({
            broadcaster: account,
            nickname: nickname
        }, 'broadcaster');
        /**
         * camera-bound
         */
        this.app.emit('op:camera-bound:send', this.camera.getContainerWorldRect(), true);
    }
    /**
     * 
     */
    unsetSelfAsBroadcaster() {
        if (this.app.dataHub.isSynchronizing)
            return;
        if (this.mode === 'broadcaster') {
            if (this.broadcaster !== this.app.account) {
                //
                console.error('--> ', this.broadcaster, this.app.account);
            }
            this.setMode('freeObserver');
            this.setBroadcaster(undefined);
            showToast({
                type: 'success',
                msg: ''
            });
            this.app.dataHub.broadcastRoomState({
                broadcaster: undefined
            }, 'broadcaster');
        }
    }
    /**
     * broadcaster
     * 
     */
    setSelfAsFollower() {
        if (this.app.dataHub.isSynchronizing)
            return;
        if (this.mode === 'broadcaster') {
            console.error('');
        }
        else if (this.mode === 'freeObserver') {
            this.setMode('follower');
            const boundary = this.camera.cameraBoundByUser[this.broadcaster];
            transitionToCameraBound(this.camera, boundary);
            showToast({
                type: 'success',
                msg: `${this.broadcasterName}`
            });
            this.app.dataHub.broadcastRoomState({
                account: this.app.account,
                mode: 'follower'
            }, 'observeMode');
        }
    }
    /**
     * setSelfAsFollower
     */
    setSelfAsFreeObserver() {
        if (this.app.dataHub.isSynchronizing)
            return;
        if (this.mode === 'broadcaster') {
            console.error('');
        }
        else if (this.mode === 'follower') {
            this.setMode('freeObserver');
            showToast({
                type: 'success',
                msg: `${this.broadcasterName}`
            });
            this.app.dataHub.broadcastRoomState({
                account: this.app.account,
                mode: 'freeObserver'
            }, 'observeMode');
            /**
             * camera-bound
             * freeObserver, camera-boundcamera
             * broadcastercamera-boundbroadcastercamera
             */
            this.app.emit('op:camera-bound:send', this.camera.getContainerWorldRect(), false);
        }
    }
    receiveBroadcaster(value) {
        const { broadcaster, nickname } = value;
        if (broadcaster) {
            this.setMode('follower');
            this.setBroadcaster(broadcaster, nickname);
            //account
            showToast({
                msg: `${this.broadcasterName}`,
                type: 'success',
                time: 3
            });
        }
        else {
            showToast({
                msg: `${this.broadcasterName}`,
                type: 'success',
                time: 3
            });
            this.setMode('freeObserver');
            this.setBroadcaster(undefined);
        }
    }
    /**
     * 
     * @param visionData
     */
    receiveBroadcasterAfterSync(lastVisionData) {
        if (lastVisionData.broadcaster === this.app.account) {
            showToast({
                type: 'success',
                msg: '',
                time: 3
            });
            this.setMode('broadcaster');
            this.setBroadcaster(this.app.account, this.app.nickname);
        }
        else if (lastVisionData.broadcaster) {
            this.setMode('follower');
            this.setBroadcaster(lastVisionData.broadcaster, lastVisionData.nickname);
            showToast({
                msg: `${this.broadcasterName}`,
                type: 'success',
                time: 3
            });
        }
        else {
            this.setMode('freeObserver');
            this.setBroadcaster(undefined);
        }
    }
    /**
     * 
     * @param lastFree
     */
    receiveObserveModeAfterSync(lastObserveModeOfAccount) {
        if (lastObserveModeOfAccount.account !== this.app.account)
            return;
        if (lastObserveModeOfAccount.mode === 'follower') {
            this.setMode('follower');
            const boundary = this.camera.cameraBoundByUser[this.broadcaster];
            transitionToCameraBound(this.camera, boundary);
            showToast({
                type: 'success',
                msg: `${this.broadcasterName}`,
            });
        }
        else {
            this.setMode('freeObserver');
            const boundary = this.camera.cameraBoundByUser[this.app.account];
            transitionToCameraBound(this.camera, boundary);
            showToast({
                type: 'success',
                msg: `${this.broadcasterName}`
            });
        }
    }
}
export default Vision;
