import { fetchResource, getResource, resourceLoaded } from "../../resource/resourceCache";
export const addImage = (op, url, opts) => {
    const currBoard = op.app.room.currBoard;
    let currPage = op.opsManager.currPage;
    if (opts && opts.hasOwnProperty('pageIndex')) {
        const pageName = currBoard.pageNames[opts.pageIndex];
        if (currBoard.pages.hasOwnProperty(pageName)) {
            currPage = currBoard.pages[pageName];
        }
    }
    if (resourceLoaded(url)) {
        const img = getResource(url);
        const width = (opts === null || opts === void 0 ? void 0 : opts.width) ? opts.width : img.width;
        const height = (opts === null || opts === void 0 ? void 0 : opts.height) ? opts.height : img.height;
        op.dispatchImage(url, width, height, currPage);
    }
    else if ((opts === null || opts === void 0 ? void 0 : opts.width) && (opts === null || opts === void 0 ? void 0 : opts.height)) {
        op.dispatchImage(url, opts === null || opts === void 0 ? void 0 : opts.width, opts === null || opts === void 0 ? void 0 : opts.height, currPage);
    }
    else {
        //TODO: dispatchboardIndexpageIndexaddImage
        currPage.addWaitingResource(url);
        fetchResource(url, (img) => {
            const width = (opts === null || opts === void 0 ? void 0 : opts.width) ? opts.width : img.width;
            const height = (opts === null || opts === void 0 ? void 0 : opts.height) ? opts.height : img.height;
            op.dispatchImage(url, width, height, currPage);
            currPage.removeWaitingResource(url);
        });
    }
};
