export const draw = (ctx, url, boundary, opts) => {
    /**
     * pagethis.opsManager.currPage
     * currPage
     */
    let { page, div, preview, id, supportToDataUrl } = opts;
    if (resourceLoaded(url) && !preview && div && id) {
        let image = div.querySelector(`[id='image-${id}']`);
        if (!image) {
            if (opts.type === 'background') {
                /**
                 * URLpagecloneNode
                 */
                image = getResource(url);
            }
            else {
                image = getResource(url).cloneNode(true);
            }
            image.id = `image-${id}`;
            image.style.userSelect = 'none';
            image.style.pointerEvents = 'none';
            image.style.position = 'absolute';
            image.style.imageRendering = '-webkit-optimize-contrast';
            div.appendChild(image);
        }
        image.width = boundary.w;
        image.height = boundary.h;
        image.style.left = `${boundary.sx}px`;
        image.style.top = `${boundary.sy}px`;
        const pxOverBorder = getImagePartOutOfContainer(boundary, opts.page.app.camera);
        if (pxOverBorder) {
            image.style.clipPath = `inset(${pxOverBorder.top}px ${pxOverBorder.right}px ${pxOverBorder.bottom}px ${pxOverBorder.left}px)`;
        }
        else {
            image.style.clipPath = 'none';
        }
    }
    else if (supportToDataUrl && resourceLoaded(url)) {
        ctx.drawImage(getResource(url, true), boundary.sx, boundary.sy, boundary.w, boundary.h);
    }
    else if (!supportToDataUrl && resourceLoaded(url)) {
        ctx.drawImage(getResource(url, false), boundary.sx, boundary.sy, boundary.w, boundary.h);
    }
    else if (resourceLoaded('imageLoading')) {
        /**
         * 
         */
        ctx.drawImage(getResource('imageLoading'), boundary.sx + 5, boundary.sy + 5, Math.max(0, boundary.w - 10), Math.max(0, boundary.h - 10));
    }
    if (!resourceLoaded(url)) {
        //currPagethis.opsManager.currPagepage
        page.addWaitingResource(url);
        fetchResource(url, () => {
            page.removeWaitingResource(url);
        });
    }
};
/**
 * boundarycontainercontainerclippath
 * @param boundary
 */
const getImagePartOutOfContainer = (boundary, camera) => {
    const { sx, sy, w, h } = boundary;
    const ex = sx + w;
    const ey = sy + h;
    const screenBound = camera.getContainerWorldRect();
    const left = Math.max(0, screenBound.centerX - screenBound.width / 2 - sx); //screen - boundary
    const right = Math.max(0, ex - (screenBound.centerX + screenBound.width / 2)); //boundary - screen
    const top = Math.max(0, screenBound.centerY - screenBound.height / 2 - sy); //screen - boundary
    const bottom = Math.max(0, ey - (screenBound.centerY + screenBound.height / 2)); //boundary - screen
    if (left > 0 || right > 0 || top > 0 || bottom > 0) {
        return {
            left, right, top, bottom
        };
    }
    else {
        return null;
    }
};
