export const getImageProperPosition = (op, width, height) => {
    const sanityWidth = op.app.container.width;
    const sanityHeight = op.app.container.height;
    const MAX_ALLOWED_WIDTH = sanityWidth;
    const MAX_ALLOWED_HEIGHT = sanityHeight;
    if (width > MAX_ALLOWED_WIDTH && height < MAX_ALLOWED_HEIGHT) {
        height = height * MAX_ALLOWED_WIDTH / width;
        width = MAX_ALLOWED_WIDTH;
    }
    else if (width < MAX_ALLOWED_WIDTH && height > MAX_ALLOWED_HEIGHT) {
        width = width * MAX_ALLOWED_HEIGHT / height;
        height = MAX_ALLOWED_HEIGHT;
    }
    else if (width > MAX_ALLOWED_WIDTH && height > MAX_ALLOWED_HEIGHT) {
        if (width / height > MAX_ALLOWED_WIDTH / MAX_ALLOWED_HEIGHT) {
            height = height * MAX_ALLOWED_WIDTH / width;
            width = MAX_ALLOWED_WIDTH;
        }
        else {
            width = width * MAX_ALLOWED_HEIGHT / height;
            height = MAX_ALLOWED_HEIGHT;
        }
    }
    width = width / op.app.camera.scale;
    height = height / op.app.camera.scale;
    let sx = op.app.camera.centerX - width / 2;
    let sy = op.app.camera.centerY - height / 2;
    return {
        sx, sy, width, height
    };
};
/**
 * 100%
 * @param op
 * @param width
 * @param height
 */
export const getBackgroundImagePorperPosition = (op, width, height) => {
    let sx = -width / 2;
    let sy = -height / 2;
    return {
        sx, sy, width, height
    };
};
