/**
 * PagePage{board = room.boards[room.currIndex]; page = board.pages[board.currIndex]}
 *
 * PageOpJsonOpPage
 */
import { resourceLoaded } from "../resource/resourceCache";
import { v4 as uuidv4 } from 'uuid';
import { refresh, cleanNonCanvasElements, drawInProgressSnapshots } from "./render";
import { calcDrawOpQueue } from "./completeFlow";
import { opNeedRefresh } from "./util";
import { addIncrementSnapshot } from "./incrementFlow";
class Page {
    constructor(app, boardName, pageIndex) {
        //drawOp
        this.drawOpQueue = {};
        this.hiddenOpQueue = {};
        /*
         * selectOpelement-eraser Op
         */
        this.lastSelectOfUser = {};
        // lastSelectSnapshot: IDrawOpSnapshot | undefined
        /**
         * select
         */
        this.lastInProgressSelectOfUser = {};
        this.lastInProgressSnapshotOfUser = {};
        /**
         * lastInProgressSnapshotOfUser
         */
        this.lastMousePointerOfUser = {};
        /**
         * preview
         * 
         *
         * event:page:readyboardcanvas
         */
        this.blockedResourceList = [];
        this._currOpId = uuidv4();
        this._nextOpId = uuidv4();
        this.app = app;
        this.opsManager = app.opsManager;
        this.boardName = boardName;
        this.pageIndex = pageIndex;
        window.page = this;
    }
    prepareNewOpId() {
        this._currOpId = this._nextOpId;
        this._nextOpId = uuidv4();
    }
    getOpId() {
        return this.app.account + '-' + this._currOpId;
    }
    /**
     * nextOpIncfalseopId
     * trueopId
     */
    getNextOpId(nextOpInc = false) {
        if (nextOpInc) {
            this.prepareNewOpId();
            return this.getOpId();
        }
        else {
            return this.app.account + '-' + this._nextOpId;
        }
    }
    get opQueue() {
        const room = this.app.room;
        try {
            return room.boardOps[this.boardName].pages[this.pageIndex].inPageSnapshotIds.map(id => room.opIdSnapshotMap[id].snapshot);
        }
        catch (_a) {
            return [];
        }
    }
    /**
     * 
     */
    get isCurrPage() {
        return this.app.room.currBoard.currPage === this;
    }
    get ctx() {
        return this.app.container.ctx;
    }
    get frontCtx() {
        return this.app.container.frontCtx;
    }
    get laserCtx() {
        return this.app.container.laserCtx;
    }
    get width() {
        return this.app.container.width;
    }
    get height() {
        return this.app.container.height;
    }
    get blocked() {
        return this.blockedResourceList.length > 0;
    }
    setPayload(payload) {
        this.payload = payload;
    }
    addWaitingResource(resUrl) {
        if (!this.blockedResourceList.includes(resUrl) && !resourceLoaded(resUrl)) {
            this.blockedResourceList.push(resUrl);
        }
    }
    removeWaitingResource(resUrl) {
        const index = this.blockedResourceList.indexOf(resUrl);
        if (index > -1) {
            this.blockedResourceList.splice(index, 1);
            if (!this.blocked) {
                //
                this.app.opsManager.hooks.pageReady.call(this.boardName, this.pageIndex);
                if (this.isCurrPage) {
                    this.app.room.pageToRefresh = this;
                    // return this.refresh()
                }
            }
        }
    }
    /**
     * snapshot
     *
     * @param snapshot
     */
    addOpInPage(snapshot, doRefresh = true) {
        if (snapshot.opId === 'none') {
            console.error('DEV-->', 'snapshot no opid', snapshot);
            return false;
        }
        /**
         * (uid)/
         *
         * mouse-pointer
         */
        if (this.lastInProgressSnapshotOfUser.hasOwnProperty(snapshot.uid)
            && !['mouse-pointer'].includes(snapshot.data.type)) {
            if (!snapshot.data.inProgress
                || snapshot.opId !== this.lastInProgressSnapshotOfUser[snapshot.uid].snapshot.opId) {
                delete this.lastInProgressSnapshotOfUser[snapshot.uid];
            }
        }
        // if (lastOp === null && snapshot.data.type !== 'select') {
        //     console.warn('DEV-->', 'New Op created', snapshot)
        // }
        // if (!snapshot.data.inProgress && snapshot.data.type !== 'select') {
        //     console.warn('DEV-->', 'New op finished', snapshot)
        // }
        /**
         * calcDrawOpQueue,
         * recoverybackgroundOp
         */
        if (opNeedRefresh(this, snapshot)) {
            this.calcDrawOpQueue();
        }
        if (!this.app.room.isReceivingBatchSnapshots) {
            if (opNeedRefresh(this, snapshot)) {
                if (['undo', 'redo', 'clear'].includes(snapshot.data.type)) {
                    this.cleanNonCanvasElements(false);
                }
                if (doRefresh) {
                    /**
                     * PagePage
                     */
                    if (this.boardName === this.app.room.currIndex &&
                        this.pageIndex === this.app.room.currBoard.currIndex.toString()) {
                        this.app.room.pageToRefresh = this;
                        // this.refresh()
                    }
                }
            }
            else {
                addIncrementSnapshot(this, snapshot);
            }
        }
        /**
         * 
         * undo, redo
         */
        this.opsManager.hooks.addOpHook.call(snapshot);
        return opNeedRefresh(this, snapshot);
    }
    /**
     * 
     * DrawOpSnapshot
     *
     * 
     * {
     *  [1111]
     *  [21]
     *  []
     * }
     * 
     */
    calcDrawOpQueue() {
        calcDrawOpQueue(this, this.app.room.isReceivingBatchSnapshots);
    }
    /**
     * containerdom
     *
     * 
     */
    cleanNonCanvasElements(includeBackground = true) {
        cleanNonCanvasElements(this, includeBackground);
    }
    refresh(opt = {}) {
        refresh(this, opt);
    }
    incRefresh() {
        drawInProgressSnapshots(this);
    }
}
export default Page;
