import { globalSnapshotTypes } from './types';
import { v4 as uuidv4 } from 'uuid';
let lastGlobalOpId;
let lastSnapshotInfo = {
    type: '',
    boardName: 'whiteboard',
    pageIndex: '0',
    dependOn: undefined,
    timestamp: 0
};
let newOpFlag = false;
/**
 * opId
 * 1. dispatch
 * 2. dependOndispatchdependOn
 * 3. beginBatchopId
 *
 * 4. undoredoimage
 */
function genNewOpId(type, dependOn, pageIndex, boardName) {
    let gen = false;
    if (lastSnapshotInfo.pageIndex !== pageIndex) {
        gen = true;
    }
    else if (lastSnapshotInfo.boardName !== boardName) {
        gen = true;
    }
    else if (!lastSnapshotInfo.inProgress) {
        //
        gen = true;
    }
    else if (type !== lastSnapshotInfo.type) {
        gen = true;
    }
    else if (typeof dependOn !== typeof lastSnapshotInfo.dependOn) {
        gen = true;
    }
    else if (typeof dependOn === 'string' && dependOn !== lastSnapshotInfo.dependOn) {
        gen = true;
    }
    else if (Array.isArray(dependOn) && Array.isArray(lastSnapshotInfo.dependOn) && dependOn.join(',') !== lastSnapshotInfo.dependOn.join(',')) {
        gen = true;
    }
    else if (newOpFlag) {
        gen = true;
    }
    else if (['undo', 'redo', 'image'].includes(type)) {
        gen = true;
    }
    return gen;
}
class Operation {
    constructor(app) {
        this.type = 'base';
        this.app = app;
        this.opsManager = app.opsManager;
        this.registerHooks();
    }
    get isMouseDown() {
        return this.app.container.isMouseDown;
    }
    /**
     * 
     */
    registerHooks() { }
    /**
     * 
     * key: 
     * value
     *
     * 
     * exec: truethis.execCommand()
     * activate: trueOpsManager
     * value
     *
     * UI'op:${type}:{eventName}'managerexecCommandOpsManagerOp
     */
    eventsToListen() {
        return {
            exec: {
                value: false,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            },
            activate: {
                value: false,
                information: {
                    args: '',
                    message: `${this.type}`
                }
            },
        };
    }
    execCommand() {
        console.warn('DEV-->', `exectrueexecCommand`);
    }
    afterActivate() {
        //operation
    }
    /**
     * 
     */
    getSnapshot(...args) {
        return {
            type: this.type
        };
    }
    /**
     * opid?
     * @param type
     * @param dependOn
     */
    opIdForseen(type, dependOn) {
        const newId = genNewOpId(type, dependOn, this.opsManager.currPage.pageIndex, this.opsManager.currPage.boardName);
        if (newId) {
            return this.opsManager.currPage.getNextOpId();
        }
        else {
            return this.opsManager.currPage.getOpId();
        }
    }
    /**
     * 
     *
     * @param snapshot
     */
    dispatch(data) {
        if (!data.hasOwnProperty('type')) {
            /**
             * 
             */
            const snapshot = {
                opId: this.opsManager.currPage.getOpId(),
                data
            };
            this.app.room.dispatch(snapshot);
        }
        else if (globalSnapshotTypes.indexOf(data.type) > -1) {
            this.dispatchGlobalAction(data);
        }
        else if (data.type === 'page') {
            this.dispatchPageAction(data);
        }
        else if (data.type === 'board') {
            this.dispatchBoardAction(data);
        }
        else {
            /**
             * 
             */
            const currBoard = this.app.room.boards[this.app.room.currIndex];
            const now = (new Date()).valueOf();
            let opId;
            if (data.reuseOpType) {
                /**
                 * opId
                 */
                opId = `${this.app.account}-${data.reuseOpType}`;
                delete data['reuseOpType'];
            }
            else {
                const newId = genNewOpId(data.type, data.dependOn, this.opsManager.currPage.pageIndex, this.opsManager.currPage.boardName);
                newOpFlag = false;
                lastSnapshotInfo = {
                    type: data.type,
                    dependOn: data.dependOn,
                    timestamp: now,
                    pageIndex: this.opsManager.currPage.pageIndex,
                    boardName: this.opsManager.currPage.boardName,
                    inProgress: data.inProgress
                };
                if (newId) {
                    this.opsManager.currPage.prepareNewOpId();
                }
                opId = this.opsManager.currPage.getOpId();
            }
            const snapshot = {
                category: 'inPageOp',
                board: this.app.room.currIndex,
                page: currBoard.currIndex,
                time: now,
                uid: this.app.account,
                opId: opId,
                data
            };
            this.app.room.dispatch(snapshot);
        }
    }
    /**
     * currBoardcurrPage
     * page
     * pagePageActionBoardAction
     * @param data
     * @param page
     */
    dispatchFromPage(data, page) {
        /**
         * 
         */
        const now = (new Date()).valueOf();
        //opId
        page.prepareNewOpId();
        const snapshot = {
            category: 'inPageOp',
            board: page.boardName,
            page: Number(page.pageIndex),
            time: now,
            uid: this.app.account,
            opId: page.getOpId(),
            data
        };
        lastSnapshotInfo = {
            type: data.type,
            dependOn: data.dependOn,
            timestamp: now,
            pageIndex: page.pageIndex,
            boardName: page.boardName,
            inProgress: data.inProgress
        };
        this.app.room.dispatch(snapshot);
    }
    /**
     * gotodeletepage
     * @param data
     */
    dispatchPageAction(data) {
        const now = (new Date()).valueOf();
        const snapshot = {
            category: 'pageOp',
            board: this.app.room.currIndex,
            time: now,
            uid: this.app.account,
            opId: uuidv4(),
            data
        };
        this.app.room.dispatch(snapshot);
    }
    /**
     * gotodeletepage
     * @param data
     */
    dispatchBoardAction(data) {
        const now = (new Date()).valueOf();
        const snapshot = {
            category: 'boardOp',
            time: now,
            uid: this.app.account,
            opId: uuidv4(),
            data
        };
        this.app.room.dispatch(snapshot);
    }
    dispatchGlobalAction(data) {
        let opId;
        const now = (new Date()).valueOf();
        if (data.reuseOpType) {
            opId = `${this.app.account}-${data.reuseOpType}`;
            delete data['reuseOpType'];
        }
        else {
            if (!lastGlobalOpId || newOpFlag) {
                lastGlobalOpId = uuidv4();
                newOpFlag = false;
            }
            opId = lastGlobalOpId;
        }
        const snapshot = {
            category: 'globalOp',
            time: now,
            uid: this.app.account,
            opId,
            data
        };
        this.app.room.dispatch(snapshot);
    }
    emit(event, ...args) {
        this.app.dispatcher.emit(event, ...args);
    }
    draw(snapshot, ctx, opts) {
        console.warn('DEV-->', `${this.type}draw`);
    }
    //dompreviewpreview
    //TextOpOp
    preview(snapshot, ctx, opts) {
        this.draw(snapshot, ctx, opts);
    }
    hide(snapshot, ctx, div) {
        //
    }
    mergePartial(snapshot, partial) {
        if (partial.hasOwnProperty('category')) {
            return partial;
        }
        else {
            return snapshot;
        }
    }
    /**
     * opId
     */
    beginBatch() {
        newOpFlag = true;
    }
    /**
     * container
     */
    isKeyDown(key) {
        return this.app.container.isKeyDown(key);
    }
    /**
     * 
     */
    get opsOfPage() {
        return this.opsManager.currPage.opQueue;
    }
    /**
     * 
     * TODO: 
     */
    get drawOpsOfPage() {
        return this.opsManager.drawOpsOfPage;
    }
    getOpTypeByOpId(opId) {
        if (this.opsManager.drawOpsOfPage.hasOwnProperty(opId)) {
            return this.opsManager.drawOpsOfPage[opId].data.type;
        }
        else {
            return undefined;
        }
    }
}
export default Operation;
