import Operation from "../Operation";
class EraserOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'eraser';
        this.eraserColor = 'rgba(230, 100, 100, .4)';
        this.width = 20;
        this.points = [];
        this.description = "\nop:eraser:activate" +
            "";
    }
    eventsToListen() {
        return {
            activate: {
                value: true,
                information: {
                    event: `op:${this.type}:activate`,
                    args: '',
                    message: `${this.type}`
                }
            },
            'eraser:width': {
                value: this.setWidth.bind(this),
                information: {
                    args: 'width: number',
                    message: ``
                }
            },
            'eraser:color': {
                value: this.setColor.bind(this),
                information: {
                    args: 'color: Color',
                    message: ``
                }
            }
        };
    }
    handleMouseMove(event, mx, my) {
        if (this.isMouseDown) {
            this.points.push([mx, my]);
            this.dispatch(this.getSnapshot());
        }
    }
    handleMouseDown(event, mx, my) {
        this.beginBatch();
        this.points = [];
        this.points.push([mx, my]);
    }
    handleMouseUp(event, mx, my) {
        if (this.isMouseDown) {
            this.dispatch(this.getSnapshot());
            this.points = [];
        }
    }
    getSnapshot() {
        return {
            type: this.type,
            points: this.points.slice(),
            width: this.width,
        };
    }
    draw(snapshot, ctx) {
        ctx.save();
        //1
        ctx.fillStyle = 'rgb(0, 0, 0)';
        const data = snapshot.data;
        const points = data.points;
        ctx.beginPath();
        ctx.globalCompositeOperation = 'destination-out';
        for (let i = 1; i < points.length; i++) {
            ctx.moveTo(points[i][0], points[i][1]);
            ctx.arc(points[i][0], points[i][1], this.width, 0, Math.PI * 2, true);
        }
        ctx.fill();
        ctx.restore();
        this.drawEraserSignpost(ctx);
    }
    /**
     * 
     */
    drawEraserSignpost(ctx) {
        if (this.opsManager.currOp instanceof EraserOp && this.isMouseDown) {
            ctx.beginPath();
            ctx.arc(this.points[this.points.length - 1][0], this.points[this.points.length - 1][1], this.width, 0, Math.PI * 2, true);
            ctx.fillStyle = this.eraserColor;
            ctx.fill();
        }
    }
    setWidth(width) {
        this.width = width;
    }
    setColor(color) {
        this.eraserColor = color;
    }
}
export default EraserOp;
