/**
 * 
 *
 * 
 * 1. id: A
 * 2. id: B
 * 3. id, A => B
 * 4. A => B
 * 5. 
 *
 * 
 */
import { showToast } from "../../component/toast";
/**
 * dataType
 */
const MINIMUM_BROADCAST_GAP_PER_DATATYPE = 70;
const overwritableTypes = ['select', 'select-hover', 'camera-bound', 'mouse-pointer', 'laser'];
/**
 * 10s
 */
const ResendSyncRequestInterval = 10;
/**
 * 5
 */
const ResendSyncRequestLimit = 8;
class DataHub {
    constructor(app, handler) {
        /**
         * 
         * dataTypetimer
         */
        this.debounceData = {};
        /**
         * server_pkg_id, ididvalue
         * idserver_pkg_id
         */
        this.clientIdToData = {};
        /**
         * 0...lastContinueServerPkgId
         */
        this.lastContinueServerPkgId = -1;
        /**
         * id > serverPkgSinceLastseq id
         */
        this.serverPkgSinceLast = [];
        /**
         * id
         */
        this.lastPkgIdToSync = -1;
        this.dataOfSync = {};
        /**
         * 
         */
        this.isSynchronizing = false;
        this._scheduleNextBroadcast = (debounceDataEntry, dataType) => {
            return requestAnimationFrame(() => {
                const now = new Date().valueOf();
                if (now - debounceDataEntry.time < MINIMUM_BROADCAST_GAP_PER_DATATYPE) {
                    /**
                     * dataType
                     * requestAnimationFrame
                     */
                    debounceDataEntry.timer = this._scheduleNextBroadcast(debounceDataEntry, dataType);
                }
                else {
                    this._doBroadcastData(debounceDataEntry, dataType);
                }
            });
        };
        this._doBroadcastData = (debounceDataEntry, dataType) => {
            /**
             * 
             */
            const data = this.app.room.mergeDebouncedData(debounceDataEntry.dataQueue, dataType);
            const params = {
                strategy: (dataType === 'snapshot') ? 1 : 2,
                data: JSON.stringify(data),
                client_pkg_id: debounceDataEntry.client_pkg_id
            };
            if (dataType !== 'snapshot') {
                params.dataType = dataType;
            }
            this.handler.broadcastData(params);
            this.clientIdToData[params.client_pkg_id] = data;
            this.debounceData[dataType] = {
                timer: null,
                client_pkg_id: -1,
                time: 0,
                dataQueue: []
            };
        };
        /**
         * 
         */
        this._setupResendSyncRequestTimer = (currRetryCount = 1) => {
            /**
             * cleartimer
             * clearTimeouttimer
             */
            clearTimeout(this.resendSyncRequestTimer);
            this.resendSyncRequestTimer = setTimeout(() => {
                if (currRetryCount <= ResendSyncRequestLimit) {
                    console.error(`-->${currRetryCount + 1}, ${this.lastContinueServerPkgId}`);
                    this.handler.sendSyncRequest(this.lastContinueServerPkgId);
                    this._setupResendSyncRequestTimer(currRetryCount + 1);
                }
                else {
                    showToast({
                        type: 'error',
                        msg: '',
                        time: 1000
                    });
                    console.error(`-->`);
                }
            }, ResendSyncRequestInterval * 1000);
        };
        this.app = app;
        this.handler = handler;
        this.bindEvents();
    }
    get room() {
        return this.app.room;
    }
    /**
     * 
     */
    get lastServerPkgId() {
        if (this.serverPkgSinceLast.length > 0) {
            return this.serverPkgSinceLast[this.serverPkgSinceLast.length - 1];
        }
        else {
            return this.lastContinueServerPkgId;
        }
    }
    /**
     * id
     * ididid
     */
    get nextLocalFakeId() {
        const lastServerPkgId = this.lastServerPkgId;
        const inUseClientIds = Object.keys(this.clientIdToData).map(_ => Number(_)).sort();
        if (inUseClientIds.length === 0) {
            return lastServerPkgId + 1;
        }
        else if (lastServerPkgId >= inUseClientIds[inUseClientIds.length - 1]) {
            return lastServerPkgId + 1;
        }
        else {
            return inUseClientIds[inUseClientIds.length - 1] + 1;
        }
    }
    /**
     * 
     */
    reset() {
        for (let key in this.debounceData) {
            if (this.debounceData[key].timer) {
                cancelAnimationFrame(this.debounceData[key].timer);
            }
        }
        this.debounceData = {};
        this.clientIdToData = {};
        this.lastContinueServerPkgId = -1;
        this.serverPkgSinceLast = [];
        this.lastPkgIdToSync = -1;
        this.dataOfSync = {};
        this.isSynchronizing = false;
        clearTimeout(this.resendSyncRequestTimer);
    }
    bindEvents() {
        this.handler.on('data', this.onData.bind(this));
        this.handler.on('broadcastAck', this.onBroadcastAck.bind(this));
        this.handler.on('syncRequestAck', this.onSendRequestAck.bind(this));
        this.handler.on('syncRequestResponse', this.onSendRequestResponse.bind(this));
    }
    /**
     * 
     * @param msg
     */
    onData(msg) {
        const { data, server_pkg_id } = msg;
        this._updateServerPkgInfo(server_pkg_id, server_pkg_id);
        if (this.isSynchronizing) {
            /**
             * 
             */
            this.dataOfSync[server_pkg_id] = data;
        }
        else {
            let { type, value } = data;
            if (type === 'snapshot') {
                this.room.execSnapshot({
                    snapshot: value,
                    pkgId: server_pkg_id
                });
            }
            else if (type === 'broadcaster') {
                this.app.vision.receiveBroadcaster(value);
            }
            else if (type === 'snapshotArr') {
                this.room.execBatchSnapshots(value.map(_ => {
                    return {
                        snapshot: _,
                        pkgId: server_pkg_id
                    };
                }));
            }
        }
    }
    /**
     * 
     * 
     */
    sendSyncRequest() {
        console.log('--> , ', this.isSynchronizing);
        if (this.isSynchronizing) {
            return;
        }
        this.isSynchronizing = true;
        this.handler.sendSyncRequest(this.lastContinueServerPkgId);
        // -1syncRequestAck
        this.lastPkgIdToSync = -1;
        /**
         * 
         */
        this.dataOfSync = {};
        /**
         * 
         */
        this._setupResendSyncRequestTimer();
    }
    /**
     * 
     * 
     */
    onSendRequestAck(message) {
        this.lastPkgIdToSync = message.last_server_pkg_id;
        this._updateAppAfterSync();
    }
    /**
     * 
     * 
     * 
     */
    onSendRequestResponse(message) {
        //
        if (!this.isSynchronizing)
            return;
        let { start_server_pkg_id, end_server_pkg_id, packages } = message;
        if (!Array.isArray(packages)) {
            packages = [];
        }
        for (let pkg of packages) {
            this.dataOfSync[pkg.server_pkg_id] = pkg.data;
        }
        this._updateServerPkgInfo(start_server_pkg_id, end_server_pkg_id);
        this._updateAppAfterSync();
    }
    /**
     * 
     * server_pkg_id, server_pkg_id
     * @param message
     */
    onBroadcastAck(message) {
        const { server_pkg_id, client_pkg_id } = message;
        this._updateServerPkgInfo(server_pkg_id, server_pkg_id);
        const data = this.clientIdToData[client_pkg_id];
        delete this.clientIdToData[client_pkg_id];
        /**
         * room
         */
        if (!data) {
            console.warn(`--> broadcastAck, client_pkg_id:${client_pkg_id}`);
        }
        if (data && data.type === 'snapshot' && client_pkg_id !== server_pkg_id) {
            this.app.room.cacheSnapshot({
                pkgId: server_pkg_id,
                snapshot: data.value
            });
        }
        else if (data && data.type === 'snapshotArr' && client_pkg_id !== server_pkg_id) {
            data.value.forEach(_ => {
                this.app.room.cacheSnapshot({
                    pkgId: server_pkg_id,
                    snapshot: _
                });
            });
        }
    }
    sendSnapshot(snapshot) {
        if (overwritableTypes.includes(snapshot.data.type)) {
            return this.broadcastData({
                data: {
                    type: 'snapshot',
                    value: snapshot
                }
            }, snapshot.opId);
        }
        else {
            return this.broadcastData({
                data: {
                    type: 'snapshot',
                    value: snapshot
                }
            }, 'snapshot');
        }
    }
    broadcastRoomState(value, dataType) {
        return this.broadcastData({
            data: {
                type: dataType,
                value: value
            }
        }, dataType);
    }
    broadcastData(params, dataType) {
        if (!this.debounceData.hasOwnProperty(dataType) || !this.debounceData[dataType].timer) {
            const client_id = this.nextLocalFakeId;
            this.debounceData[dataType] = {
                timer: null,
                time: new Date().valueOf(),
                client_pkg_id: client_id,
                dataQueue: []
            };
            /**
             * nextLocalFakeIdclient_id
             */
            this.clientIdToData[client_id] = {};
        }
        const debounceDataEntry = this.debounceData[dataType];
        debounceDataEntry.dataQueue.push(params.data);
        if (debounceDataEntry.timer) {
            return debounceDataEntry.client_pkg_id;
        }
        debounceDataEntry.timer = this._scheduleNextBroadcast(debounceDataEntry, dataType);
        return debounceDataEntry.client_pkg_id;
    }
    /**
     * 
     */
    _updateAppAfterSync() {
        if (this.lastContinueServerPkgId >= this.lastPkgIdToSync) {
            /**
             * isSynchronizingfalsetimer
             */
            clearTimeout(this.resendSyncRequestTimer);
        }
        if (this.lastContinueServerPkgId >= this.lastPkgIdToSync && this.isSynchronizing) {
            console.log(`--> : ${this.lastContinueServerPkgId}--${this.lastPkgIdToSync}`);
            this.isSynchronizing = false;
            if (LOG) {
                console.log('-->', 'event:sync_finished');
            }
            this.app.emit('event:editable:change', this.app.editable);
            this.app.dispatcher.emit('event:sync_finished');
            const data = this._getDataToSync();
            let broadcasterData;
            for (let i = data.length - 1; i >= 0; i--) {
                if (data[i].data.type === 'broadcaster') {
                    broadcasterData = data[i].data.value;
                    break;
                }
            }
            const snapshotData = [];
            for (let pkg of data) {
                if (pkg.data.type === 'snapshot') {
                    snapshotData.push({
                        pkgId: pkg.pkgId,
                        snapshot: pkg.data.value
                    });
                }
                else if (pkg.data.type === 'snapshotArr') {
                    for (let snapshot of pkg.data.value) {
                        snapshotData.push({
                            pkgId: pkg.pkgId,
                            snapshot: snapshot
                        });
                    }
                }
            }
            if (broadcasterData) {
                this.app.vision.receiveBroadcasterAfterSync(broadcasterData);
            }
            this.app.room.execBatchSnapshots(snapshotData, (hasBoundary) => {
                if (!hasBoundary) {
                    /**
                     * boundary, boundary
                     */
                    if (this.app.defaultCameraBound) {
                        this.app.setCameraBound(this.app.defaultCameraBound);
                    }
                    else if (this.app.hasBackground()) {
                        this.app.fitToContent(false);
                    }
                }
            });
        }
    }
    _getDataToSync() {
        const seqIds = Object.keys(this.dataOfSync).sort((a, b) => {
            return Number(a) - Number(b);
        });
        return seqIds.map(id => {
            try {
                return {
                    data: JSON.parse(this.dataOfSync[id]),
                    pkgId: Number(id)
                };
            }
            catch (_a) {
                return undefined;
            }
        }).filter(_ => _);
    }
    /**
     *  lastContinueServerPkgId, serverPkgSinceLast
     * @param start: 
     * @param end :   [start, end]
     *
     * start->endserverPkgSinceLast
     */
    _updateServerPkgInfo(start, end) {
        let i = 0, curr = start;
        //serverPkgSinceLast[start, end]
        while (i <= this.serverPkgSinceLast.length) {
            if (curr > end) {
                break;
            }
            else if (curr <= this.lastContinueServerPkgId) {
                curr++;
                continue;
            }
            else if (i === this.serverPkgSinceLast.length) {
                /**
                 * [curr, end]serverPkgSinceLast
                 */
                const arr = [];
                while (curr <= end) {
                    arr.push(curr);
                    curr += 1;
                }
                this.serverPkgSinceLast.splice(i, 0, ...arr);
            }
            else if (this.serverPkgSinceLast[i] < curr) {
                i++;
            }
            else if (this.serverPkgSinceLast[i] === curr) {
                curr++;
                i++;
            }
            else {
                this.serverPkgSinceLast.splice(i, 0, curr);
                curr++;
                i++;
            }
        }
        //serverPkgSinceLastlastContinueServerPkgIdlastContinueServerPkgId
        i = 0;
        for (; i < this.serverPkgSinceLast.length; i++) {
            if (this.serverPkgSinceLast[i] === this.lastContinueServerPkgId + i + 1) {
                continue;
            }
            else {
                break;
            }
        }
        this.lastContinueServerPkgId += i;
        this.serverPkgSinceLast.splice(0, i);
    }
}
export default DataHub;
