/**
 * ctrl-v
 */
import Operation from "../Operation";
class LockOp extends Operation {
    constructor() {
        super(...arguments);
        this.type = 'lock';
        this.description = "";
    }
    eventsToListen() {
        return {
            'lock': {
                value: this.lockOps.bind(this),
                information: {
                    args: 'opIds',
                    message: ``
                }
            },
            'unlock': {
                value: this.unlockOps.bind(this),
                information: {
                    args: 'opIds',
                    message: ``
                }
            }
        };
    }
    lockOps(opIds) {
        this.dispatch(this.getSnapshot(opIds, 'lock'));
    }
    unlockOps(opIds) {
        this.dispatch(this.getSnapshot(opIds, 'unlock'));
    }
    getSnapshot(opIds, action) {
        return {
            type: this.type,
            action,
            dependOn: opIds.slice()
        };
    }
    /**
     * 
     */
    transformOp(baseOp, transformOp) {
        const newData = Object.assign({}, baseOp.data, {
            locked: transformOp.data.action === 'lock' ? true : false
        });
        return Object.assign({}, baseOp, {
            data: newData
        });
    }
}
export default LockOp;
