import ToastCtrl from './toast-ctrl';
/**
 * option.msg toast
 * option.time : 2snull: 
 * @param {*} option
 */
export const showToast = (option) => {
    const { msg, time = 2, type = 'info', top = 30 } = option;
    const param = {
        message: msg.constructor === String ? msg : JSON.stringify(msg),
        time,
        type,
        top
    };
    ToastCtrl.show(param);
};
export const hideToast = () => {
    ToastCtrl.hide();
};
