import Toast from './toast';
let timer;
export default class ToastCtrl {
    static createInstance() {
        this._instance = this._instance || new Toast();
        return this._instance;
    }
    static show(inOptions) {
        const { time = 3 } = inOptions;
        if (!this._instance) {
            this.createInstance();
        }
        this._instance.show(inOptions);
        if (timer) {
            clearTimeout(timer);
        }
        if (time) {
            timer = setTimeout(() => {
                this.hide();
            }, time * 1000);
        }
    }
    static hide() {
        if (timer) {
            clearTimeout(timer);
        }
        if (this._instance) {
            this._instance.hide();
        }
    }
    static destory() {
        if (this._instance) {
            this._instance.destroy();
            this._instance = null;
        }
    }
}
ToastCtrl._instance = null;
