class Simulator {
    constructor(app) {
        this._lastDirection = 0;
        this._interruptFreeDraw = false;
        this.app = app;
    }
    reconnect(rebuildSocket = false, resetApp = false) {
        console.log(`DEV-->  : ${resetApp}, socket: ${rebuildSocket}`);
        if (resetApp) {
            this.app.resetState();
        }
        if (rebuildSocket) {
            this.app.dataHub.handler._simulateReconnect();
        }
        else {
            /**
             * socket
             */
            this.app.readyToSyncData();
        }
    }
    generateRandomLines(points, repeat, intervalBtwPoints = 15, intervalBtwLines = 1000) {
        this._interruptFreeDraw = false;
        this._generateRandomLines(points, repeat, intervalBtwPoints, intervalBtwLines);
    }
    interruptRandomLines() {
        this._interruptFreeDraw = true;
    }
    _generateRandomLines(points, repeat, intervalBtwPoints = 15, intervalBtwLines = 1000) {
        console.log(`${repeat}`);
        if (this._interruptFreeDraw) {
            return console.log('');
        }
        if (repeat <= 0) {
            return;
        }
        setTimeout(() => {
            this._generateRandomLines2(points, intervalBtwPoints)
                .then(() => {
                return this._generateRandomLines(points, repeat - 1, intervalBtwPoints, intervalBtwLines);
            });
        }, intervalBtwLines);
    }
    _generateRandomLines2(total, intervalBtwPoints) {
        const el = this.app.container.el;
        const width = el.offsetWidth;
        const height = el.offsetHeight;
        let x = Math.random() * width;
        let y = Math.random() * height;
        let event = new MouseEvent('mousedown', {
            clientX: x,
            clientY: y
        });
        this.app.container.el.dispatchEvent(event);
        let promise = this._simulateMouseMove(x, y, 0);
        for (let i = 0; i < total; i++) {
            promise = promise
                .then(lastXY => {
                if (intervalBtwPoints > 0) {
                    return new Promise(res => {
                        setTimeout(() => {
                            res(lastXY);
                        }, intervalBtwPoints);
                    });
                }
                else {
                    return lastXY;
                }
            })
                .then(res => {
                if (this._interruptFreeDraw) {
                    return Promise.reject(res);
                }
                return this._simulateMouseMove(res.x, res.y, i);
            });
        }
        return promise
            .catch((res) => {
            return res;
        })
            .then(res => {
            event = new MouseEvent('mouseup', {
                clientX: res.x,
                clientY: res.y
            });
            document.dispatchEvent(event);
        });
    }
    _simulateMouseMove(x, y, currIndex) {
        return new Promise((res) => {
            setTimeout(() => {
                const [dx, dy] = this._getDxDy(currIndex);
                x += dx;
                y += dy;
                let event = new MouseEvent('mousemove', {
                    clientX: x,
                    clientY: y
                });
                document.dispatchEvent(event);
                res({
                    x, y
                });
            }, 0);
        });
    }
    _getDxDy(currIndex) {
        const RANDOM_BASE = 10;
        const TURN_AFTER = 30;
        if (currIndex % TURN_AFTER === 0) {
            this._lastDirection = Math.floor(Math.random() * 4);
        }
        if (this._lastDirection === 0) {
            return [Math.random() * RANDOM_BASE, Math.random() * RANDOM_BASE];
        }
        else if (this._lastDirection === 1) {
            return [Math.random() * RANDOM_BASE, Math.random() * -RANDOM_BASE];
        }
        else if (this._lastDirection === 2) {
            return [Math.random() * -RANDOM_BASE, Math.random() * RANDOM_BASE];
        }
        else {
            return [Math.random() * -RANDOM_BASE, Math.random() * -RANDOM_BASE];
        }
    }
}
export default Simulator;
